<template>
     <!--é—®å¤§å®¶åˆ—è¡¨é¡µé¢-->
     <div ref="askeveryone" class="askeveryone-content steep_padding" @scroll="scroll">
          <div class="askevent-footer">
               <input @focus="focus" @blur="blur" ref="input" type="text" v-model="askValue" placeholder="å‘66ä½å·²ä¹°è¿‡çš„äººæé—®ï¼Œ4~40å­—" style="font-size: 1.2rem">
               <span @click="sendAsk">æé—®</span>
          </div>
          <div class="askevery-header">
               <HeaderPage :colorType="'white'">
                    <span slot="headerTitle">é—®å¤§å®¶</span>
               </HeaderPage>
          </div>
               <div class="title-list" v-if="false">
                    <!--<h4>å…³äºŽ æŸåº—é“ºåç™½è‰²è¿åŠ¨éž‹æ–°æ¬¾...çš„é—®é¢˜</h4>-->
                    <ul >
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                         <li>å…¨éƒ¨</li>
                    </ul>
               </div>
               <ul class="content_ul">
                    <li v-for="(item,index) in askList" :key="index + 'ins'" @click="toAskDetails(item)">
                         <div>
                              <span>é—®</span>
                              <span style="white-space: nowrap;text-overflow: ellipsis;overflow: hidden;word-break: break-all;width: 90%;">{{ item.title }}</span>
                         </div>
                         <div>
                              <span>ç­”</span>
                              <span v-if="false">æŒºå‡†çš„</span>
                              <span v-if="item.content == ''" style="color: #9d9d9d">æš‚æ— å›žç­”</span>
                              <span v-if="item.content != ''">{{ item.content }}</span>
                         </div>
                         <div>
                              <span>{{ item.answer }}ä¸ªå›žç­”</span>
                              <span>æ›´æ–°äºŽ{{ item.format_create_time }}</span>
                         </div>
                    </li>
               </ul>
               <div class="find_more">
                    <span @click="moreList" v-if="moreBool">æŸ¥çœ‹æ›´å¤š</span>
                    <span v-if="!moreBool">å·²åŠ è½½å…¨éƒ¨</span>
               </div>

     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     import storage from "@/common/js/storage.js"
     export default {
          name: "AskEveryone",
          data(){
               return {
                    askValue:"",
                    page:1,
                    askList:[],
                    moreBool:true,
                    scrollTop:0
               }
          },
          components:{
               HeaderPage,
          },
          methods:{
               //å¤±åŽ»ç„¦ç‚¹
               blur(){
               },
               //èšç„¦
               focus(){
               },
               moreList(){
                    this.page++
                    this.getAskList()
               },
               //åˆ°é—®é¢˜è¯¦æƒ…
               toAskDetails(item){
                    this.$store.commit("setAskContent",item)
                    location.href = "#/askdetails"
               },
               //å‘èµ·æé—®
               sendAsk(){
                    if (this.askValue == ""){
                         this.$toast.center("è¯·è¾“å…¥æ‚¨æƒ³æé—®çš„é—®é¢˜")
                         return
                    }
                    var obj = {
                         goods_id:this.$route.query.goods_id,
                         title:this.askValue
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_PROBLEM_ADD_PROBLEM,(result) => {
                         if (result.code == 1){
                              this.$toast.center("é—®é¢˜å‘è¡¨æˆåŠŸ")
                              this.askValue = ""
                              this.page = 1
                              this.getAskList((data) => {
                                   this.askList = data
                                   this.moreBool = true
                                   this.$refs.askeveryone.scrollTop = 0
                              })
                         }
                    })
               },
          //     èŽ·å–é—®é¢˜åˆ—è¡¨
               getAskList(callBack){
                    var obj = {
                         goods_id:this.$route.query.goods_id,
                         page: this.page
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_PROBLEM_PROBLEM_LIST,(result) => {
                         if (result.code == 1) {
                              if (result.data.length != 0){
                                   if (callBack){
                                        callBack(result.data)
                                   } else{
                                        this.askList = this.askList.concat(result.data)
                                   }
                              }else{
                                   this.moreBool = false
                              }
                         }
                    })
               },
               scroll(){
                    this.scrollTop = this.$refs.askeveryone.scrollTop
               }
          },
          mounted() {
               // $('.title-list > ul li').eq(0).addClass('activeLi')
          },
          created(){
               var stoInfo = storage.get("askList")
               if (stoInfo) {
                         this.askList = stoInfo.askList
                        this.scrollTop = stoInfo.scrollTop
                        this.page = stoInfo.page
                        this.moreBool = stoInfo.moreBool
                         this.$nextTick(() => {
                              this.$refs.askeveryone.scrollTop = this.scrollTop
                         })
               }else{
                    this.getAskList()
               }

          },
          beforeRouteLeave(to,from,next){
               if (to.name == "askdetails"){
                    var obj = {
                         askList:this.askList,
                         scrollTop:this.scrollTop,
                         page:this.page,
                         moreBool:this.moreBool
                    }
                    storage.set("askList",obj)
               }else{
                    storage.remove("askList")
               }
               next()
          }
     }
</script>




