<style scoped lang="scss">
     #order_history{
          height: 100vh;
          overflow-y: scroll;
          background-color: #f6f6f6;
          padding-top: 5.1rem;
     }
     .header{
          position: fixed;
          width: 100%;
          top: 0;
          left: 0;
     }
     .list_content{
          padding: 0 1.2rem;
          ul{
               li{
                    background-color: #ffffff;
                    padding: 0.9rem;
                    border-radius: 0.7rem;
                    margin-bottom: 0.7rem;
                    .list_head_shop{
                         display: flex;
                         align-items: center;
                         justify-content: flex-start;
                         .shop_thumb{
                              width: 2.5rem;
                              height: 2.5rem;
                              background-size: cover;
                              margin-right: 1rem;
                         }
                         span{
                              font-size: 1.5rem;
                              margin-right: 1rem;
                         }
                         >img{
                              width: 0.5rem;
                              height: 1rem;
                         }
                    }
                    .order_goods_list{
                         margin-top: 1.2rem;
                         .goods_infos{
                              display: flex;
                              justify-content: flex-start;
                              .goods_thumb{
                                   width: 10rem;
                                   height: 10rem;
                                   border-radius: 0.7rem;
                                   background-size: cover;
                                   flex-shrink: 0;
                              }
                              .goods_title_miao{
                                   margin-left: 1rem;
                                   flex-grow: 1;
                                   display: flex;
                                   flex-direction: column;
                                   justify-content: space-between;
                                   p{
                                        text-overflow: -o-ellipsis-lastline;
                                        overflow: hidden;
                                        text-overflow: ellipsis;
                                        display: -webkit-box;
                                        -webkit-line-clamp: 2;
                                        line-clamp: 2;
                                        -webkit-box-orient: vertical;
                                        font-size: 1.5rem;
                                   }
                                   span{
                                        font-size: 1.3rem;
                                        color: #9d9d9d;
                                   }
                              }

                         }
                         .hui_btn{
                              text-align: right;
                              margin-top: 0.5rem;
                              span{
                                   width: 9rem;
                                   height: 2.9rem;
                                   display: inline-flex;
                                   align-items: center;
                                   justify-content: center;
                                   font-size: 1.3rem;
                                   color: #9d9d9d;
                                   border: solid 1px #9d9d9d;
                                   border-radius: 1.45rem;
                              }
                         }
                    }

               }
          }
     }
     .more_data{
          font-size: 1.3rem;
          color: #9d9d9d;
          text-align: center;
          margin: 1.5rem 0;
     }
</style>
<template>
     <!--è®¢å•å›žæ”¶ç«™-->
     <div id="order_history" @scroll="_scroll" ref="orderHistory">
          <div class="header">
               <HeaderPage>è®¢å•å›žæ”¶ç«™</HeaderPage>
          </div>
          <div class="list_content">
               <ul>
                    <li @click="_toOrderEetails(item.order_no)" v-for="(item,index) in orderList" :key="item.aid">
                         <div class="list_head_shop">
                              <div class="shop_thumb" :style="{backgroundImage:'url('+ item.shop_thumb +')'}"></div>
                              <span>{{ item.shop_name }}</span>
                              <img src="@/common/image/publicImg/mail_ico_point_v.png" alt="">
                         </div>
                         <div class="order_goods_list">
                              <div class="goods_infos" v-for="(goodsItem,goodsIns) in item.goods" :key="goodsItem.aid">
                                   <div class="goods_thumb" :style="{backgroundImage: 'url('+ goodsItem.thumb +')'}"></div>
                                   <div class="goods_title_miao">
                                        <p>{{ goodsItem.title }}</p>
                                        <span>{{ goodsItem.specs }}</span>
                                   </div>

                              </div>
                              <div class="hui_btn">
                                   <span>æ¢å¤è®¢å•</span>
                              </div>
                         </div>
                    </li>
               </ul>
          </div>
          <div class="more_data">
               <span v-if="moreBool">æ­£åœ¨åŠ è½½æ›´å¤š..</span>
               <span v-if="!moreBool">å·²åŠ è½½å…¨éƒ¨</span>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "OrderHistory",
          data(){
               return{
                    page:1,
                    orderList:[],
                    scrollBool:true,
                    moreBool:true
               }
          },
          created(){
               this._getOrderList()
          },
          methods:{
               //è·³è½¬åˆ°è®¢å•è¯¦æƒ…
               _toOrderEetails(order_no) {
                    this.$router.push({name: "rderetails", query: {order_no}})
               },
               _getOrderList(){
                    var obj = {
                         order_type:"shop_goods",
                         page:this.page,
                         is_delete:1
                    }
                    this.postObj(obj,window.PATHS.SHOPS_ORDER_GET_ORDER_LIST,(result) => {
                         if (result.code == 1){
                              if (result.data.length == 0){
                                   this.moreBool = false
                                   this.scrollBool = false
                                   return
                              } else{
                                   this.orderList = this.orderList.concat(result.data)
                                   setTimeout(() => {
                                        this.scrollBool = true
                                   },500)
                              }
                         }
                    })
               },
               _scroll(){
                    const windowHeight = window.innerHeight
                    var scrollTop = this.$refs.orderHistory.scrollTop
                    var height = this.$refs.orderHistory.scrollHeight
                    this.scrollMore(scrollTop,height,windowHeight,() => {
                         if (this.scrollBool){
                              this.scrollBool = false
                              this.page++
                              this._getOrderList()
                         }
                    })
               }
          },
          components:{
               HeaderPage
          }
     }
</script>





