</template>

<script>
     import storage from '@/common/js/storage.js'
     export default {
          name: "HomePage",
          data() {
               return {
                    overTimeBool:true,
                    count: 0,
                    isLoading: false,
                    goodList: [],
                    goodsPage: 1,
                    homeMes:null,
                    scrollBool:true,
                    goodsLoadBool:true,
                    scrollTop:0,
                    leaveBool:true,
                    backColor:{
                         backgroundImage: 'linear-gradient(to bottom,#f55056 30%,#ffffff)',
                         opacity: 1
                    },
                    sckillStr:null,
                    sckillDao:1,
                    timeOverNum:null,
                    shoppingCarNum:0,
                    tokenBool:false,
               //     ç”¨æˆ·
                    userInfo:null,
                    showBlack:false,
                    //ä¸å–œæ¬¢å¼¹æ¡†å®šä½
                    dolikeas:{
                         left:0,
                         top:0
                    },
                    topBg:0,
                    autoplay:3000,
                    topLoop:true,
               }
          },
          mounted() {
               var that = this
               setTimeout(() => {
                    that.tokenBool = window.token? false : true
               },1000)
          },
          methods: {
               //å¾—åˆ°ç”Ÿé²œä¸“åŒº
               _toFresh(){
                    location.href = "#/freshindex"
               },
               //æ»šåŠ¨
               scroll(){
                    var scrollTop = this.$refs.homeContent.scrollTop
                    this.scrollTop = scrollTop
                    var height = this.$refs.homeContent.scrollHeight
                    var windowHeight = window.innerHeight
                    this.scrollMore(scrollTop,height,windowHeight,() => {
                         if (this.scrollBool){
                              this.scrollBool = false
                              //å¦‚æžœæ˜¯æ™®é€šå•†å“
                                   if (this.goodsLoadBool){
                                        this.goodsPage++
                                        this.getGoodsList()
                                   }
                         }
                    })
                         if (scrollTop >= 100){
                              $(".homeHeader").css({backgroundColor:"#f55056"})
                         }else{
                              $(".homeHeader").css({backgroundColor:"transparent"})
                         }
                         if (scrollTop >= 0){
                              this.topBg = -scrollTop
                         }
                         if (scrollTop <= 1){
                              this.$refs.homeContent.scrollTop = 0
                         }
                         // banneråœæ­¢æ»šåŠ¨
                         if(scrollTop >= window.innerHeight / 2){
                              this.overTimeBool = false
                              this.autoplay = 0
                         }else{
                              this.overTimeBool = true
                               this.autoplay = 3000
                         }
               },
               //é•¿æŒ‰
               touchShow(ev){
                    this.showBlack = true
               },
               clearGoods(index){
                    this.showBlack = true
                    var width = this.$refs.goodsLi[index].getBoundingClientRect().width / 2
                    var height = this.$refs.goodsLi[index].getBoundingClientRect().height / 2
                    var x = window.innerWidth - this.$refs.goodsLi[index].getBoundingClientRect().x - width - 13
                    var y = this.$refs.goodsLi[index].getBoundingClientRect().y + height + 15
                    this.dolikeas.left = x
                    this.dolikeas.top = y
               },
               //åˆ†äº«æ™®é€šå•†å“
               shareCom(comInfo){
                    var obj = {
                         title:comInfo.title,
                         content:comInfo.advertising_title,
                         url:window.HOST + "/goods/detail?uid=" + this.userInfo.id + "&gid=" + comInfo.aid,
                         imgsrc:comInfo.thumb
                    }
                    this.shareSame(obj)
               },
               //åˆ°å¿…ä¹°å¥½è´§
               toMustBuy(){
                    location.href = "#/mustbuygoods"
               },
               //ç‚¹å‡»ç™»å½•
               loginYs(){
                    location.href = "app://login?" + this.getUrl()
               },
               //banneræ»šåŠ¨
               changeBanner(index){
                    var that = this
                    this.backColor.opacity = '0.6'
                    this.backColor.backgroundImage = 'linear-gradient(to bottom,'+this.homeMes.sider[index].bg_color+' 30%,#ffffff)'
                    setTimeout(() => {
                         that.backColor.opacity = '1'
                    },300)
               },
               //åˆ·æ–°
               onRefresh() {
                    this.getUserInfo((data) => {
                         if(data){
                              this.userInfo = data
                              console.log(this.userInfo,"ç”¨æˆ·ä¿¡æ¯")
                         }else{
                              this.userInfo = null
                         }
                    })
                    var that = this
                    clearInterval(window.homeTime)
                    that.goodsPage = 1
                    that.goodList = []
                    that.goodsLoadBool = true
                    that.getGoodsList()
                    that.getHomeMes()
                    setTimeout(() => {
                         that.isLoading = false;
                    },1000);
               },
               //åˆ°è´­ç‰©è½¦é¡µé¢
               tiShoppingCar(){
                    location.href = "#/shoppingcar"
               },
               //ç‚¹å‡»banneråˆ°è½åœ°é¡µ
               toSqueezePage(url){
                    var token = window.token
                    return "app://view/" + url + '?token=' + token
               },
               //ç‚¹å‡»åˆ†ç±»
               toClassify(){
                    // location.href = "#/becvlog"
                    // location.href = "#/vlog"
                    // location.href = "#/mustdetail"
                    // location.href = "#/classify"
                    // location.href = "#/order"
                    // location.href = "#/housindex"
                    // location.href = "#/ent"
                    // location.href = "#/offerent"
                    // location.href = "#/mydraw"
                    // location.href = "#/collect"
                    // location.href = "#/shoppingcar"
                    // location.href = "#/place"
                    // location.href = "#/mycomment"
                    // location.href = "#/footprint"
                    // location.href = "#/paysuccess"
                    // location.href = "#/paydefeated"
                    // location.href = "#/bill"
                    // location.href = "#/commentindex"
                    // this.$router.push({name:"afterlist",query:{isyuan:1}})
                    // location.href = "#/orderhistory"
                    //è§†é¢‘å¸¦è´§
                    location.href = "#/videoshop"
               //     å•†å“æ©±çª—
               //      location.href = "#/vlogshowcase"
               //      location.href = "#/vlog"
               },
               //ç‚¹å‡»æœç´¢
               toSearch(){
                    location.href = "#/search"
               },
               //ç‚¹å‡»æ‰«ä¸€æ‰«
               scan(){
                    location.href = "app://scan"
               },
               //ç‚¹å‡»0å…ƒæ¢è´­
               toZeroPage(){
                    location.href = "#/zero"
               },
               //ç‚¹å‡»é™æ—¶æŠ½å¥–
               toDraw(){
                    location.href = "#/draw"
               },
               //åˆ°æˆ‘çš„æžœå›­é¡µé¢
               toFruit(){
                    if (this.maleToken()){
                         location.href = "#/myfruit"
                    }
               },
               //åˆ°å¹³æ‹¼å›¢é¡µé¢
               toTeam(){
                    location.href = "#/teampay"
               },
               //åˆ°é™æ—¶ç§’æ€
               toSkill(){
                    this.$router.push({
                         name:"seckill",
                         query:{
                              sckillTime:this.sckillStr
                         }
                    })
               },
               //åˆ°å•†å“è¯¦æƒ…é¡µ
               toDetails(good_id) {
                    this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
               },
               //èŽ·å–å…¨éƒ¨å•†å“å•†å“åˆ—è¡¨
               getGoodsList() {
                    var that = this
                    var obj = {
                         goods_type: '',
                         page: this.goodsPage,
                    }
                    this.postObj(obj, window.PATHS.SHOPS_GOODS_GET_LIST, (result) => {
                         if (result.code == 1) {
                              if (result.data.length == 0){
                                   this.goodsLoadBool = false
                              }else{
                                   this.goodList = this.goodList.concat(result.data)
                              }
                              setTimeout(() => {
                                   that.scrollBool = true
                              },500)
                         }
                    })
               },
               onChangeBanner(index) {
                    this.current = index;
               },
               //å½“å‰åœº
               getCurrentHous(Hous){
                    var arr =[8,14,20];
                         if(Hous < 8 || Hous == 24){
                              return 8;
                         }
                         if(Hous >= 20 && Hous < 24){
                              return 20;
                         }
                         var a1 = arr.filter(function(item){
                              return item <= Hous;
                         });
                         return a1.pop();
                    },
               //ä¸‹ä¸€åœº
               getNextHous(Hous){
                         if(Hous < 8 || Hous == 24){
                              return 8;
                         }
                         if(Hous >= 20 && Hous < 24){
                              return 24;
                         }
                         var arr =[8,14,20];
                         var a1 = arr.filter(function(item){
                              return item <= Hous;
                         });
                         var num = a1.pop();
                         return num + 6;
                    },
          //     èŽ·å–è·ç¦»ä¸‹ä¸€åœºç§’æ•°
               getSecond(time){
                    var seconds = time.getSeconds() + time.getMinutes() * 60
                    var Next = this.getNextHous(time.getHours())
                    var Hours = time.getHours()
                    this.sckillDao = (Next - Hours) * 3600 - seconds
                    this.timeOver()
               },
               //å°†ç§’æ•°è½¬æ¢æ—¶é—´æ ¼å¼
               timeOver(){
                    var that = this
                    window.homeTime = setInterval(() => {
                         this.sckillDao--
                              if (this.sckillDao < 0){
                                   clearInterval(window.homeTime)
                                   var isChnage = 1
                                   this.getHomeMes((val) => {
                                        var newTime = new Date(val*1000)
                                        this.sckillStr = this.getCurrentHous(newTime.getHours())
                                        this.getSecond(newTime)
                                   })
                              }else{
                                   var time =  this.sckillDao
                                   var min=Math.floor(this.sckillDao%3600);
                                   this.timeOverNum = Math.floor(time/3600) + ":" + (Math.floor(min/60) < 10? '0'+Math.floor(min/60):Math.floor(min/60)) + ":"+ (time%60 < 10? '0' + time%60 : time%60);
                              }
                    },1000)
               },
          //     èŽ·å–é¦–é¡µæ•°æ®
               getHomeMes(callBack){
                    var obj = {}
                    this.postObj(obj,window.PATHS.HOME_VESIONONE,(result) => {
                         if (result.code == 1){
                              if (callBack){
                                   callBack(result.timestamp)
                              }else{
                                   this.homeMes = result.data
                                   this.backColor.backgroundImage = 'linear-gradient(to bottom,'+this.homeMes.sider[0].bg_color  + ' 30%,#ffffff)'
                                   var newTime = new Date(result.timestamp*1000)
                                   this.sckillStr = this.getCurrentHous(newTime.getHours())
                                   this.getSecond(newTime)
                              }
                         }
                    })
               },
          //     èŽ·å–è´­ç‰©è½¦å•†å“æ•°é‡
               _getCratList() {
                    var obj = {}
                    this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCART_GETLIST, (result) => {
                         if (result.code == 1){
                              if (result.data.lists.length == 0){
                                   this.shoppingCarNum == ""
                              }else{
                                   result.data.lists.forEach((item) => {
                                        item.goods.forEach(() => {
                                             this.shoppingCarNum++
                                        })
                                   })
                              }
                         }
                    })

               },
          },
          activated(){
               //æŒ‚è½½åœ¨mainä¸­çš„æ–¹æ³•
               this.getUserInfo((data) => {
                         if(data){
                              this.userInfo = data
                         }else{
                              this.userInfo = null
                         }
               })

               if (this.$refs.homeContent){
                    this.$refs.homeContent.scrollTop = this.scrollTop
               }
               this.leaveBool = true
           //     èŽ·å–è´­ç‰©è½¦å•†å“æ•°é‡
               this._getCratList()

          },
          created(){
               this.getHomeMes()
               this.getGoodsList()
               //æŒ‚è½½åœ¨mainä¸­çš„æ–¹æ³•
               this.getUserInfo((data) => {
                    if(data){
                         this.userInfo = data
                    }else{
                         this.userInfo = null
                    }
               })
          },
          beforeRouteLeave(to,from,next){
               this.leaveBool = false
               this.shoppingCarNum = 0
               next()
          }
     }
</script>





