<template>
     <!--ä¸­å¥–æ˜Žç»†ï¼šå·²ç»å¼€è¿‡å¥–çš„-->
     <div class="win_draw_content" v-if="drawInfo != null">
          <div class="draw_header">
               <HeaderPage>ä¸­å¥–æ˜Žç»†</HeaderPage>
               <img src="@/common/image/detailsImg/mail_ico_rule.png"style="width: 1.6rem;height: 1.6rem;"/>
          </div>
          <div class="draw-info">
               <span>æœŸæ•°:ç¬¬1000{{ drawInfo.aid }}</span>
               <span>å·²è´­:{{ drawInfo.current }}äºº</span>
               <span>ä»½æ•°:å…±{{ drawInfo.number }}ä»½</span>
               <span>æ¯ä»½:ï¿¥{{ drawInfo.single_price }}</span>
               <span>å¼€å§‹æ—¶é—´:{{ drawInfo.create_time }}</span>
               <span>ç»“æŸæ—¶é—´:{{ drawInfo.end_time }}</span>
          </div>
          <div class="winpeople">
               <h3>ä¸­å¥–äººå‘˜</h3>
               <div>
                    <div>
                         <img :src="drawInfo.hedaimgurl" alt="" style="width: 4rem;height: 4rem;border-radius: 50%">
                         <div>
                              <h4 style="font-size: 1.5rem">{{ drawInfo.nickname }}</h4>
                              <span>å¹¸è¿ç :</span><span style="color: #e7204f">101014024</span>
                         </div>
                    </div>
                    <span>
                         2019/9/9&nbsp&nbsp15:00:00
                    </span>
               </div>
          </div>
          <div class="people" v-if="drawPeopleList.length != 0">
               <span>å‚å¥–äººå‘˜</span>
          </div>
          <ul>
               <li v-for="(item,index) in drawPeopleList" :key="index + 'peoloins'">
                    <div>
                         <img :src="item.hedaimgurl" alt="" style="width: 4rem;height: 4rem;border-radius: 50%">
                         <div>
                              <h4 style="font-size: 1.5rem">{{ item.nickname }}</h4>
                              <span>å¹¸è¿ç :</span><span style="color: #e7204f">1000{{ item.aid }}</span>
                         </div>
                    </div>
                    <span>
                         {{ item.create_time }}
                    </span>
               </li>
          </ul>
          <div ref="scrollFooter" style="text-align: center;margin-top: 1rem;font-size: 1.3rem">
               <van-loading size="24px" v-if="drawListBool">åŠ è½½ä¸­...</van-loading>
               <span v-if="!drawListBool" class="overFooter">å·²ç»æ»‘åˆ°åº•äº†</span>
          </div>
     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     export default {
          name: "DrawPeople",
          data(){
               return {
                    drawInfo:null,
                    drawPeoplePage:1,
                    drawPeopleList:[],
                    drawListBool:true
               }
          },
          methods:{
          //     èŽ·å–æœ¬æœŸæŠ½å¥–äººå‘˜åå•
               getDrawPeople(){
                    var obj = {
                         page:this.drawPeoplePage,
                         period_id:this.drawInfo.aid
                    }
                    this.postObj(obj,window.PATHS.DUOBAO_GETLIST,(result) => {
                         if (result.code == 1){
                             if (result.data.length != 0){
                                  for (var i = 0; i < result.data.length; i++){
                                       this.drawPeopleList.push(result.data[i])
                                  }
                             } else{
                                  this.drawListBool = false
                             }
                         } else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               scroll(){
                   if (this.drawListBool){
                        var tabTop = this.$refs.scrollFooter.getBoundingClientRect().top
                        var htmlHeight = window.innerHeight
                        if (tabTop <= htmlHeight - 20) {
                             this.drawPeoplePage ++
                             this.getDrawPeople()
                        }
                   }
               },
          },
          mounted(){
               window.addEventListener("scroll",this.scroll)
          },
          created(){
               this.drawInfo = this.$store.state.drawInfo
               this.getDrawPeople()
               console.log(this.$store.state.drawInfo)
          },
          components:{
               HeaderPage
          }
     }
</script>

<style scoped>
     .winpeople > div > div img{
          margin-right: 1rem;
     }
     .winpeople > div > div{
          display: flex;
          justify-content: left;
          align-items: center;
     }
     .winpeople > div{
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 1.2rem;
          height: 6rem;
          background-color: white;
          font-size: 1.3rem;
     }
     .winpeople h3{
          font-size: 1.7rem;
          line-height: 4.4rem;
          text-align: center;
          margin-top: 1rem;
          background-color: white;
     }

     ul li > div img{
          margin-right: 1rem;
     }
     ul li > div{
          display: flex;
          justify-content: left;
          align-items: center;
     }
     ul li {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 1.2rem;
          height: 6rem;
          background-color: white;
          font-size: 1.3rem;
          margin-top: 1rem;
     }

     .win_draw_content .people{
          font-size: 1.7rem;
          line-height: 4.4rem;
          text-align: center;
          margin-top: 1rem;
          background-color: white;
     }

     .draw-info span:last-child{
          margin-bottom: 0;
     }
     .draw-info span{
          display: block;
          font-size: 1.5rem;
          margin-bottom: 1.5rem;
     }
     .draw-info{
          padding: 2rem 1.2rem;
          width: 100%;
          background-color: white;
     }
     .draw_header > img{
          position: absolute;
          right: 1.2rem;
          top: 1.4rem;
     }
     .draw_header{
          width: 100%;
          position: fixed;
          top: 0;
     }
     .win_draw_content{
          background-color: #f6f6f6;
          margin-top: 4.4rem;
          padding-bottom: 1rem;
          min-height: 100vh;
     }
</style>



