<!--é™æ—¶ç§’æ€-->
<template>
  <div @scroll="scroll" ref="seckillContent" id="seckill-content" class="steep_padding">
     <div class="seckill_header" :class="headerCsBool? 'header_active' : ''">
          <HeaderPage :colorType="'white'">
               <span slot="headerTitle">é™æ—¶ç‰¹æƒ </span>
          </HeaderPage>
     </div>
       <div class="header_banner" :style="{top:headerBannerTop + 'px'}">
            <img src="@/common/image/seckill/topbanner.png" alt="">
       </div>
       <div class="seckIllrel_content">
            <van-pull-refresh style="min-height: 100vh" v-model="isLoading" @refresh="_onRefresh">
                 <div class="top_active_choose">
                      <ul v-if="homeMes">
                           <li @click="toDraw">
                                <div class="active_choose_title">
                                     <h4>é™æ—¶æŠ½å¥–</h4>
                                     <span style="color:#8758fb">æƒŠå–œä¸“äº«æŠ˜æ‰£</span>
                                </div>
                                <div class="active_choose_img_list left_choose_img_list">
                                     <div
                                             v-for="(item,index) in homeMes.hotDuobao"
                                             :key="index + 'draw'"
                                             :style="{
                                           backgroundImage: 'url('+ item.thumb +')'
                                      }"
                                     >
                                     </div>
                                </div>
                           </li>
                           <li @click="_toZero">
                                <div class="active_choose_title">
                                     <h4>éº¦ç©—å…‘æ¢</h4>
                                     <span>å“è´¨ç¾Žå¥½ç”Ÿæ´»</span>
                                </div>
                                <div class="active_choose_img_list right_choose_img_list">
                                     <div
                                             v-for="(item,index) in homeMes.hotExchange"
                                             :key="index + 'dui'"
                                             :style="{
                                           backgroundImage: 'url('+ item.thumb +')'
                                      }"
                                     ></div>
                                </div>
                           </li>
                      </ul>
                 </div>
                 <div class="seckill_table">
                      <img class="seckill_logo" src="@/common/image/seckill/logo.png" alt="">
                      <ul>
                           <li
                                   v-for="(item,index) in sessionList"
                                   :class="session == item? 'actibe_table' : ''"
                                   :key="index + 'ins'"
                                   @click="changeSession(item)"
                           >
                                <h4 ><span class="num_family">{{ item }}:00</span>åœº</h4>
                                <span v-if="item > isskillTime">æœªå¼€å§‹</span>
                                <span v-if="item <= isskillTime">æŠ¢è´­ä¸­</span>
                           </li>
                      </ul>
                 </div>
                 <div class="seckill_goods_list">
                      <ul>
                           <li @click="_toDetail(item.aid)" v-for="(item,index) in goodsList" :key="item.aid">
                                <div class="goods_thumb" :style="{backgroundImage:'url('+ item.thumb +')'}"></div>
                                <div class="goods_list_right">
                                     <p>{{ item.title }}</p>
                                     <div class="goods_price">
                                          <div>
                                               <span class="how_gobuy"><span class="num_family" style="color: #ff0000">{{ item.sales }}</span>äººå·²æŠ¢è´­</span>
                                               <div>
                                               <span class="shop_price">
                                                    <span style="font-size: 1.1rem">ï¿¥</span><span style="font-size: 1.8rem">{{ item.shop_price.substring(0,item.shop_price.length - 3) }}</span><span style="font-size: 1.1rem">{{ item.shop_price.substring(item.shop_price.length - 3,item.shop_price.length) }}</span>
                                               </span>
                                                    <span class="market_price">200.00</span>
                                               </div>
                                          </div>

                                          <template v-if="isskillTime >= session">
                                               <span v-if="item.extra_data.is_kill == 0" class="botm_btn_none">å·²ç»“æŸ</span>
                                               <span v-if="item.extra_data.is_kill == 1" class="botm_bot">ç«‹å³æŠ¢è´­</span>
                                          </template>
                                          <span v-if="isskillTime < session" class="botm_btn_none">æœªå¼€å§‹</span>
                                     </div>
                                </div>
                           </li>
                      </ul>
                 </div>
            </van-pull-refresh>

       </div>
  </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
    export default {
        name: "SeckillPage",
         data(){
             return {
                  //åœºæ¬¡æ•°ç»„
                  sessionList:[8,14,20],
                  //å½“å‰çŠ¶æ€çš„åœºæ¬¡
                  session:8,
                  page:1,
                  goodsList:[],
                  scrollBool:true,
                  moreBool:true,
                  headerCsBool:false,
                  headerBannerTop:0,
                  homeMes:null,
                  //ç”¨æ¥åˆ¤æ–­åœºæ¬¡æ˜¯å¦å¼€å§‹
                  isskillTime:0,
                  isLoading:false,
                  scrollTop:0
             }
         },
         methods:{
             _onRefresh(){

                  this.getHomeMes((val) => {
                       this.isskillTime = val
                  })
                  setTimeout(() => {
                       this.isLoading = false
                  },1000)
             },
              //å½“å‰åœº
              _getCurrentHous(Hous){
                   Hous = 16
                   var arr =[8,14,20];
                   if(Hous < 8){
                        return 8;
                   }
                   if(Hous >= 20){
                        return 8;
                   }
                   var a1 = arr.filter(function(item){
                        return item <= Hous;
                   });
                   return a1.pop();
              },
             _toZero(){
                  location.href = "#/zero"
             },
              //ç‚¹å‡»é™æ—¶æŠ½å¥–
              toDraw(){
                   location.href = "#/draw"
              },
             //åˆ°å•†å“è¯¦æƒ…é¡µ
              _toDetail(good_id,is_kill){
                   this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
              },
             scroll(){
                  var height = this.$refs.seckillContent.scrollHeight
                  var scrollTop = this.$refs.seckillContent.scrollTop
                  this.scrollTop = this.$refs.seckillContent.scrollTop
                  var windowHeight = window.innerHeight
                  if(scrollTop <= 0){
                       this.$refs.seckillContent.scrollTop = 0
                  }
                  this.scrollMore(scrollTop,height,windowHeight,() => {
                       if (this.scrollBool){
                            this.scrollBool = false
                            if (this.moreBool){
                                 this.page++
                                 this._getGoodsList()
                            }
                       }
                  })
                  if (scrollTop >= 40){
                       this.headerCsBool = true
                  }else{
                       this.headerCsBool = false
                  }
                  if (scrollTop > 0){
                       this.headerBannerTop = -scrollTop
                  }else{
                       this.headerBannerTop = 0
                  }
             },
              //     èŽ·å–é¦–é¡µæ•°æ®
              getHomeMes(callBack){
                   var obj = {}
                   this.postObj(obj,window.PATHS.HOME_VESIONONE,(result) => {
                        if (result.code ==1 ){
                             this.homeMes = result.data
                             console.log(this.homeMes)
                             if (callBack){
                                  var newTime = new Date(result.timestamp*1000)
                                  var sckillStr = newTime.getHours()
                                  callBack(sckillStr)
                             }

                        }
                   })
              },
              //èŽ·å–å•†å“åˆ—è¡¨
              _getGoodsList(session){
                   var obj = {
                        goods_type:"goods_seckill",
                        page:this.page,
                        seckill:this.session
                   }
                   this.postObj(obj,window.PATHS.SHOPS_GOODS_GET_LIST,(result) => {
                        if (result.code == 1){
                             if (result.data.length != 0){
                                  this.moreBool = true
                                  this.goodsList = this.goodsList.concat(result.data)
                                  console.log(this.goodsList)
                                  setTimeout(() => {
                                       this.scrollBool = true
                                  },500)
                             }
                        }
                   })
              },
              changeSession(item){
                  this.page = 1
                   this.session = item
                   this.goodsList = []
                   this._getGoodsList(8)
              }
         },
         mounted(){
         },
         created() {
             this.session = this.$route.query.sckillTime
              this.isskillTime = this.$route.query.sckillTime
             this._getGoodsList(this.session)
              this.getHomeMes()
         },
         activated(){
              this.$refs.seckillContent.scrollTop = this.scrollTop
              if (this.$route.meta.keepLoadBool){
                   this.session = this.$route.query.sckillTime
                   this.isskillTime = this.$route.query.sckillTime
                   this._getGoodsList(this.session)
                   this.getHomeMes()
              }
         },
         components:{
              HeaderPage
         },
         beforeRouteLeave(to,from,next){
             if (to.name == 'DetailsPage'){
                  from.meta.keepLoadBool = false
             }else{
                  from.meta.keepLoadBool = true
             //     åˆå§‹åŒ–æ•°æ®
                  //åœºæ¬¡æ•°ç»„
                  sessionList:[8,14,20],
                      //å½“å‰çŠ¶æ€çš„åœºæ¬¡
                      this.session = 8,
                      this.page = 1,
                      this.goodsList = [],
                      this.scrollBool = true,
                      this.moreBool = true,
                      this.headerCsBool = false,
                      this.headerBannerTop = 0,
                      this.homeMes = null,
                      this.isskillTime = 0,
                      this.isLoading = false,
                      this.scrollTop = 0
             }
             next()
         }
    }
</script>





