<template>
     <!--æˆ‘çš„æŠ½å¥–-->
     <div class="my-draw">
          <div class="fool-header">
               <div class="header">
                    <div @click="goBack" class="returnBlack-par">
                         <img class="returnBlack" src="@/common/image/publicImg/nav_ico_Return_black.png" alt="">
                         <span class="header-title">æˆ‘çš„æŠ½å¥–</span>
                    </div>
               </div>
               <ul>
                    <li class="active-table" @click="changeOrder(-1,$event)">å…¨éƒ¨</li>
                    <li @click="changeOrder(0,$event)">æœªä¸­å¥–</li>
                    <li @click="changeOrder(1,$event)">å·²ä¸­å¥–</li>
                    <li @click="changeOrder(2,$event)">å·²é¢†å–</li>
                    <li @click="changeOrder(3,$event)">å·²å‘è´§</li>
               </ul>
          </div>
          <ul class="order-list">
               <li @click.stop="toOrderEetails(item.order_no)" v-for="(item,index) in drawOrder">
                    <div class="imgWai">
                         <img :src=item.thumb alt="">
                    </div>
                    <div class="order-comInfo">
                         <p>{{ item.title }}è¯¶å“¦å…¥å›´æˆ‘å¶ç„¶å¾·ç”Ÿç§‘æŠ€ç¦åˆ©å¡å°‘å¥³æˆæœ¬å¥³æ—¶ä»£å³»å³°è§è¯†åˆ°äº†å°…å“¦å‘œè£</p>
                         <span>å¹¸è¿ç :1000{{ item.duobao_id }}</span>
                         <span>ç¬¬1000{{ item.aid }}æœŸ</span>
                         <span @click.stop="getGoods(item)" v-if="item.self_status == 1" class="getGoods">é¢†å–å¥–å“</span>
                         <span v-if="item.self_status == 0" style="background-color: #9d9d9d" class="getGoods">æœªä¸­å¥–</span>
                         <span v-if="item.self_status == 2" style="" class="getGoods">æŸ¥çœ‹è®¢å•</span>
                         <span v-if="item.self_status == 3" style="background-color: green" class="getGoods">å·²å‘è´§</span>
                    </div>
               </li>
          </ul>
     </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
     export default {
          name: "MyDraw",
          data(){
               return {
                    drawOrder:null,
                    orderStatu:-1
               }
          },
          methods:{
               //è¿”å›žä¸Šä¸€é¡µ
               goBack(){
                    if (this.$route.query.is_user){
                         this.$route.meta.keepAlive = false
                         location.href = window.EQUITMENTBACK
                    }else{
                         this.$route.meta.keepAlive = false
                         window.history.go(-1);
                    }

               },
               //åˆ°è®¢å•è¯¦æƒ…
               toOrderEetails(order_no){
                 this.$router.push({name:"rderetails",query:{order_no}})
               },
               //é¢†å–å¥–å“
               getGoods(item){
                    this.$store.commit("setDrawInfo",item)
                    location.href = "#/entdraw"
               },
               //ç‚¹å‡»table
               changeOrder(states,ev){
                    $(ev.originalTarget).addClass("active-table").siblings().removeClass("active-table")
                    this.orderStatu = states
                    this.getDrawOrder()
               },
               getDrawOrder(){
                    var obj = {
                         status:this.orderStatu
                    }
                    this.postObj(obj,window.PATHS.DUOBAO_GETMYLIST,(result) => {
                         if (result.code == 1){
                              this.drawOrder = result.data
                              console.log(this.drawOrder)
                         } else{
                              this.$toast.center(result.info)
                         }

                    })
               },
          },
          created(){
               this.getDrawOrder()
          },
          components:{
               HeaderPage
          }
     }
</script>

<style scoped>
     .header-title{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%);
          font-size: 1.7rem;
     }
     .returnBlack{
          width: 1.25rem;
          height: 2.2rem;
     }
     .returnBlack-par{
          width: 10%;
          height: 100%;
          display: flex;
          align-items: center;
          padding-left: 1.2rem;
     }
     .header{
          height: 4.4rem;
          position: relative;
          width: 100%;
          background-color: #ffffff;
     }
     .getGoods{
          display: flex;
          align-items: center;
          justify-content: center;
          width: 7.5rem;
          height: 2.5rem;
          background-color: #e7204f;
          position: absolute;
          right: 0;
          border-radius: 1.3rem;
          bottom: 0;
          color: #ffffff !important;
     }
     .order-comInfo p{
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
          font-size: 1.3rem;

     }
     .order-comInfo span{
          font-size: 1.3rem;
          color: #888888;
     }
     .order-comInfo{
          float: right;
          width: 70%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: 100%;
          position: relative;
     }
     .imgWai{
          float: left;
          width: 8.5rem;
          height: 8.5rem;
          overflow: hidden;
     }
     .order-list li img{
          width: 100%;
     }
     .order-list li{
          padding: 1.5rem;
          background-color: #ffffff;
          margin-bottom: 1rem;
          height: 11.5rem;
          overflow: hidden;
     }
     .order-list{
          width: 35.1rem;
          margin: 0 auto;
          margin-top: 1rem;
     }
     .active-table{
          color: #e7204f;
     }
     .fool-header ul li{
          width: 20%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 1.5rem;
     }
     .fool-header ul{
          display: flex;
          justify-content: space-between;
          height: 4rem;
     }
.fool-header{
     position: fixed;
     top: 0;
     width: 100%;
     background-color: #ffffff;
     z-index: 20;
}
     .my-draw{
          background-color: #f6f6f6;
          min-height: 100vh;
          padding-top: 8.4rem;
     }
</style>



