<style scoped lang="scss">
     .searchs ul li {
          padding: 5px 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 10rem;
          background-color: rgba(224, 224, 224, 0.36);
          margin-left: 10px;
          margin-top: 10px;
     }

     .searchs ul {
          display: flex;
          flex-wrap: wrap;
          font-size: 1.4rem;
          color: #212121;
     }

     .searchs {
          margin-bottom: 1.5rem;
          margin-top: 20px;
     }

     .contenter h3 {
          font-size: 1.4rem;
          color: #212121;
          font-weight: normal;
     }

     .contenter {
          width: 35.1rem;
          margin: 0 auto;
          margin-top: 1.05rem;
     }

     .headerSearch .search img {
          position: absolute;
          right: 2rem;
          top: 1rem
     }

     .headerSearch .search input {
          padding: 1.1rem 0;
          border: none;
          width: 26rem;
          height: 3.4rem;
          border-radius: 2rem;
          padding-left: 4.4rem;
          font-size: 1.3rem;
          color: #a8a8a8;
          background-color: rgba(224, 224, 224, 0.36);
          width: 100%;
     }

     .head-right a {
          margin-left: 1.55rem;
          line-height: 3.4rem;
          font-size: 1.4rem;
          color: #9d9d9d;
     }

     .headerSearch .search {
          margin-left: -0.8rem;
          position: relative;
          flex-grow: 1;
     }

     .headerSearch {
          width: 100%;
          padding: 0.5rem 1.2rem;
          display: flex;
          justify-content: space-between;
          box-shadow: 0 0 2px 0 #d1d1d1;
          align-items: center;

     }
     .content {
          height: 100%;
     }
     .hisrory_search{
          display: flex;
          justify-content: space-between;
          align-items: center;
          img{
               width: 1rem;
               height: 1rem;
          }
     }

</style>

<!--æœç´¢é¡µé¢-->
<template>
     <div class="content">
          <div class="headerSearch steep_padding">
                    <div @click="goBack" style="width: 3.3rem;height: 100%">
                         <img src="@/common/image/publicImg/left_return.png" style="width: 0.85rem;height: 1.65rem;"/>
                    </div>
                    <div class="search">
                         <form action="" v-on:submit="searchResult(null)">
                              <input ref="inptFouc" type="text" v-model="searchText" placeholder="æœç´¢å•†å“">
                         </form>
                         <img @click="searchText=null" src="@/common/image/detailsImg/nav_ico_Eliminate.png"
                              style="width: 1.4rem;height: 1.4rem;"/>
                    </div>
               <div class="head-right">
                    <a @click="searchResult(null)">æœç´¢</a>
               </div>
          </div>
          <div class="contenter">
               <div class="historySearch searchs" v-if="searchHistoryList.length != 0">
                    <div class="hisrory_search">
                         <h3>åŽ†å²æœç´¢</h3>
                         <img @click="cleanSearchHistory" src="@/common/image/searchImg/clearall.png" alt="">
                    </div>
                    <ul>
                         <li @click="searchResult(item)" v-for="(item,index) in searchHistoryList"
                             :key="index + 'hisins'">{{ item }}
                         </li>
                    </ul>
               </div>
               <div class="searchs" v-if="hotWordsList.length != 0">
                    <div>
                         <h3>çƒ­é—¨æœç´¢</h3>
                    </div>
                    <ul>
                         <li @click="searchResult(item.keyword)" v-for="(item,index) in hotWordsList" :key="index + 'hotins'">{{ item.keyword }}</li>
                    </ul>
               </div>
          </div>
     </div>
</template>

<script>
     import storage from '@/common/js/storage.js'
     import { Dialog } from 'vant';
     export default {
          name: "SearchPage",
          data() {
               return {
                    searchText: null,
                    searchHistoryList: [],
                    hotWordsList:[]
               }
          },
          methods: {
               //æ¸…ç©ºæœç´¢è®°å½•
               cleanSearchHistory() {
                    Dialog.confirm({
                         message: 'ç¡®å®šåˆ é™¤å…¨éƒ¨æœç´¢åŽ†å²ï¼Ÿ',
                         cancelButtonColor:"#9d9d9d",
                         confirmButtonColor:"#f55056"
                    }).then(() => {
                         this.searchHistoryList = []
                         storage.remove("searchHistory")
                    }).catch(() => {
                    });

               },
               //ç‚¹å‡»æœç´¢ã€
               searchResult(itemText) {
                    if (itemText) {
                         this.searchText = itemText
                    } else {
                         if (this.searchText == null || !this.searchText) {
                              this.$toast.center("è¯·è¾“å…¥æ‚¨æƒ³æœç´¢çš„å…³é”®å­—")
                              return
                         } else {
                              if (storage.get("searchHistory")) {
                                   var clearArr = storage.get("searchHistory")
                                   var newarr = new Set(clearArr)
                                   if (!newarr.has(this.searchText)) {
                                        if (clearArr.length >= 8) {
                                             clearArr.pop()
                                        }
                                        clearArr.unshift(this.searchText)
                                        storage.set("searchHistory", clearArr)
                                   }
                              } else {
                                   var arr = []
                                   arr.push(this.searchText)
                                   storage.set("searchHistory", arr)
                                   console.log(storage.get("searchHistory"))
                              }
                         }
                    }
                    location.replace("#/searcjresult?searchText=" + this.searchText)
                    // this.$router.push({name: "searcjresult", query: {searchText: this.searchText}})
               },
               //è¿”å›žä¸Šä¸€çº§
               goBack() {
                    window.history.go(-1)
               },
          //     èŽ·å–çƒ­é—¨æœç´¢å…³é”®å­—
               getHotWords(){
                    var obj = {}
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_HOT_KEYWORDS,(result) => {
                         if (result.code == 1){
                              this.hotWordsList = result.data
                         } else{
                              this.$toast.center(result.info)
                         }
                    })
               },
          },
          mounted() {
               this.$nextTick(() => {
                    this.$refs.inptFouc.focus()
               })
          },
          created() {
               this.getHotWords()
               //     èŽ·å–æœç´¢è®°å½•
               if (storage.get("searchHistory")) {
                    this.searchHistoryList = storage.get("searchHistory")
               }
          }

     }
</script>





