<style scoped>
     .header{
          position: relative;
     }
     .add_address{
          font-size: 1.3rem;
     }
     .is_default{
          background-color: #f55056;
          color: #ffffff;
          display: inline-block;
          /*line-height: 2rem;*/
          width: 3.35rem;
          border-radius: 1rem;
          text-align: center;
          margin-right: 5px;
     }
     .usdate_address{
          font-size: 1.1rem;
          color: #9d9d9d;
          padding: 20px 0;
          padding-left: 30px;

     }
     .address_info p{
          font-size: 1.1rem;
          width: 19.75rem;
     }
     .address_info{
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-left: 10px;
     }
     .address_content{
          display: flex;
          justify-content: flex-start;
     }
     .like_img{
          width: 4rem;
          height: 4rem;
          border-radius: 50%;
          background-color: #9d9d9d;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.9rem;
          color: #ffffff;
          position: relative;
     }
     ul li{
          padding: 10px 12px;
          background-color: #ffffff;
          display: flex;
          justify-content: space-between;
          align-items: center;
     }
     .place-content > span{
          display: inline-block;
          width: 100%;
          line-height: 4.9rem;
          background-color: #e7204f;
          text-align: center;
          font-size: 1.7rem;
          color: white;
          position: fixed;
          bottom: 0;
     }
     .place-content > div ul li > div > img{
          position: absolute;
          right: 1.5rem;
          top: 1.4rem;
     }
     .place-content >>> .van-radio__label{
          font-size: 1.2rem;
          color: #8a857c;
     }
     .place-content{
          background-color: #f6f6f6;
          height: 100vh;
          overflow-y: scroll;
     }
     .flex_header{
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          z-index: 11;
     }
     .none_address_header{
          background-color: #f55056;
          color: #ffffff;
          font-size: 1rem;
          padding: 5px 13px;
     }
</style>
<template>
     <div class="place-content" :style="{paddingTop:padTop + 'px'}">
          <div ref="flexHeader" class="flex_header">
               <div class="header">
                    <HeaderPage>
                         <span slot="headerTitle">æ”¶è´§åœ°å€</span>
                         <span slot="headerRight" class="add_address" v-on:click.prevent="toNewPlace">æ–°å¢žæ”¶è´§åœ°å€</span>
                    </HeaderPage>
               </div>
               <div class="none_address_header">è‹¥è´­ä¹°ç‰¹æ®Šå•†å“æˆ–è¿›å£å•†å“ï¼Œè¯·å¡«å†™çœŸå®žçš„æ”¶è´§äººä¿¡æ¯ï¼Œå¦åˆ™æ— æ³•æ”¶è´§</div>
          </div>
          <div>
               <van-radio-group v-model="radio">
                    <ul>
                         <li v-for="(item,index) in addressList" :key="index + 'ins'" @click.prevent="chooseAddress(item,index)">
                              <div class="address_content">
                                   <div class="like_img">
                                        {{ item.user_name[0] }}
                                   </div>
                                   <div class="address_info">
                                        <div>
                                             <span style="font-size: 1.4rem">{{ item.user_name }}</span>
                                             <span style="font-size: 1.2rem;color: #9d9d9d;margin-left: 10px;">{{ item.user_phone }}</span>
                                        </div>
                                        <p> <span v-if="item.isdefault == 1" class="is_default">é»˜è®¤</span>{{ item.provinceName + " " + item.cityName + " " + item.countryName + " " + item.user_address}}</p>
                                   </div>
                              </div>
                                   <span @click.stop="changeAddress(item)" class="usdate_address">ç¼–è¾‘</span>
                         </li>
                    </ul>
               </van-radio-group>
          </div>

     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     export default {
          name: "PlacePage",
          data(){
               return{
                    radio:0,
                    addressList:[],
                    padTop:0
               }
          },
          mounted(){
              // èŽ·å–åœ°å€åˆ—è¡¨
              this.getAddList()
               this.padTop = this.$refs.flexHeader.clientHeight

          },
          methods:{
               //é€‰æ‹©åœ°å€å­˜åˆ°vuex
               chooseAddress(item,index){
                    this.$store.commit("setAddress",item)
                    setTimeout(function () {
                         window.history.go(-1)
                    },200)
               },
               //èŽ·å–åœ°å€åˆ—è¡¨
               getAddList(){
                    this.postObj({},window.PATHS.ADDRESS_GET_LIST,(data) => {
                         if (data.code == 1){
                              this.addressList = data.data
                              console.log(this.addressList)
                         }
                    })
               },
               //ç¼–è¾‘åœ°å€
               changeAddress(item){
                    this.$router.push({name:"newplace",params:{item}})
               },
               //ç‚¹å‡»è®¾ç½®ä¸ºé»˜è®¤
               seDefault(item,index){
                    var placeObj = {
                         aid:item.aid,
                         option:'default',
                    }
                    this.postObj(placeObj,window.PATHS.ADDRESS_SET_OPTION,(result) => {
                         if (result.code == 0){
                              alert(result.info)
                              return
                         }
                    })
               },
               toNewPlace(){
                    location.href = '#/newplace'
               }
          },
          components:{
               HeaderPage
          }
     }
</script>





