                                      <span>èŠ±å‘—æ”¯ä»˜</span>
                                 </div>
                                 <img :class="checkedQi? 'activePay' :''" class="pat_right" src="@/common/image/entcom/payright.png" alt="">
                            </section>
                            <ul v-if="checkedQi">
                                 <li @click="comShore('alipay',3)">
