<script>
     import HeaderPage from "../../components/HeaderPage"
export default {
     name:"payDefeated",
    data(){
        return{
            orderInfo:null,
            allPrice:0,
            userInfo:null,
            checkedQi:false,
            showBack:false,
            duration:1800,
            time:'00:00:00',
        }
    },
    methods:{
        getPartBool(bool){
            this.showBack = bool
        },
        changeTime(value){
            var min = Math.floor(value%3600);
            var time = Math.floor(value/3600) + ":" + (Math.floor(min/60) < 10? '0'+Math.floor(min/60):Math.floor(min/60)) + ":"+ (value % 60 < 10? '0' + value % 60 : value % 60);
            return time;
        },
        // è¿”å›ž
        toBack(){
            this.backBool = true
             var url = "#/rderetails?order_no=" + this.$store.state.order_no
             location.replace(url)
        },
        // æ”¯ä»˜
        comShore(type,hb_fq_num){
            var url = "app://shoporder/pay/" + this.orderInfo.order_no + '/' + this.orderInfo.pay_price + '?type=' + type
                if(hb_fq_num){
                    url+='&hb_fq_num=' + hb_fq_num
                }
                location.href = url
        },
        getOrder(){
            var obj = {
                order_no:this.$store.state.order_no
                    // order_no:"SPBC112478457033660"
            }
            this.postObj(obj,window.PATHS.SHOPS_ORDER_GET_INFO,(result) => {
                if(result.code == 1){
                    this.orderInfo = result.data
                    this.userInfo = result.user
                    this.orderInfo.goods.forEach((item) => {
                        this.allPrice += Number(item.amount_price)
                    })
                    console.log(result)
                }else{
                    this.$toast.center(result.info)
                }
            })
        }
    },
    mounted(){
    },
    created(){
        var that = this
        window.timeOver = setInterval(() => {
            that.duration--
            that.time = that.changeTime(that.duration)
        },1000)
        this.getOrder()
    },
    computed:{
               // åˆ†ä¸‰æœŸ
            thrExpect(){
                return this.stagePrice(this.orderInfo.pay_price,3,2.3)
            },
            // åˆ†å…­æœŸ
            sixExpect(){
                return this.stagePrice(this.orderInfo.pay_price,6,4.5)
            },
            // åˆ†åäºŒæœŸ
            twelveExpect(){
                return this.stagePrice(this.orderInfo.pay_price,12,7.5)
            }
        },
     components:{
          HeaderPage
     },
        beforeRouteLeave(to,from,next){
                clearInterval(window.timeOver)
                next()
        },
}
</script>




