<style scoped lang="scss">
     .active_table {
          font-weight: 600;
          color: #ffffff;
     }
     #fresh_index {
          height: 100vh;
          overflow-y: scroll;
          background-color: #92d1f5;
     }
     .header_table ul li {
          margin-right: 2.5rem;
          font-size: 1.5rem;
          display: inline-block;
          position: relative;
     }
     .active_table:after{
          content: "";
          display: block;
          width: 40%;
          position: absolute;
          bottom: -0.5rem;
          left: 50%;
          height: 0.3rem;
          transform: translateX(-50%);
          background-color: #ffffff;
          border-radius: 0.5rem;
     }
     .header_table ul {
          display: inline-block;
          font-size: 1.3rem;
          white-space: nowrap;
          padding-left: 1.5rem;
     }
     .header_table {
          overflow-x: scroll;
          width: 100%;
          height: 5rem;
          display: flex;
          align-items: center;
          background-color: #92d1f5;
          border-bottom: solid 1px #ffffff;
          }
     .left_back {
          width: 4rem;
          line-height: 100%;
          text-align: left;
     }
     .fresh_header {
          background-size: 100% auto;
          background-position: 0 0;
          background-repeat: no-repeat;
          overflow: hidden;
          position: fixed;
          width: 100%;
          top: 0;
          z-index: 20;
          background-color: #ffffff;
     }
     .fresh_content{
          padding-top: 4.4rem
     }
     .banner{
          width: 100%
     }
     .fresh_goods_list{
          padding: 0 1.2rem;
          margin-top: 1.3rem;
          ul{
               display: flex;
               justify-content: space-between;
               flex-wrap: wrap;
               li{
                    background-color: #ffffff;
                    border-radius: 0.5rem;
                    width: 49%;
                    overflow: hidden;
                    margin-bottom: 1rem;
                    .goods_thumb{
                         width: 100%;
                         padding-top: 100%;
                         background-size: cover;
                    }
                    .goods_info{
                         padding: 0.6rem 0.8rem;
                         p{
                              font-size: 1.5rem;
                              text-overflow: -o-ellipsis-lastline;
                              overflow: hidden;
                              text-overflow: ellipsis;
                              display: -webkit-box;
                              -webkit-line-clamp: 2;
                              line-clamp: 2;
                              -webkit-box-orient: vertical;
                         }
                         .goods_pirce_btn_buy{
                              margin-top: 1.4rem;
                              .goods_price{
                                   font-size: 1.5rem;
                                   color: #ff0000
                              }

                         }
                         .market_price_part{
                              display: flex;
                              align-items: center;
                              justify-content: space-between;
                              .market_price{
                                   color: #9d9d9d;
                                   text-decoration: line-through;
                                   font-size: 1.1rem
                              }
                              button{
                                        width: 5.5rem;
                                        height:2rem;
                                        border-radius:1rem;
                                        background-color: #f55056;
                                        border: none;
                                        font-size: 1.1rem;
                                        color: #ffffff
                                   }
                         }

                    }
               }
          }
     }
     .activeScroll{
          overflow: hidden !important;
     }
</style>
<template>
<!--ç”Ÿé²œé¡µé¢-->
     <div ref="freshIndex" id="fresh_index" class="steep_padding" @scroll="scroll">
               <div class="fresh_header" ref="freshHeader">
                    <HeaderPage>
                         <span slot="headerTitle">ç”Ÿé²œä¸“åŒº</span>
                              <img @click="toSearch" slot="headerRight" style="width:2.2rem;height:2.2rem" src="@/common/image/publicImg/search.png" alt="">
                    </HeaderPage>
               </div>

               <div class="fresh_content">
                    <img class="banner" src="@/common/image/fresh/banner.png" alt="">
                    <van-sticky :offset-top="stickyTop">
                          <div ref="headerTable" class="header_table">

                              <ul>
                                   <li
                                        ref="tableLi"
                                        :class="index == tabIns? 'active_table' : ''"
                                        v-for="(item,index) in infoList"
                                        :key="index + 'tagins'"
                                        @click="tabclk(item.aid,index)"
                                   >
                                   <span>{{ item.name }}</span>
                                   </li>
                              </ul>
                    </div>
                    </van-sticky>
                    <div class="fresh_goods_list">
                         <ul>
                              <li @click="toDetail(item.aid)" v-for="(item,index) in goodsList" :key="index + 'ins'">
                                   <div class="goods_thumb" :style="{backgroundImage:'url('+ item.thumb +')'}"></div>
                                   <div class="goods_info">
                                        <p>{{ item.title }}</p>
                                        <div class="goods_pirce_btn_buy">
                                             <span class="goods_price">
                                                  <span>ç‰¹æƒ ä»·:</span><span style="font-size:1.3rem">ï¿¥</span><span class="num_family">
                                                       {{ item.shop_price.substring(0,item.shop_price.length - 1) }}
                                                  </span>
                                             </span>

                                        </div>
                                        <div class="market_price_part">
                                             <span class="market_price">åŽŸä»·:ï¿¥{{ item.market_price.substring(0,item.market_price.length - 3) }}</span>
                                             <button>é©¬ä¸ŠæŠ¢</button>
                                        </div>
                                   </div>
                              </li>
                         </ul>
                    </div>
               </div>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "MustbuyGoods",
          data() {
               return {
                    tabIns:0,
                    infoList:[],
                    goodsList:[],
                    page:1,
                    stickyTop:0,
                    tableScroll:0,
                    scrollBool:true,
                    moreBool:true,
                    scrollTop:0,
                    scrollLeft:0
               }
          },
          methods: {
               // ä¸Šæ»‘äº‹ä»¶
               touchUp(){
                    this.$toast.center("æ»‘åŠ¨äº†")
               },
               // åˆ°æœç´¢
               toSearch(){
                    location.href = "#/search"
               },
               // åˆ°å•†å“è¯¦æƒ…é¡µ
               toDetail(good_id){
                     this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
               },
               // æ»šåŠ¨
               scroll(){
                    this.scrollTop = this.$refs.freshIndex.scrollTop
                    this.stickyTop = this.$refs.freshHeader.clientHeight

                    var scrollTop = this.$refs.freshIndex.scrollTop
                    var height = this.$refs.freshIndex.scrollHeight
                    const windowHeight = window.innerHeight
                    this.scrollMore(scrollTop,height,windowHeight,() => {
                         if(this.scrollBool){
                              this.scrollBool = false
                              if(this.moreBool){
                                   this.page++
                                   this.getGoodsList(this.infoList[this.tabIns].aid)
                              }
                         }
                    })
               },
               // ç‚¹å‡»table
               tabclk(aid,index){
                    var liLeft =  this.$refs.tableLi[index].offsetLeft
                    var evWidth = this.$refs.tableLi[index].clientWidth / 2
                    var windWidth = document.body.offsetWidth / 2
                    this.tableScroll = liLeft - windWidth + evWidth
                    $(".header_table").animate({
                         scrollLeft: liLeft - windWidth + evWidth
                    },200)

                    if(this.tabIns == index){
                         return
                    }
                    this.tabIns = index
                    this.page = 1
                    this.getGoodsList(aid,(data) => {
                         this.goodsList = data
                    })
               },
               getList(){
                    var obj = {
                         pid:2172
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_COLUMN,(result) => {
                         if (result.code == 1){
                              this.infoList = result.data
                              this.infoList.unshift({
                                   name:"ç²¾é€‰ç‰¹æƒ "
                              })
                              this.getGoodsList(this.infoList[0].aid)
                         }
                    })
               },
               // èŽ·å–å•†å“åˆ—è¡¨
               getGoodsList(son_column_id,callback){

                    if(this.tabIns == 0){
                          var obj = {
                              page:this.page,
                              top_column_id:2172
                         }
                    }else{
                    var obj = {
                         page:this.page,
                         son_column_id
                    }
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_GET_LIST,(result) => {
                         if(result.code == 1){
                              if(callback){
                                   callback(result.data)
                              }else{
                                   if(result.data.length == 0){
                                        this.moreBool = false
                                        return
                                   }
                                   this.goodsList = this.goodsList.concat(result.data)
                                   setTimeout(() => {
                                        this.scrollBool = true
                                   },500)
                                   console.log(this.goodsList)
                              }

                         }
                    })
               },
               // åˆå§‹åŒ–dataæ•°æ®
               undateData(){
                    this.tabIns = 0
                    this.infoList = []
                    this.goodsList = []
                    this.page = 1
                    this.stickyTop = 0
                    this.tableScroll = 0
                    this.scrollBool = true
                    this.moreBool = true,
                    this.scrollTop = 0
                    this.scrollLeft = 0
               },
          },
          created() {
               this.getList()
          },
          activated(){
                    this.$refs.freshIndex.scrollTop = this.scrollTop
                    this.$refs.headerTable.scrollLeft = this.scrollLeft
                    if(this.$route.meta.keepLoadBool){
                         this.getList()
                    }
          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to,from,next){
               if(to.name == "DetailsPage" || to.name == 'search'){
                    this.scrollLeft = this.$refs.headerTable.scrollLeft
                    from.meta.keepLoadBool = false
               }else{
                    this.undateData()
                     from.meta.keepLoadBool = true
               }
               next()
          }
     }
</script>





