<template>
     <!--é™æ—¶æŠ½å¥–-->
     <div class="Zero-content steep_padding">
          <div class="draw_head">
               <HeaderPage>
                    <span slot="headerTitle">æŠ½å¥–</span>
                    <div slot="headerRight" @click="toSearch" class="header_right">
                         <img src="@/common/image/publicImg/search.png" alt="" style="width: 1.5rem;height: 1.5rem;">
                    </div>
               </HeaderPage>

          </div>
          <ul>
               <li v-for="(item,index) in comData" :key="index + 'ins'" @click="todetails(item.aid)">
                    <img :src="item.thumb"/>
                    <div class="li-right">
                         <p>{{ item.title }}</p>
                         <div class="progress_parent">
                              <div class="progress">
                                   <div class="progress-num" :style="{width:item.extra_data.current/item.extra_data.number*100 + '%'}"></div>
                              </div>
                              <span>å·²æŠ½å¥–{{(item.extra_data.current/item.extra_data.number*100).toFixed(2) + '%' }}</span>
                         </div>
                                   <div class="draw_price">
                                        <span class="num_family" style="font-size: 1.8rem;color: #f55056">{{ item.extra_data.single_price }}</span>
                                        <img src="@/common/image/detailsImg/zuanshi@2x.png" alt="" style="width: 1.7rem;height: 1.35rem;">
                                        <span style="text-decoration: line-through;font-size: 1.2rem;color: #888888;">ï¿¥{{ item.market_price }}</span>
                                   </div>
                                        <div>
                                             <img src="@/common/image/publicImg/mail_ico_Crowd.png" alt="" style="width: 1.25rem;height: 1rem;">
                                             <span style="margin-left: 2px;"><span style="color: #f55056" class="num_family">{{ item.sales }}</span>äººå·²ä¸­å¥–</span>
                                        </div>
                    </div>
                    <div style="display: flex;align-items: center;justify-content: space-between;">
                         <span class="draw_now">ç«‹å³æŠ½å¥–</span>
                    </div>
               </li>
          </ul>
     </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
     export default {
          name: "ZeroPage",
          data() {
               return {
                    comData: ''
               }
          },
          methods: {
               //åˆ°æœç´¢é¡µé¢
               toSearch(){
                    location.href = "#/search"
               },
               //è½¬æ¢å€’è®¡æ—¶
               getTime(time){
                    var min=Math.floor(time%3600);
                    return Math.floor(time/3600) + "æ—¶" + Math.floor(min/60) + "åˆ†"+ time%60 + "ç§’";
               },
               onClickLeft() {
                    window.history.go(-1)
               },
               todetails(good_id) {
                    this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
               }
          },
          mounted() {
               // èŽ·å–æ•°æ®
               var obj1 = {page: 1, goods_type: 'goods_duobao'};
               this.postObj(obj1, window.PATHS.SHOPS_GOODS_GET_LIST, (result) => {

                    if (result.code == 0) {
                         alert(result.info)
                         return
                    }
                    this.comData = result.data
                    for (var i = 0; i < this.comData.length; i++) {
                         if (!this.comData[i].extra_data.current) {
                              this.comData[i].extra_data.current = 1
                         }
                    }
                    console.log(this.comData)
               })

          },
          components:{
               HeaderPage
          }
     }
</script>

<style scoped>
     .progress_parent > span{
          color: #9d9d9d;
          font-size: 0.9rem;
          margin-left: 10px;
     }
     .progress_parent{
          display: flex;
          align-items: center;
     }
     .header_title{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: 1.7rem;
     }
     .draw_head{
          width: 100%;
          position: fixed;
          top: 0;
          background-color: #ffffff;
          z-index: 10;
     }
     .progress-num {
          width: 50%;
          height: 100%;
          background-color: #f55056;
          position: absolute;
          z-index: 9;
          top: 0;
          left: 0;
          border-radius: 0.7rem;
     }

     .progress {
          position: relative;
          width: 12rem;
          height: 0.7rem;
          background-color: #f6f6f6;
          border-radius: 0.6rem;
     }

     .draw_now {
          position: absolute;
          display: block;
          width: 7.6rem;
          line-height: 3rem;
          text-align: center;
          background-color: #f55056;
          border-radius: 1.5rem;
          color: #ffffff;
          font-size: 1.4rem;
          bottom: 1rem;
          right: 1rem;
     }

     .Zero-content ul li .li-right > div:last-of-type {
          width: 100%;
          display: flex;
          align-items: center;
     }

     .Zero-content ul li .li-right p {
          height: 3.5rem;
          font-size: 1.3rem;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
          font-weight: 500;
     }

     .Zero-content ul li .li-right {
          margin-left: 1rem;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
     }

     .Zero-content ul li > img {
          width: 10rem;
          height: 10rem;
          border-radius: 1rem;
          display: flex;
          flex-shrink: 0;
     }

     .Zero-content ul li {
          padding: 15px 15px;
          background-color: white;
          display: flex;
          justify-content: flex-start;
          margin-bottom: 1rem;
          position: relative;
          border-radius: 0.5rem;
     }

     .Zero-content ul {
          padding: 1.2rem;
          padding-top: 5.4rem;
     }

     .van-nav-bar {
          line-height: 4.4rem;
          height: 4.4rem;
     }

     .van-nav-bar__title {
          font-size: 1.7rem;
     }

     .van-nav-bar__arrow {
          font-size: 2.5rem;
          color: #000;
     }

     .Zero-content {
          background-color: #f6f6f6;
          min-height: 100vh;
          padding-top: 5.4rem;
     }
</style>



