                             <img src="@/common/image/publicImg/huiRight.png" alt="">
                        </div>
                   </div>
                   <div class="order_info border_radius_bottom border_radius_top">
                        <div style="display: flex;align-items: center">
                             <span>è®¢å•ç¼–å·:{{ order_info.order_no }}</span>
                             <span class="copy" @click="copy(order_info.order_no)">å¤åˆ¶</span>
                        </div>
                        <div style="margin-top: 1rem">
                             <span>ä¸‹å•æ—¶é—´:</span>
                             <span>{{ order_info.create_time }}</span>
                        </div>
                        <div style="margin-top: 1rem" v-if="order_info.pay_time != ''">
                             <span>ä»˜æ¬¾æ—¶é—´:</span>
                             <span>{{ order_info.pay_time }}</span>
                        </div>
                        <div style="margin-top: 1rem" v-if="order_info.shipping_time != ''">
                             <span>å‘è´§æ—¶é—´:</span>
                             <span>{{ order_info.shipping_time }}</span>
                        </div>
                        <div style="margin-top: 1rem" v-if="order_info.success_time != ''">
                             <span>æ”¶è´§æ—¶é—´:</span>
                             <span>{{ order_info.success_time }}</span>
                        </div>

                   </div>
                   <div class="footer steep_padding_bottom">
                        <template v-if="order_info.goods_types != 'goods_group'">
                             <template v-if="order_info.goods_types.indexOf('goods_duobao') == -1">
                                  <span v-if="order_info.order_status == 0 || order_info.order_status == 1" @click.stop="show = true" class="clearSpan">å–æ¶ˆè®¢å•</span>
                                  <span v-if="order_info.order_status == 0" class="spanRed pay_btn" @click="pay(order_info.order_no,order_info.order_price)">æ”¯ä»˜ï¿¥{{ order_info.pay_price }}</span>
                                  <span v-if="order_info.order_status == 1" @click="remind" class="waring_send">æé†’å‘è´§</span>
                                  <!--<span @click="toLogisticsUrl" class="looked_address" v-if="order_info.order_status == 3">æŸ¥çœ‹ç‰©æµ</span>-->
                                  <!--<span v-if="(order_info.order_status >= 1 && order_info.order_status != 2)" style="background-color: #e7204f;border: none; color: #ffffff;margin-left: 1rem" @click="againBuy(order_info.order_no)" >å†æ¥ä¸€å•</span>-->
                             </template>
                             <span @click.stop="lengthen(order_info.order_no)" class="lengthen" v-if="order_info.order_status == 3">å»¶é•¿æ”¶è´§</span>
                             <span class="shore_get" v-if="order_info.order_status == 3" @click="showOrder">ç¡®è®¤æ”¶è´§</span>
                             <span class="comment_btn" v-if="order_info.order_status == 4" @click="comment(order_info.order_no,false)">ç«‹å³è¯„ä»·</span>
                             <span @click="deleteOrderDe" class="clearSpan" v-if="order_info.order_status == 2">åˆ é™¤è®¢å•</span>
                        </template>
                        <template v-if="order_info.goods_types == 'goods_group'">
                             <!--<span class="clearSpan" >åˆ é™¤è®¢å•</span>-->
                             <span @click.stop="show = true" class="clearSpan" v-if="order_info.order_status == 0 || (order_info.order_status == 1 && order_info.period.info.current == 1)">å–æ¶ˆè®¢å•</span>
                             <span v-if="order_info.order_status == 1 && order_info.shipping_status == 0" @click="remind" class="waring_send">æé†’å‘è´§</span>
                             <span @click="shareTeam" class="group_invent" v-if="order_info.order_status == 1 && order_info.shipping_status == -1">é‚€è¯·å¥½å‹æ‹¼å•</span>
                             <span v-if="order_info.order_status == 0" class="spanRed pay_btn" @click="pay(order_info.order_no,order_info.order_price)">æ”¯ä»˜ï¿¥{{ order_info.pay_price }}</span>
                             <span @click.stop="lengthen(order_info.order_no)" class="lengthen" v-if="order_info.order_status == 3">å»¶é•¿æ”¶è´§</span>
                             <span class="shore_get" v-if="order_info.order_status == 3" @click="showOrder">ç¡®è®¤æ”¶è´§</span>
                             <span class="comment_btn" v-if="order_info.order_status == 4" @click="comment(order_info.order_no,false)">ç«‹å³è¯„ä»·</span>
                             <span @click="deleteOrderDe" class="clearSpan" v-if="order_info.order_status == 2">åˆ é™¤è®¢å•</span>
                        </template>
                   </div>
                   <!--æ”¯ä»˜è¿‡ç¨‹æ˜¾ç¤ºçš„å¼¹çª—-->
                   <div class="show-shore" v-if="showAlert2">
                        <h3>ç¡®è®¤æ”¯ä»˜æ˜¯å¦æˆåŠŸ</h3>
                        <div>
                             <span @click="shorePay()">ç¡®å®š</span>
                        </div>
                   </div>
                   <div class="show-logistics" v-if="showUrl">
                        <iframe id="iframeUrl" :src=order_info.logistics_url >
                        </iframe>
                        <div @click="showUrl = false" style="position: absolute;top: 0;width: 7rem;height:5rem;z-index: 900;">
                        </div>
                   </div>
              </div>
          </template>
     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     import storage from "@/common/js/storage.js"
     import { Dialog } from 'vant';
     export default {
          name: "OrderEetails",
          data(){
               return {
                    order_info:null,
                    showAlert2:false,
                    show:false,
                    radio: '-1',
                    showUrl:false,
                    //ç³»ç»Ÿæ—¶é—´æˆ³
                    xttimestamp:0,
                    //ç­‰å¾…ä»˜æ¬¾çš„æ—¶é—´
                    payOverTime:3600,
                    payOverTimeStr:'',
                    userInfo:null
               }
          },
          mounted(){
          },
          methods:{
               //åˆ°åº—é“º
               toShop(){
                    this.$router.push({name:"shop",query:{shop_id:this.order_info.shop_id}})
               },
               //åˆ°å‘ç¥¨è¯¦æƒ…
               toBillDetail(){
                    this.$store.dispatch("setOrderBillInfo",this.order_info)
                    this.$router.push({
                         name:"billdetail"
                    })
               },
               //è°ƒå–å•†å“è¯¦æƒ…
               getComInfo(goods_id, callback) {
                    var obj = {
                         goods_id
                    }
                    this.postObj(obj, window.PATHS.SHOPS_GOODS_GET_INFO, (result) => {
                         if (result.code == 1) {
                              callback(result.data)
                         } else {
                              callback(false)
                         }
                    })
               },
               //é‚€è¯·å¥½å‹æ‹¼å•
               shareTeam() {
                    var obj = {
                         title: this.order_info.goods[0].title,
                         content: "æ‹¼å›¢ä¼˜æƒ ",
                         url: window.HOST + "/goods/group?uid=" + this.userInfo.id + "&goods_id=" + this.order_info.goods[0].goods_id + "&period_id=" + this.order_info.goods[0].period_id,
                         imgsrc: this.order_info.goods[0].thumb,
                         nickname: this.userInfo.nickname,
                         headimgurl: this.userInfo.headimgurl,
                    }
                    this.getComInfo(this.order_info.goods[0].goods_id, (data) => {
                         if (data) {
                              obj.shop_price = data.shop_price,
                                  obj.market_price = data.market_price,
                                  obj.sales = data.sales,
                                  obj.needPersor = data.extra_data.number - data.extra_data.current,
                                  obj.duration = data.extra_data.duration,
                                  obj.extra_type = data.extra_table
                              console.log(obj)
                              this.shareSame(obj)
                         }
                    })
               },
               //åˆ é™¤è®¢å•
               deleteOrderDe(){
                    Dialog.confirm({
                         title: 'åˆ é™¤è®¢å•',
                         message: 'ç¡®è®¤è¦åˆ é™¤è¯¥è®¢å•å—'
                    }).then(() => {
                         var obj = {
                              order_no:this.order_info.order_no
                         }
                         this.postObj(obj, window.PATHS.ORDER_UDELORDER, (result) => {
                              if (result.code == 1) {
                                   storage.set("orderPageRefaus",true)
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },500)
                              }
                         })
                    }).catch(() => {
                    });
               },
               //å»¶é•¿æ”¶è´§
               lengthen(order_no) {
                    Dialog.confirm({
                         message: 'ç¡®è®¤å»¶é•¿æ”¶è´§æ—¶é—´ï¼Ÿæ¯ç¬”è®¢å•åªèƒ½å»¶é•¿ä¸€æ¬¡å“¦',
                         confirmButtonColor: "#f55056",
                         cancelButtonColor: "#f55056"
                    }).then(() => {
                         var obj = {
                              order_no
                         }
                         this.postObj(obj,window.PATHS.SHOPSORDER_CONFIRMDEFER,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("å»¶é•¿æ”¶è´§æˆåŠŸ")
                              }
                         })
                    }).catch(() => {
                         // on cancel
                    });
               },
               //æŸ¥çœ‹å”®åŽ
               findaftersale(item){
                    this.$router.push({name:"afterlist",query:{order_no:this.order_info.order_no, order_goods_id:item.aid}})
               },
               //åˆ°ç”³è¯·å”®åŽ
               aftersale(val){
                    val.time = this.xttimestamp
                    this.$store.dispatch("setAfterInfo",val)
                    this.$store.dispatch("setAfterOrderInfo",this.order_info)
                    location.href = "#/afterindex"
               },
               //åˆ°æ‹¼å›¢æˆåŠŸ
               toGroupSuccess(){
                    this.$store.dispatch("setGroupSuccess",this.order_info.period.lists)
                    console.log()
                    location.href = "#/ordersuccess"
               },
               //ç¡®è®¤æ”¶è´§
               showOrder(){
                    Dialog.confirm({
                         title: 'ç¡®è®¤æ”¶è´§',
                         message: 'ç¡®è®¤å·²ç»æ”¶è´§è´§ç‰©ï¼Œé¿å…äººè´¢ä¸¤ç©º'
                    }).then(() => {
                         this.affirm(this.order_info.order_no)
                    }).catch(() => {
                         // on cancel
                    });
               },
               //æé†’å‘è´§
               remind(){
                    Dialog.alert({
                         confirmButtonColor:"#f55056",
                         message: 'å–å®¶å·²æ”¶åˆ°ä½ çš„æé†’ï¼Œä¼šå°½å¿«å‘è´§'
                    }).then(() => {
                         // on close
                    });
               },
               //åˆ°å•†å“è¯¦æƒ…
               toComDetail(good_id){
                    this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
               },
               toLogisticsUrl(){
                    this.showUrl = true
                    // var urlStyle = document.getElementById('iframeUrl').contentWindow.document.getElementsByClassName("g-header")
                    // urlStyle.style.display = "none";
               },
               //å†æ¥ä¸€å•
               // againBuy(order_no){
               //      this.$store.commit('setagainOrder',order_no)
               //      location.href = "#/againorder"
               // },
               //ç«‹å³è¯„ä»·ä¸Žè¿½è¯„
               comment(order_no,bool){
                    this.postObj({order_no},window.PATHS.SHOPS_ORDER_GET_INFO,(result) => {
                         if (result.code == 1){
                              if (result.data.goods.length > 1){
                                   this.$store.commit('setCommentOrder',order_no)
                                   location.href = '#/comment'
                              } else {
                                   if (bool){
                                        this.$router.push({name:'commenteeild',params:{comInfo:result.data.goods[0],order_no,bool}})
                                   } else {
                                        this.$router.push({name:'commenteeild',params:{comInfo:result.data.goods[0],order_no}})
                                   }
                              }
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //ç¡®è®¤æ”¶è´§
               affirm(order_no){
                    var that = this
                         this.postObj({order_no},window.PATHS.ORDER_SHIOOINGORDER,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("æ”¶è´§æˆåŠŸ")
                                   storage.set("orderPageRefaus",true)
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },1500)
                              }else{
                                   this.$toast.center(result.info)
                              }
                         })
               },
               //èŽ·å–è¯¥è®¢å•ä¿¡æ¯
               getInfo(){
                    var order_no = this.$route.query.order_no
                    //èŽ·å–vuexä¼ é€’è¿‡æ¥çš„order_no
                    this.postObj({order_no},window.PATHS.SHOPS_ORDER_GET_INFO,(result) => {
                         if (result.code == 1){
                              this.order_info = result.data
                              this.userInfo = result.user
                              console.log(this.order_info,"è®¢å•è¯¦æƒ…")
                              this.xttimestamp = result.timestamp
                              //å¦‚æžœæ˜¯æœªä»˜æ¬¾æ˜¾ç¤ºå€’è®¡æ—¶
                              if (this.order_info.order_status == 0){
                                   window.payTimeOver = setInterval(() => {
                                        this.payOverTime--
                                        this.payOverTimeStr = this.changeTime(this.payOverTime)
                                   },1000)
                              }
                         }
                    })
               },
               //å–æ¶ˆè®¢å•
               deleteOrder() {
                    var that = this
                         var obj = {}
                         obj.order_no = this.$route.query.order_no
                         if (this.radio != "-1"){
                              if (this.radio == "1") {
                                   obj.remark = "æˆ‘ä¸æƒ³ä¹°äº†"
                              }
                              if (this.radio == "2") {
                                   obj.remark = "ä¿¡æ¯å¡«å†™é”™è¯¯é‡æ–°æ‹"
                              }
                              if (this.radio == "3") {
                                   obj.remark = "å–å®¶ç¼ºè´§"
                              }
                              if (this.radio == "4") {
                                   obj.remark = "åŒåŸŽè§é¢äº¤æ˜“"
                              }
                              if (this.radio == "5") {
                                   obj.remark = "å…¶ä»–åŽŸå› "
                              }
                         }else{
                              this.$toast.center("è¯·é€‰æ‹©æ‚¨å–æ¶ˆè®¢å•çš„åŽŸå› ")
                              return
                         }

                         this.postObj(obj, window.PATHS.ORDER_CANCELORDER, (result) => {
                              console.log(result)
                              this.show = false
                              setTimeout(() => {
                                   that.clearOrderBool = true
                              },1500)
                              if (result.code == 1){
                                   this.$toast.center('å–æ¶ˆæˆåŠŸ')
                                   storage.set("orderPageRefaus",true)
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },1500)
                              }else{
                                   this.$toast.center(result.info)
                              }
                         })
               },
               //ç¡®è®¤ç”¨æˆ·æ˜¯å¦ä»˜é’±äº†
               shorePay(){
                    var that = this
                    this.showAlert2 = false
                    this.postObj({order_no:this.order_info.order_no},window.PATHS.ORDER_GETORDER,(data) => {
                         if (data.code == 1){
                              if (data.data.pay_status == 1){
                                   this.$toast.center('æ”¯ä»˜æˆåŠŸ')
                                   storage.set("orderPageRefaus",true)
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },1500)
                                   return
                              }else {
                                   this.$toast.center('æ”¯ä»˜å¤±è´¥')
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },1500)
                              }
                         }else {
                              this.$toast.center(data.info)
                         }
                    })
               },
               copy(order_no){
                   location.href = "app://send/copy/?" + order_no
               },
          //     æ”¯ä»˜æˆåŠŸçš„æ–¹æ³•
          //     ä»˜æ¬¾
               pay(order_no,price){
                    var url = "app://shoporder/pay/" + order_no + '/' + price + '?type=all'
                    location.href = url
               },
          //     ç­‰å¾…ä»˜æ¬¾å€’è®¡æ—¶
          },
          created(){
               this.getInfo()
          },
          computed:{
               //å°†æ—¶é—´æˆ³è½¬æ¢æˆå¤©æ—¶åˆ†
               time(){
                    var times = ""
                    var timeSec = this.order_info.success_expire - this.xttimestamp
                         // var min = Math.floor(timeSec%3600);
                         // var times = Math.floor(timeSec/3600) + ":" + (Math.floor(min/60) < 10? '0'+Math.floor(min/60):Math.floor(min/60)) + ":"+ (timeSec % 60 < 10? '0' + timeSec % 60 : timeSec % 60);
                         var day = Math.floor(timeSec%86400)
                         var horver = Math.floor(day % 3600)
                         var seday = Math.floor(timeSec/86400) + "å¤©"
                         var hour = Math.floor(horver/3600) <= 0? "00æ—¶" : Math.floor(horver/3600) < 10? ('0' + Math.floor(horver/3600)+ 'æ—¶') : Math.floor(horver/3600)  + "æ—¶"
                         times = seday + hour
                         return times;
               },
          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to,from,next){
               clearInterval(window.payTimeOver)
               if (to.name == "DetailsPage") {
                    to.meta.keepLoadBool = true
               }
               next()
          }
     }
</script>




