<style scoped lang="scss">
     #group_success{
          height: 100vh;
          overflow-y: scroll;
          padding-top: 4.4rem;
          background-color: #f6f6f6;
     }
.group_success_header{
     display: flex;
     align-items: center;
     color: #f55056;
          img{
               width: 1.5rem;
               height: 1.5rem;
               margin-right: 15px;
          }
}
     .group_success_flex_header{
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
     }
     .group_user_img ul{
          display: flex;
          align-items: center;
          justify-content: center;
          li{
               width: 5rem;
               height: 5rem;
               background-size: cover;
               border-radius: 50%;
               margin-left: 15px;
          }
     }
     .back_home{
          display: block;
          text-align: center;
          margin: 0 auto;
          width: 31.5rem;
          line-height: 4rem;
          border-radius: 2rem;
          background-color: #f55056;
          color: #ffffff;
          font-size: 1.5rem;
          margin-top: 20px;
     }
     .group_info{
          background-color: #ffffff;
          padding-top: 16px;
          padding-bottom: 7px;
     }
     .goods_img{
          width: 100%;
          height: 18rem;
          background-size: cover;
     }
     .goodsList{
          padding: 0 12px;
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          margin-top: 10px;
          li{
               width: 49%;
               margin-bottom: 10px;
               border-radius: 5px;
               overflow: hidden;
               background-color: #ffffff;
               .goods_info{
                    padding: 7px 8px;
                    .how_people{
                         font-size: 1.1rem;
                         margin-top: 16px;
                    }
                    .goods_price{
                         margin-top: 10px;
                         .shop_price{
                              color: #f55056;
                              span{
                                   font-family: æ•°å­—ä¸“ç”¨;
                              }
                         }
                         .market_price{
                              text-decoration: line-through;
                              color: #9d9d9d;
                              margin-left: 10px;
                         }
                    }
               }
               p{
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    word-break: break-all;
                    font-size: 1.5rem;
               }
          }
     }
     .footer_loading{
          text-align: center;
          margin: 15px 0;
          >span{
               font-size: 1.3rem;
          }
     }
</style>

<template>
     <!--è®¢å•è¯¦æƒ…æ‹¼å•æˆåŠŸ-->
     <div ref="group_success" id="group_success" @scroll="scroll">
          <div class="group_success_flex_header">
               <HeaderPage>
                    <div class="group_success_header">
                         <img src="@/common/image/orderEetailsImg/bingo.png" alt="">
                         <span>æ‹¼å•æˆåŠŸ</span>
                    </div>
               </HeaderPage>
          </div>
          <div class="group_info">
               <div class="group_user_img">
                    <ul>
                         <li v-for="(item,index) in userImg" :key="index + 'useins'" :style="{backgroundImage: 'url('+ item.headimgurl +')'}"></li>
                    </ul>
               </div>
               <a onclick="javascript:history.go(-1)" class="back_home">è¿”å›ž</a>
          </div>
          <ul class="goodsList">
               <li v-for="(item,index) in goodsList" :key="index + 'lisns'">
                    <div class="goods_img" :style="{backgroundImage:'url('+ item.thumb +')'}"></div>
                    <div class="goods_info">
                         <p>{{ item.title }}</p>
                         <div class="how_people">
                              <span style="color: #f55056" class="num_family">{{ item.sales }}</span><span style="color: #9d9d9d">äººå·²æ‹¼å›¢æˆåŠŸ</span>
                         </div>
                         <div class="goods_price">
                              <span class="shop_price">
                                   <span style="font-size: 1.1rem">ï¿¥</span><span style="font-size: 1.7rem"> {{ item.shop_price }}</span>
                              </span>
                              <span class="market_price">
                                   ï¿¥{{ item.market_price }}
                              </span>
                         </div>
                    </div>
               </li>
          </ul>
          <div class="footer_loading">
               <van-loading v-if="moreBool" size="24px">åŠ è½½ä¸­...</van-loading>
               <span v-if="!moreBool">å·²åŠ è½½å…¨éƒ¨</span>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "OrderSuccess",
          data(){
               return{
                    goodsList:[],
                    page:1,
                    scrollBool:true,
                    moreBool:true,
                    userImg:[]
               }
          },
          mounted(){
          },
          methods:{
               scroll(){

                    var winHeight = window.innerHeight
                    var height = this.$refs.group_success.scrollHeight - winHeight / 2
                    var scrollTop = this.$refs.group_success.scrollTop
                    this.scrollMore(scrollTop,height,winHeight,(bool) => {
                         if (this.scrollBool){
                              this.scrollBool = false
                              this.page++
                              this.getList()
                         }
                    })
               },
               //èŽ·å–æ‹¼å›¢æ•°æ®
               getList(){
                    var that = this
                    var obj1 = {
                         goods_type:'goods_group',
                         page:this.page
                    }
                    this.postObj(obj1,window.PATHS.SHOPS_GOODS_GET_LIST,(result) => {
                         if (result.code == 1){
                              if (result.data.length == 0){
                                   this.moreBool = false
                              } else{
                                   if (result.data.length < 15) {
                                        this.moreBool = false
                                        this.goodsList = this.goodsList.concat(result.data)
                                   }else{
                                        this.goodsList = this.goodsList.concat(result.data)
                                        this.moreBool = true
                                   }

                              }
                              setTimeout(() => {
                                   that.scrollBool = true
                              },1000)
                         }
                         console.log(this.goodsList)
                    })
               },
          },
          created(){
               this.userImg = this.$store.state.groupSuccess
               this.getList()
          },
          components:{
               HeaderPage
          }
     }
</script>





