<style scoped lang="scss">
     #express_list{
          height: 100vh;
          overflow-y: scroll;
          padding-top: 5rem;
     }
     .header_search{
          display: flex;
          align-items: center;
          justify-content: space-between;
          position: fixed;
          top: 0;
          left: 0;
          z-index: 50;
          width: 100%;
          height: 4.4rem;
          background-color: #ffffff;
          padding: 0 12px;
          box-shadow: 0 2px 5px 0 #e7e7e7;
          .left{
               position: relative;
               width: 5%;
               height: 100%;
               img{
                    width: 0.85rem;
                    height: 1.65rem;
               }
          }
          input{
               height: 2.9rem;
               width: 85%;
               border: none;
               font-size: 1.5rem;
               background-color: #f6f6f6;
               border-radius: 1.45rem;
               padding-left: 20px;

          }
          .search_btn{
               width: 10%;
               display: inline-block;
               text-align: right;
               font-size: 1.4rem;
          }
     }
     .search_result{
          position: fixed;
          left: 0;
          top: 4.4rem;
          background-color: #ffffff;
          width: 100%;
          height: 100vh;
          overflow-y: scroll;
          z-index: 20;
          font-size: 1.4rem;
          ul{
               li{
                    margin-top: 1rem;
                    padding: 1rem 1.2rem;
               }
          }
     }
</style>

<template>
     <!--ç‰©æµå…¬å¸åˆ—è¡¨-->
     <div id="express_list" v-if="expressList.length != 0">
          <div class="header_search" @click="showSearch = true">
               <div class="left" onclick="javascript:history.go(-1)">
                    <img class="same_img" src="@/common/image/publicImg/left_return.png" alt="">
               </div>
               <input v-model="textVal" type="text" placeholder="è¯·è¾“å…¥å¿«é€’å…¬å¸åç§°">
               <span class="search_btn" v-if="!searBool">æœç´¢</span>
               <span @click="searBool = false,textVal = ''" class="search_btn" v-if="searBool">å–æ¶ˆ</span>
          </div>
          <div class="search_result" v-if="searBool">
               <ul>
                    <li @click="choose(item)" v-for="(item,index) in searchArr" :key="item.code">
                         {{ item.name }}
                    </li>
               </ul>
          </div>
          <van-index-bar v-if="!searBool" :sticky="false" :index-list="abcList" :default="sckil">
                         <van-index-anchor :index="item" v-for="(item,index) in abcList" :key="index + 'abc'">
                              <span>{{ item }}</span>
                              <van-cell
                                      @click="choose(textInfo)"
                                      v-for="(textInfo,ins) in sliall(item)"
                                      :key="textInfo + 'txt'"
                                      :title="textInfo.name"
                              />
                         </van-index-anchor>
          </van-index-bar>
     </div>
</template>

<script>
     export default {
          name: "ExpressList",
          data(){
               return{
                    expressList:[],
                    abcList:["A","B","C","D","E","F","G","H","I","J","K","L","M","N","O","P","Q","R","S","G","U","V","W","X","Y","Z",],
                    textVal:"",
                    sckil:"B",
                    showSearch:true,
                    searchArr:[],
                    searBool:false
               }
          },
          methods:{
               choose(val){
                    this.$store.dispatch("setExpress",val)
                    window.history.go(-1)
               },
          //     èŽ·å–ç‰©æµå…¬å¸
               getExpressList(){
                    var obj = {}
                    this.postObj(obj,window.PATHS.aftersale.INDEX_GETKUAIDICOMPANY,(result) => {
                         if (result.code == 1){
                              this.expressList = result.data
                              console.log(this.expressList)
                         }
                    })
               },

          },
          watch:{
               textVal(oldVal,newVal){
                    oldVal = oldVal.toUpperCase()
                    const str = this.expressList.filter((item) => {
                         if (item.code.indexOf(oldVal) > -1){
                              return true
                         }
                         if (item.name.indexOf(oldVal) > -1){
                              return true
                         }
                         return false
                    })
                    this.searchArr = str
                    if (this.searchArr.length == 0 || oldVal == ""){
                         this.searBool = false
                    }else{
                         this.searBool = true
                    }
               }
          },

          created() {
               this.getExpressList()
          },
          computed:{
               sliall(){
                    return function(val){
                         var newArr = []
                         this.expressList.forEach((item,index) => {
                              if (item.abc == val){
                                   newArr.push(item)
                              }
                         })
                         return newArr
                    }
               }
          }
     }
</script>





