<template>
     <div class="img-content" style="min-height: 100vh;display: flex;align-items: center">
          <div class="black-bg"></div>
               <swiper :options="swiperOption" ref="imgOverview" style="height: 100%;">
                    <swiper-slide v-for="(img,index) in imgArr" :key="index">
                         <div class="swiper-zoom-container" >
                              <img class="" :src="img" alt="" @click="goBack">
                         </div>
                    </swiper-slide>
               </swiper>
          </div>
</template>

<script>
     export default {
          name: "ImgPage",
          data(){
               return {
                    imgArr: [],
                    imgIndex:null,
                    swiperOption: {
                         zoom: true,
                         pagination: {
                              el: '.swiper-pagination'
                         },
                         navigation: {
                              nextEl: '.swiper-button-next',
                              prevEl: '.swiper-button-prev'
                         }
                    },
                    backBool:false
               }
          },
          mounted() {
               this.imgArr = this.$store.state.lookImgArr
               this.imgIndex = this.$store.state.imgIndex
          },
          methods:{
               goBack(){
                    window.history.go(-1)
               },

          }
     }
</script>

<style scoped>
     .swiper-container{
          width: 100vw !important;
          height: 100vh !important;
          display: flex;
          align-items: center;
     }
     .black-bg{
          /*background-color: #000;*/
          width: 100vw;
          height: 100vh;
          position: absolute;
          top: 0;
          left: 0;

     }
     .img-content >>> .swiper-wrapper{
          display: flex !important;
          align-items: center !important;
          max-height: 100vh;
     }
     img{
          max-width: 100vw;
          min-width: 100vw;
     }
.content-img{
     /*background-color: #000;*/
     width: 100vw;
     height: 100vh;
     position: relative;
}
</style>



