//èŽ·å–token
const getToken = function(to) {
     if (to.query.token) {
          window.token = to.query.token
          storage.set('token',to.query.token)
     }else{
          window.token = storage.get('token')
     }
     if (to.query.token === ""){
          window.token = ""
          storage.set('token',"")
     }
     var userAgaen = navigator.userAgent
     if (userAgaen.indexOf("com.ajiani.maidahui") == -1){
          window.token = 'fd8870a59fb0c327fd5b506d5f9eeb02'
     }
}
//è·¯ç”±å®ˆå«
router.beforeEach((to, from, next) => {
     getToken(to)
     next()
})

export default router



