<script>
     export default {
          name: "CollectPage",
          data(){
               return {
                    active:0,
                    isUser:true,
                    goodsPage:1,
                    goodsList:null,
                    shopsList:null,
                    shopsPage:1,
                    headerHeight:0,
                    isLoading: false,
                    num:6
               }
          },
          methods:{
               //å–æ¶ˆæ”¶è—å•†å“
               cleanLikeGood(goods_id,index){
                    var obj = {
                         goods_id
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_COLLECT,(result) => {
                         if (result.code == 1){
                              this.goodsList.splice(index,1)
                              this.$toast.center("å–æ¶ˆæˆåŠŸ")
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               //å–æ¶ˆæ”¶è—åº—é“º
               cleanLikeShop(shop_id,index){
                    var obj = {
                         shop_id
                    }
                    this.postObj(obj,window.PATHS.SHOPS_STAR,(result) => {
                         if (result.code == 1){
                              this.shopsList.splice(index,1)
                              this.$toast.center("å–æ¶ˆæˆåŠŸ")
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               //åˆ·æ–°
               onRefresh() {
                    var that = this
                    setTimeout(() => {
                         this.$toast('åˆ·æ–°æˆåŠŸ');
                         this.isLoading = false;
                         that.getLikeShops()
                         that.getLikeGoods()
                    }, 500);
               },
               //åˆ°å•†å“è¯¦æƒ…
               toGoodsDetail(aid){
                    this.$router.push({name: 'DetailsPage', query: {comId: aid}})
               },
               clkTable(name,title){
                    if (name == 1){
                         if (this.shopsList == null){
                             this.getLikeShops()
                         }
                    }

               },
               //èŽ·å–æˆ‘çš„æ”¶è—åº—é“º
               getLikeShops(){
                    var obj = {
                         page:this.shopsPage
                    }
                    this.postObj(obj,window.PATHS.SHOPS_STAR_LIST,(result) => {
                         if (result.code == 1){
                              this.shopsList = result.data
                              console.log(this.shopsList)
                         } else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               //èŽ·å–æˆ‘çš„æ”¶è—å•†å“
               getLikeGoods(){
                    var obj = {
                         page:this.goodsPage
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_COLLECT_LIST,(result) => {
                         if (result.code == 1){
                              this.goodsList = result.data
                              // èŽ·å–é¡¶éƒ¨çš„é«˜åº¦
                              console.log(this.goodsList)
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               goBack(){
                    if (this.$route.query.is_user){
                         this.$route.meta.keepAlive = false
                         location.href = window.EQUITMENTBACK
                    }else{
                         this.$route.meta.keepAlive = false
                         window.history.go(-1);
                    }

               },
               showShopClear(index){
                    this.$refs.Shoopswipecell[index].open('right')
               },
               showGoodsClear(index){
                    this.$refs.Goodswipecell[index].open('right')
               },
               todetaile(){
               },
