          mounted(){
               this.headerHeight = this.$refs.header.clientHeight
               this.$toast.center(this.headerHeight)
               this.$route.meta.keepAlive = true
          },
          created() {
               this.getLikeGoods()
          },
          beforeRouteLeave(to,from,next){
               if (to.name == "DetailsPage") {
                    from.meta.keepAlive = true
               }
               if (to.name == "shop") {
                    from.meta.keepAlive = true
               }
               next()
          },
