                    }
               }
          }
     }
     .ater_prcie_patr{
          display: flex;
          align-items: center;
     }
     .remake{
          padding: 15px;
     }
     .return_need{
          background-color: #ffffff;
          margin-top: 7px;
     }
     .remake{
          font-size: 1.3rem;
          input{
               border: none;
          }
     }
     .need_pictuat{
          padding: 15px;
          h5{
               font-size: 1.3rem;
          }

     }
.updata_img{
     width: 80px;
     height: 80px;
     position: relative;
     img{
          width: 100%;
          height: 100%;
     }
     span{
          position: absolute;
