          color: #9d9d9d;
     }
}
     .updata_img_part{
          margin-top: 13px;
     }
     .need_pictuat{
          border-top: solid 1px #f6f6f6;
     }
     .submit_btn{
          display: inline-block;
          position: fixed;
          bottom: 10px;
          left: 50%;
          transform: translateX(-50%);
          line-height: 4rem;
          width: 90%;
          text-align: center;
          background-color: #f55056;
          font-size: 1.5rem;
          color: #ffffff;
          border-radius: 2rem;
     }
     .get_bot_type{
          padding: 0 13px;
          padding-top: 15px;
          h4{
               text-align: center;
               font-size: 1.5rem;
          }
     }
     .et_oods_adio{
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 15px 0;
          font-size: 1.5rem;
     }
     .et_oods_adio_one{
          border-bottom: solid 1px #f6f6f6;
     }
     .sely_back{
          font-size: 1.1rem !important;
          color: #9d9d9d;
     }
     .addres_info{
          width: 100%;
          font-size: 1.1rem;
          display: flex;
          justify-content: space-between;
          margin-left: 15px;
          >img{
               margin-left: 10px;
          }
          p{
               margin-top: 7px;
          }
          .name_phone{
               display: flex;
               align-items: center;
               justify-content: space-between;
          }
     }
     .get_address{
          flex-shrink: 0;
     }
     .address_li{
          align-items: flex-start !important;
     }
     .return_price{
          border: none;
          font-family: æ•°å­—ä¸“ç”¨;
     }
     .aftersale_content{
          padding-top: 4.4rem;
     }
</style>
<template>
     <!--ç”³è¯·å”®åŽ-->
