               <!--å”®åŽæ–¹å¼-->
               <div class="after_table" v-if="!afterTypeBool">
                    <ul>
                         <li @click="afterTypeBool = true ,afterType = 1">
                              <img src="@/common/image/aftersale/Rectangle-path@2x.png" alt="">
                              <div>
                                   <h5>é€€æ¬¾</h5>
                                   <span>æ²¡æœ‰æ”¶åˆ°è´§ç‰©ï¼Œæˆ–ä¸Žå–å®¶åå•†ä¸é€€è´§åªé€€æ¬¾</span>
                              </div>
                         </li>
                         <li @click="afterTypeBool = true ,afterType = 2">
                              <img src="@/common/image/aftersale/huanhuo@2x.png" alt="">
                              <div>
                                   <h5>é€€è´§é€€æ¬¾</h5>
                                   <span>å·²æ”¶åˆ°è´§ï¼Œéœ€è¦é€€è¿˜æ”¶åˆ°çš„è´§ç‰©</span>
                              </div>
                         </li>
                         <li  @click="afterTypeBool = true ,afterType = 3">
                              <img src="/@/common/image/aftersale/huanhuo@2x (2).png" alt="">
                              <div>
                                   <h5>æ¢è´§</h5>
                                   <span>å·²æ”¶åˆ°è´§ï¼Œéœ€è¦æ›´æ¢æ”¶åˆ°çš„è´§ç‰©</span>
                              </div>
                         </li>
                    </ul>
               </div>
               <!--å”®åŽæ–¹å¼é€‰æ‹©åŽçš„å†…å®¹-->
               <!--é€€æ¬¾-->
               <template v-if="afterTypeBool">
                    <div class="return_info">
                         <ul>
                              <li @click="showGetGoods = true" v-if="afterType == 1">
                                   <span>è´§ç‰©çŠ¶æ€</span>
                                   <div class="li_right">
                                        <span class="hui_span">{{ GetGoodsRadio == '-1'? 'è¯·é€‰æ‹©' : GetGoodsRadio == '1'? 'æœªæ”¶åˆ°è´§ç‰©' : 'å·²æ”¶åˆ°è´§ç‰©'  }}</span>
                                        <img src="@/common/image/publicImg/huiRight.png" alt="">
                                   </div>
                              </li>
                              <li @click="showCause = true">
                                   <span v-if="afterType==3">æ¢è´§åŽŸå› </span>
                                   <span v-if="afterType==1 || afterType==2">é€€æ¬¾åŽŸå› </span>
                                   <div class="li_right">
                                        <span class="hui_span">{{ cuaseType == -1? 'è¯·é€‰æ‹©' : causeList[cuaseType] }}</span>
                                        <img src="@/common/image/publicImg/huiRight.png" alt="">
                                   </div>
                              </li>
                              <li v-if="afterType == 3">
                                   <span v-if="afterType==3">æ¢è´§å•†å“</span>
                                   <span class="sely_back">{{ orderGoodsInfo.specs }}</span>
                              </li>
                              <li v-if="afterType == 1 || afterType == 2">
                                   <div class="ater_prcie_patr">
                                        <div class="ater_prcie">é€€æ¬¾é‡‘é¢:ï¿¥
                                             <input class="return_price" type="number" v-model="price">
                                             <!--<span class="num_family" style="color: #f55056">ï¿¥{{ price }}</span>-->
                                        </div>
                                   </div>
                              </li>
                              <li v-if="afterType == 2">
                                   <span>é€€è´§æ–¹å¼</span>
                                   <span class="sely_back">è‡ªè¡Œå¯„å›ž</span>
                              </li>
                              <li class="address_li" v-if="afterType == 3" @click="toAddress">
                                   <span class="get_address">æ”¶è´§åœ°å€</span>
                                   <div class="addres_info">
                                        <div style="width: 100%;">
                                             <div class="name_phone">
                                                  <span>{{ receiver_name }}</span>
                                                  <span>{{ receiver_phone }}</span>
                                             </div>
                                             <p>{{ receiver_address }}</p>
                                        </div>
                                        <img src="@/common/image/publicImg/huiRight.png" alt="">
                                   </div>
                              </li>
                         </ul>
                         <div class="return_need">
                              <div class="remake">
                                   <span>é€€æ¬¾è¯´æ˜Ž:</span>
                                   <input v-model="remake" type="text" placeholder="é€‰å¡«">
                              </div>
                              <div class="need_pictuat">
                                   <h5>ä¸Šä¼ å‡­è¯</h5>
                                   <div class="updata_img_part">
                                        <van-uploader
                                                v-model="img"
                                                multiple
                                                :max-count="3"
                                                accept="image/*"
                                        >
                                             <div  class="updata_img">
                                                  <img src="@/common/image/commentImg/usdateImg.png" alt="">
