                         </div>
                    </div>
               </template>

               <!--è´§ç‰©æ˜¯å¦æ”¶åˆ°è´§çš„ä¸‹æ‹‰å¼¹çª—-->
               <van-popup
                       v-model="showGetGoods"
                       position="bottom"
                       :style="{ height: '50%' }"
                       round
               >
                    <div class="get_bot_type">
                         <h4>è´§ç‰©çŠ¶æ€</h4>

                         <van-radio-group v-model="GetGoodsRadio" checked-color="#f55056">
                              <div class="et_oods_adio et_oods_adio_one">
                                   <span>æœªæ”¶åˆ°è´§ç‰©</span>
                                   <van-radio name="1" @click="showGetGoods = false"></van-radio>
                              </div>
                              <div class="et_oods_adio">
                                   <span>å·²æ”¶åˆ°è´§ç‰©</span>
                                   <van-radio name="2" @click="showGetGoods = false"></van-radio>
                              </div>
                         </van-radio-group>
                    </div>
               </van-popup>
               <!--é€€æ¬¾åŽŸå› -->
               <van-popup
                       v-model="showCause"
                       position="bottom"
                       :style="{ height: '50%' }"
                       round
               >
                    <div class="get_bot_type">
                         <h4>é€€æ¬¾åŽŸå› </h4>

                         <van-radio-group v-model="cuaseType" checked-color="#f55056">
                              <div class="et_oods_adio et_oods_adio_one" v-for="(item,index) in causeList" :key="index + 'cauins'">
                                   <span>{{ item }}</span>
                                   <van-radio :name="index" @click="showCause = false"></van-radio>
                              </div>
                         </van-radio-group>
                    </div>
               </van-popup>
               <span v-if="afterTypeBool" @click="submitAfter" class="submit_btn">æäº¤</span>
          </div>
     </div>
</template>

<script>
     import { Dialog } from 'vant';
     import HeaderPage from "../../components/HeaderPage"
     import lrz from 'lrz'
     export default {
          name: "AfterIndex",
          data(){
               return{
                    afterTypeBool:false,
                    headerText:"ç”³è¯·å”®åŽ",
                    orderGoodsInfo:null,
                    showGetGoods:false,
                    showCause:false,
                    img: [],
                    GetGoodsRadio:"-1",
                    afterType:-1,
               //     é€€è´§åŽŸå› 
                    causeList:["é€€è¿è´¹","å•†å“æè¿°çš„å°ºå¯¸ä¸Žå®žç‰©ä¸ç¬¦","å•†å“æè¿°çš„é¢œè‰²/æ¬¾å¼ä¸Žå®žç‰©ä¸ç¬¦","å•†å“æè¿°çš„æè´¨é¢æ–™ä¸Žå®žç‰©ä¸ç¬¦","å•†å“è´¨é‡é—®é¢˜","ç¼©æ°´/è¤ªè‰²","å°‘ä»¶/æ¼å‘","åŒ…è£…/å•†å“ç ´æŸ/æ±¡æ¸","æœªæŒ‰çº¦å®šæ—¶é—´å‘è´§","å–å®¶å‘é”™è´§","å¼€çº¿/èµ°ä¸","å…¶ä»–"],
                    cuaseType:-1,
                    isSeven:false,
                    remake:"",
                    orderInfo:null,
                    receiver_name:"",
                    receiver_address:"",
                    receiver_phone:"",
                    price:"",
                    newImgArr:[],
                    outTime:null
               }
          },
          methods:{
               //å°†base64è½¬æˆæ–‡ä»¶
               toFile(dataurl, filename) {
                    var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
                        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
                    while (n--) {
                         u8arr[n] = bstr.charCodeAt(n);
                    }
                    return new File([u8arr], filename, {type: mime});
               },
               //åŽ‹ç¼©å›¾ç‰‡è½¬æˆbase64
               dealImage(path, type, obj, callback) {
                    var img = new Image();
                    img.src = path;
                    img.onload = function () {
                         var that = this;
                         // é»˜è®¤æŒ‰æ¯”ä¾‹åŽ‹ç¼©
                         var w = that.width, h = that.height;
                         if (obj.width && obj.width < w ){
                              w = obj.width;
                              h = w / that.width * that.height
                         }
                         if (obj.height && obj.height < h ) {
                              h = obj.height;
                              w = h / that.height * that.width
                         }
                         //ç”Ÿæˆcanvas
                         var canvas = document.createElement('canvas'),
                             ctx = canvas.getContext('2d');
                         canvas.width = w;
                         canvas.height = h;
                         ctx.drawImage(that, 0, 0, w, h);

                         // é»˜è®¤å›¾ç‰‡è´¨é‡ä¸º0.7
                         var quality = 0.3;
                         if (obj.quality && obj.quality <= 1 && obj.quality > 0) {
                              quality = obj.quality;
                         }
                         // å›žè°ƒå‡½æ•°è¿”å›žbase64çš„å€¼
                         var base64 = canvas.toDataURL(type);
                         callback(base64);
                    }
               },
               //ä¸Šä¼ å›¾ç‰‡åˆ°åŽå°
               sendImg(file){
                    var that = this
                    this.postFile(file, (result) => {
                         if (result.code == 1) {
                              this.newImgArr.push(result.data.id)
                              if (this.newImgArr.length == this.img.length){
                                   var ImgStar = this.newImgArr.join(",")
                                   this._same(ImgStar)
                              }
                         } else {
                              that.$toast.center(result.info)
                         }
                    })
               },
               //åˆ°æ”¶è´§åœ°å€
               toAddress(){
                    location.href = "#/place"
               },
               _same(imglist){
                    var that = this
                    var obj = {
                         order_goods_id:this.orderGoodsInfo.aid,
                         refund_type:this.afterType,
                         is_shipping:this.GetGoodsRadio == "-1"? '' : this.GetGoodsRadio == "1"? 0 : 1
                    }
                    //     æ˜¯ä¸ƒå¤©
                    if (this.isSeven) {
                         if (this.cuaseType == -1){
                              this.$toast.center("è¯·é€‰æ‹©å”®åŽåŽŸå› ")
                              return
                         }
                    }
                    if (this.cuaseType == this.causeList.length - 1){
                         if (this.remake == ""){
                              this.$toast.center("è¯·å¡«å†™é€€è´§è¯´æ˜Ž")
                              return
                         }
                    }
                    if (this.price > this.orderGoodsInfo.amount_price - this.orderGoodsInfo.refund_amount) {
                         this.$toast.center("é€€æ¬¾é‡‘é¢ä¸å¯å¤§äºŽè®¢å•æ€»ä»·æ ¼")
                         return
                    }
                    if (this.afterType == 3){
                         obj.receiver_name = this.receiver_name
                         obj.receiver_phone = this.receiver_phone
                         obj.receiver_address = this.receiver_address
                    }
                    obj.refund_price = this.price
                    obj.reason = this.causeList[this.cuaseType]
                    obj.content = this.remake
                    obj.picture = imglist
                    this.$loading("æäº¤ä¸­")
                    this.postObj(obj,window.PATHS.aftersale.SHOPSREFUND_ADDORDER,(result) => {
                         if (result.code == 1){
                              this.$loading.close()
                              this.$toast.center("æäº¤ç”³è¯·æˆåŠŸ")
                              setTimeout(() => {
                                   //æŸ¥çœ‹å”®åŽ
                                   location.replace("#/afterlist?order_no=" + this.orderInfo.order_no + "&order_goods_id=" + this.orderGoodsInfo.aid)
                              },500)
                         }
                         console.log(result)
                    })
               },
               submitAfter(){

                    var that = this
                    if (this.img.length == 0){
                         this._same("")
                    } else{
                         this.img.forEach((item,index) => {
                              // ä¸ç”¨åŽ‹ç¼©
                              if (!(item.file.size >=2097152)) {
                                   that.sendImg(item.file)
                              }else{
                                   //     åŽ‹ç¼©
                                   var reader = new FileReader();
                                   reader.readAsDataURL(item.file);
                                   reader.onload = function (e) {
                                        var image_base64 = this.result;
                                        that.dealImage(image_base64, item.file.type, {
                                             width: 2000,
                                             height:2000,
                                             quality: 0.7
                                        }, function (base64Codes) {
                                             var data = that.toFile(base64Codes, item.file.name)
                                             that.sendImg(data)
                                        });
                                   }
                              }
                         })
                    }

               },
               firstGet(){
                    this.orderGoodsInfo = this.$store.state.afterInfo
                    this.orderInfo = this.$store.state.afterOrderInfo
                    this.price = this.orderGoodsInfo.amount_price - this.orderGoodsInfo.refund_amount
                    console.log(this.orderGoodsInfo,"å•†å“ä¿¡æ¯")
                    console.log(this.$store.state.afterOrderInfo,"è®¢å•ä¿¡æ¯")
                    var date = new Date(this.$store.state.afterOrderInfo.success_time.replace(/-/g, '/'))
                    //æ”¶è´§æ—¶é—´æŒ«
                    var time3 = date.getTime() / 1000;
                    var restTime = this.orderGoodsInfo.time - time3
                    //åˆ¤æ–­æ˜¯å¦å¤§äºŽ7å¤©
                    if (restTime < 601200){
                         this.isSeven = false
                         this.causeList.unshift("ä¸ƒå¤©æ— ç†ç”±")
                         this.cuaseType = 0
                         console.log(this.causeList)
                    }else{
                         this.cuaseType = -1
                         this.isSeven = true
                    }
                    this.outTime = setTimeout(() => {
                         Dialog.alert({
                              message: 'å·²è¶…æ—¶'
                         }).then(() => {
                              window.history.go(-1)
                         });
                    },3600000)
                    if (this.$store.state.address) {
                         this.receiver_name = this.$store.state.address.user_name
                         this.receiver_address = this.$store.state.address.provinceName + " " + this.$store.state.address.cityName + " " + this.$store.state.address.countryName + " " + this.$store.state.address.user_address
                         this.receiver_phone = this.$store.state.address.user_phone
                    }else{
                         this.receiver_name = this.$store.state.afterOrderInfo.receiver_name
                         this.receiver_address = this.$store.state.afterOrderInfo.receiver_address
                         this.receiver_phone = this.$store.state.afterOrderInfo.receiver_phone
                    }
               },
               leave(){
                    this.afterTypeBool = false
                    this.showGetGoods = false
                    this.showCause = false
                    this.img = []
                    this.GetGoodsRadio = "-1"
                    this.afterType = -1
                    this.cuaseType = -1
                    this.isSeven = false
                    this.remake = ""
                    this.orderInfo = null
                    this.receiver_name = ""
                    this.receiver_address = ""
                    this.receiver_phone = ""
                    this.price = ""
                    this.newImgArr = []
                    clearTimeout(this.outTime)
                    this.causeList = ["é€€è¿è´¹","å•†å“æè¿°çš„å°ºå¯¸ä¸Žå®žç‰©ä¸ç¬¦","å•†å“æè¿°çš„é¢œè‰²/æ¬¾å¼ä¸Žå®žç‰©ä¸ç¬¦","å•†å“æè¿°çš„æè´¨é¢æ–™ä¸Žå®žç‰©ä¸ç¬¦","å•†å“è´¨é‡é—®é¢˜","ç¼©æ°´/è¤ªè‰²","å°‘ä»¶/æ¼å‘","åŒ…è£…/å•†å“ç ´æŸ/æ±¡æ¸","æœªæŒ‰çº¦å®šæ—¶é—´å‘è´§","å–å®¶å‘é”™è´§","å¼€çº¿/èµ°ä¸","å…¶ä»–"]
                    this.$store.commit("setAddress",null)
               },
          },
          created(){
               this.firstGet()
          },
          activated(){
               if (this.$route.meta.keepLoadBool){
                    this.firstGet()
               }
          },
          beforeRouteLeave(to,from,next){
               if (to.name == "rderetails") {
                    this.leave()
                    from.meta.keepLoadBool = true
               }
               next()
          },
          components:{
               HeaderPage
          }
     }
</script>





