<template>
     <!--ä¸­å¥–è®°å½•-->
     <div class="drawlist-content">
         <div class="drawlist-header">
              <HeaderPage>ä¸­å¥–è®°å½•</HeaderPage>
         </div>
          <ul>
               <li @click="towindraw(item)" v-for="(item,index) in drawList" :key="index + 'drawins'">
                    <img :src=item.thumb alt="" style="width: 10rem;height: 9.8rem;background-color: black">
                    <div>
                         <h3 style="color: #212121;font-size: 1.6rem">ç¬¬1000{{ item.aid }}æœŸ</h3>
                         <span style="color: #e9204f">ä¸­å¥–äºº:&nbsp{{ item.nickname }}</span>
                         <span>å¹¸è¿ç :&nbsp1000{{ item.duobao_id }}</span>
                         <span>ä¸­å¥–æ—¶é—´:&nbsp{{ item.end_time }}</span>
                    </div>
                    <img src="@/common/image/detailsImg/3@2x.png" style="width: 1.1rem;height: 1.9rem;position: absolute;right: 1.3rem;top: 6.3rem;"/>
               </li>
          </ul>
     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     export default {
          name: "DrawList",
          data(){
               return {
                    drawList:null,
                    page:1
               }
          },
          components:{
               HeaderPage
          },
          methods:{
               towindraw(obj){
                    this.$store.commit("setDrawInfo",obj)
                    location.href = '#/windraw'

               },
               getDrawList(){
                    var obj = {
                         goods_id:this.$route.query.goods_id,
                         page:this.page
                    }
                    this.postObj(obj,window.PATHS.DUOBAO_CURRENTLIST,(result) => {
                         if (result.code == 1){
                              this.drawList = result.data
                              console.log(this.drawList)
                         } else{
                              this.$toast.center(result.info)
                         }
                    })
               },
          },
          created() {
               this.getDrawList()

          }
     }
</script>

<style scoped>
     ul li > div span{
          display: block;
          margin-top: 0.6rem;
     }
     ul li > div{
          margin-left: 1rem;
     }
     ul li{
          display: flex;
          justify-content: left;
          padding-top: 2.4rem;
          padding-bottom: 2.4rem;
          padding-left: 1.2rem;
          font-size: 1.4rem;
          color: #9d9d9d;
          background-color: white;
          margin-bottom: 1rem;
          position: relative;
     }
.drawlist-header{
     position: fixed;
     top: 0;
     width: 100%;
     box-shadow: 0.1rem 0 0.6rem #b6b6b6;
     z-index: 15;
}
     .drawlist-content{
          background-color: #f6f6f6;
          margin-top: 4.4rem;
     }
</style>



