                         return
                    }
                    this.postObj({},window.PATHS.GAME.GAMESTREE_USER_SEEDS,(result) => {
                         if (result.code == 1){
                              this.bagList = result.data
                              if (this.bagList.length != 0){
                                   this.seedBool = true
                              } else {
                                   this.$toast.center("èƒŒåŒ…ä»€ä¹ˆç§å­éƒ½æ²¡æœ‰å“¦ï¼åŽ»å•†åŸŽè´­ä¹°å§")
                              }
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               // //èŽ·å–èƒŒåŒ…é“å…·
               getBagprops(){
                    if (this.propsBool){
                         this.propsBool = false
                         return
                    }
                    this.postObj({},window.PATHS.GAME.GAMESTREE_USER_PROPS,(result) => {
                         if (result.code == 1){
                              this.propsList = result.data
                              // this.
                              console.log(this.propsList)
                              if ( this.propsList.length != 0){
                                   this.propsBool = true
                              } else {
                                   this.$toast.center("å·¥å…·é‡Œé¢ä»€ä¹ˆéƒ½æ²¡æœ‰å“¦!åŽ»å•†åŸŽè´­ä¹°å§")
                              }
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //èŽ·å–ç”¨æˆ·ç§æ¤çŠ¶æ€
               getSignState(){
                    this.postObj({},window.PATHS.GAME.GAMESTREE_GET_LANDS,(result) => {
                         if (result.code == 1){
                              this.signState = result.data
                              if (this.signState.length > 0){
                                   if (this.signState[0].level == 2){
                                        if ( storage.get("firstBool") == null || !storage.get("firstBool")) {
                                             this.firstAlert = true
                                        }
                                        storage.set("firstBool",1)
                                   }
                                   if (this.signState[0].level == 6){
                                        storage.remove("firstBool")
                                             this.lastAlert = true
                                   }
                              }
                         }else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //ç§ä¸‹ä¸€é¢—ç§å­
               sendSeed(seed_id){
                    if (this.signState.length != 0){
                         this.$toast.center("å·²æœ‰ç§å­åœ¨ç§æ¤äº†ï¼")
                    } else {
                         var obj = {
                              land_no:this.land_no,
                              seed_id:seed_id
                         }
                         this.postObj(obj,window.PATHS.GAME.GAMESTREE_SEND_SEEDS,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("ç§æ¤æˆåŠŸ")
                                   this.getSignState()
                              } else {
                                   this.$toast.center(result.info)
                              }
                         })
                    }
               },
               //ç­¾åˆ°
               sign(){
                    this.postObj({},window.PATHS.GAME.GAMESTREE_SIGN,(result) => {
                         if (result.code == 1){
                              this.getSignList()
                              this.$toast.center("ç­¾åˆ°æˆåŠŸ")
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //åˆ°å•†åŸŽé¡µé¢
               toShop(){
                    location.href = "#/gameshop"
               },
               //å¼¹å‡ºç­¾åˆ°é¢æ¿
               signIn() {
                    this.getSignList()
                    this.signBool = true
                    this.blackBool = true
               },
               //èŽ·å–ç­¾åˆ°è¡¨
               getSignList(){
                    this.postObj({},window.PATHS.GAME.GAMESTREE_SIGN_LIST,(result) => {
                         if (result.code == 1){
                              this.signList = result.data
                              console.log(this.signList)
                         } else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               userWater() {
                    this.$toast.center("æµ‡æ°´äº†")
               },
               //è¿”å›žä¸Šä¸€é¡µ
               goBack(){
                    location.href = "#/"
               },
          //     èŽ·å–æŽ’è¡Œæ¦œ
               getRank(){
                    var obj = {
                         size:10,
                         page:this.page,
                         is_rbot:1
                    }
                    this.postObj(obj,window.PATHS.GAME.GAMESTREE_RANKING,(result) => {
                         if (result.code == 1){
                              this.rankList = result.data
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               },
          //     èŽ·å–ç”¨æˆ·ä¿¡æ¯
               getUserInfo(){
                    this.postObj({},window.PATHS.MEBER_GET_INFO,(result) => {
                         if (result.code == 1){
                              this.userInfo = result.data
                         }else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          },
          mounted() {
               var that = this
               if (window.timeGame) {
                    clearInterval(window.timeGame)
               }
               window.timeGame = setInterval(function () {
                    // console.log(that.$store.state.gameMessageList)
                    var arr = that.$store.state.gameMessageList.shift()
                    if (arr) {
                         that.messageBool = false
                         setTimeout(function () {
                              that.message =  arr
                              that.messageBool = true
                         },500)
                    }
               },5000)

               this.getSignState()
               this.getRank()
               this.getUserInfo()
               this.getIntegral()
          },
     }
</script>

