<template>
     <!--æ¸¸æˆå•†åŸŽ-->
     <div class="gameShop-content">
          <div class="header">
               <HeaderPage>å•†åŸŽ</HeaderPage>

          </div>
          <!--å¯¼èˆª-->
          <div class="table">
               <div v-if="activeWidth != null" class="active-li" :style="{width:activeWidth + 'px',left:tableLeft + 'px'}"></div>
               <ul>
                    <li ref="tableLi" @click="tableCilk($event,0)">
                         <span>è®¸æ„¿æ ‘</span>
                    </li>
                    <li @click="tableCilk($event,1)">
                         <span>ç§å­</span>
                    </li>
                    <li @click="tableCilk($event,2)">
                         <span>é“å…·</span>
                    </li>
               </ul>
          </div>
          <div  style="display: inline-block;width: 6.5rem;height: 2.5rem;border-radius: 1.25rem;background-color:rgb(244,211,165);margin-top: 1rem;margin-left: 2.2rem;display: flex;align-items: center;padding-left: 1rem;margin-bottom: 1rem">
               <img src="@/common/image/game/maisui@2x.png" style="width: 2rem;height: 2rem;"/>
               <span v-if="userInfo != null" style="margin-left: 0.4rem;color: rgb(138,87,42);font-size: 1.1rem">{{ userInfo.integral }}</span>
          </div>
          <!--ç§å­ä¸Žé“å…·-->
          <div class="fruits" v-if="buySeed || buyProps">
               <ul>
                    <li  @click="buy(item)" v-for="(item,index) in seedList" :key="index + 'ins'">
                         <div style="width: 100%;height: 7rem;display: flex;justify-content: center;align-items: center">
                              <img :src=item.icon />
                         </div>
                         <h5 style="font-size: 1.1rem;text-align: center">{{ item.name }}</h5>
                         <span style="font-size: 0.9rem;display: block;text-align: center;margin-top: 1rem">æˆç†ŸåŽå¯èŽ·å¾—æžœæ ‘ç§å­</span>
                         <div style="width: 9.35rem;height: 3rem;display: flex;justify-content: center;align-items: center;background-color: rgb(83,204,143);border-radius: 0.5rem;position: absolute;bottom: 0.5rem;left: 50%;transform: translateX(-50%)">
                              <img src="@/common/image/game/maisui@2x.png" style="width: 1.75rem;height: 1.75rem;"/>
                              <span style="margin-left: 0.5rem;font-size: 1.5rem;color: #ffffff;">{{ item.price }}</span>
                         </div>
                    </li>
                    <li>
                         <img src="@/common/image/game/amizingting.png" style="width: 100%;height: 100%;"/>
                    </li>
               </ul>
          </div>
          <!--è®¸æ„¿æ ‘-->
          <div class="wishing" v-if="buyWishing">
               <ul>
                    <li v-for="(item,index) in seedList" @click="buy(item)">
                         <img :src=item.goods_thumb alt="">
                         <div>
                              <p>{{ item.goods_name }}</p>
                              <span style="font-size: 1.2rem;color: #9d9d9d">{{ item.name }}</span>
                              <div class="buy-button">
                                   <img src="@/common/image/game/maisui@2x.png" style="width: 1.75rem;height: 1.75rem;"/>
                                   <span style="font-size: 1.5rem;color: #ffffff;margin-left: 0.5rem">{{ item.price }}</span>
                              </div>
                         </div>
                    </li>
               </ul>
          </div>
          <!--ç¡®è®¤è´­ä¹°çš„å¼¹çª—-->
          <div v-if="buyBool" class="shore-buy">
               <div style="width: 100%;text-align: center;margin-top: 1rem">
                    <img :src=aletItem.icon style="width:5.5rem;height: 6.1rem;margin-bottom: 2rem ;"/>
               </div>
               <div v-if="buyProps" style="display: flex;align-items: center;justify-content: center;margin-bottom: 1rem">
                    <img @click="jian" src="@/common/image/game/jian.png" style="width: 2rem;height: 2rem;"/>
                    <span style="margin: 0 1rem;font-size: 1.3rem">{{ propsNum }}</span>
                    <img @click="add" src="@/common/image/game/add.png" style="width: 2rem;height: 2rem;"/>
               </div>
               <h5 style="text-align: center;font-size: 1.1rem;font-weight: 500;margin-top: 1rem;width: 80%;margin: 0 auto">ç¡®å®šä½¿ç”¨{{ price }}éº¦ç©—å…‘æ¢{{ aletItem.name }}<span v-if="buySeed || buyWishing">å¹¶ç§ä¸‹å—ï¼Ÿ</span></h5>
               <div style="padding: 0 1rem;width: 100%;position: absolute;bottom: 0.55rem;justify-content: space-between;display: flex;">
                    <span @click="blackBool = false,buyBool = false" style="width: 9rem;line-height: 3.5rem;text-align: center;border-radius: 0.5rem;border: solid 1px rgb(246,90,102);font-size: 1.5rem ;color: rgb(246,90,102)">å–æ¶ˆ</span>
                    <span @click="buyShore" class="buy-shore" style="width: 9rem;line-height: 3.5rem;text-align: center;background-image: url('@/common/image/game/buttomBg.png');">ç¡®å®š</span>
               </div>
               <!--å…³ä¸æŒ‰é’®-->
               <img @click="blackBool = false,buyBool = false" src="@/common/image/game/clear2.png" alt="" style="width: 2.7rem;height: 2.7rem;position: absolute;top: -1rem;right: -1rem;">
          </div>
          <!--é»‘è‰²è’™ç‰ˆ-->
          <div v-if="blackBool" class="black"></div>
     </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
     export default {
          name: "GameShop",
          data(){
               return {
                    seedList:null,
                    buyBool:false,
                    blackBool:false,
                    aletItem:null,
                    buyCount:1,
                    price:null,
                    buySeed:false,
                    buyProps:false,
                    activeWidth:null,
                    tableLeft:0,
                    buyWishing:true,
                    type:1,
                    page:1,
                    signState:null,
                    land_no:1,
                    userInfo:null,
                    propsNum:1
               }
          },
          methods: {
               //è®¡ç®—ä»·é’±
               priceSuan(){
                    this.price = this.aletItem.price * this.propsNum
               },
               //è´­ä¹°é“å…·åŠ 
               add(){
                    if (this.propsNum >= 100){
                         this.$toast.center("ä¸èƒ½è´­ä¹°æ›´å¤šäº†å“¦ï¼")
                         return
                    }
                    this.propsNum++
                    this.priceSuan()
               },
               //è´­ä¹°é“å…·å‡åŽ»
               jian(){
                    if (this.propsNum <= 1){
                         this.$toast.center("ä¸èƒ½å†å°‘äº†å“¦ï¼")
                         return
                    }
                    this.propsNum--
                    this.priceSuan()
               },
               //ç¡®è®¤è´­ä¹°
               buyShore(){

                         //     è´­ä¹°è®¸æ„¿ç§å­
                         if (this.buyWishing) {
                              var obj = {
                                   seed_id:this.aletItem.aid,
                                   number:this.buyCount,
                                   goods_id:this.aletItem.goods_id,
                                   shop_id:this.aletItem.shop_id
                              }
                              this.postObj(obj,window.PATHS.GAME.GAMESTREE_PAY_SEEDS,(result) => {
                                   if (result.code == 1){
                                        var obj = {
                                             seed_id:this.aletItem.aid,
                                             land_no:this.land_no
                                        }
                                        this.sendSeed(obj)
                                   } else{
                                        this.$toast.center(result.info)
                                   }
                              })
                         }
                         //è´­ä¹°ç§å­
                         if (this.buySeed){
                              var obj = {
                                   seed_id:this.aletItem.aid,
                                   number:this.buyCount
                              }
                              this.postObj(obj,window.PATHS.GAME.GAMESTREE_PAY_SEEDS,(result) => {
                                   if (result.code == 1){
                                        var obj = {
                                             seed_id:this.aletItem.aid,
                                             land_no:this.land_no
                                        }
                                        this.sendSeed(obj)
                                   } else{
                                        this.$toast.center(result.info)
                                   }
                              })
                         }
                    //è´­ä¹°é“å…·
                    if (this.buyProps){
                         var obj = {
                              prop_id:this.aletItem.aid,
                              number:this.propsNum
                         }
                         this.postObj(obj,window.PATHS.GAME.GAMESTREE_PAY_PROPS,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("è´­ä¹°æˆåŠŸ,å·²æ”¾å…¥èƒŒåŒ…")
                                   this.buyBool = false
                                   this.blackBool = false
                              } else{
                                   this.$toast.center(result.info)
                              }
                         })
                    }
               },
               //ç§ä¸‹ä¸€é¢—ç§å­
               sendSeed(obj){
                    var that = this
                    this.postObj(obj,window.PATHS.GAME.GAMESTREE_SEND_SEEDS,(result) => {
                         if (result.code == 1){
                              this.buyBool = false
                              this.blackBool = false
                              this.$toast.center("ç§æ¤æˆåŠŸ")
                              location.href = "#/myfruit"
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //è´­ä¹°ä¸€ä¸ªç§å­çš„å¼¹çª—
               buy(item){
                    if (this.signState.length == 0 || this.buyProps){
                         this.buyBool = true
                         this.blackBool = true
                         this.aletItem = item
                         this.price = item.price
                    }else {
                         this.$toast.center("å½“å‰å·²ç»æœ‰ç§å­åœ¨ç§æ¤äº†")
                    }
               },
               //ç‚¹å‡»table
               tableCilk(ev,index){
                    this.tableLeft = index * this.activeWidth
                    if (index == 0){
                         this.type = 1
                         this.getWishing()
                         this.buySeed = false
                         this.buyProps = false
                    }
                    if (index == 1){
                         this.type = 0
                         this.buyWishing = false
                         this.buyProps = false
                         this.getSeed()
                    }
                    if (index == 2){
                         this.buyWishing = false
                         this.buySeed = false
                         this.getprops()
                    }
               },
               //èŽ·å–æ‰€æœ‰é“å…·
               getprops(){
                    this.postObj({},window.PATHS.GAME.GAMESTREE_PROPS,(result) => {
                         if (result.code == 1){
                              this.seedList = result.data
                              this.buyProps = true
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //èŽ·å–æ‰€æœ‰ç§å­
               getSeed(){
                    var obj = {
                         type:this.type
                    }
                    this.postObj(obj,window.PATHS.GAME.GAMESTREE_SEEDS,(result) => {
                         if (result.code == 1){
                              this.seedList = result.data
                              this.buySeed = true
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          //     èŽ·å–æ‰€æœ‰è®¸æ„¿æ ‘
               getWishing(){
                    var obj = {
                         type :this.type,
                         page:this.page
                    }
                    this.postObj(obj,window.PATHS.GAME.GAMESTREE_SEED_GOODS,(result) => {
                         if (result.code == 1){
                              this.seedList = result.data
                              this.buyWishing = true
                              console.log(this.seedList)
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //èŽ·å–ç”¨æˆ·ç§æ¤çŠ¶æ€
               getSignState(){
                    this.postObj({},window.PATHS.GAME.GAMESTREE_GET_LANDS,(result) => {
                         if (result.code == 1){
                              console.log(result.data)
                              this.signState = result.data
                         }else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //     èŽ·å–ç”¨æˆ·ä¿¡æ¯
               getUserInfo(){
                    this.postObj({},window.PATHS.MEBER_GET_INFO,(result) => {
                         if (result.code == 1){
                              this.userInfo = result.data
                         }else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          },
          mounted(){
               this.getUserInfo()
               this.getSignState()
               var that = this
               this.getWishing()
               setTimeout(() => {
                    that.activeWidth = that.$refs.tableLi.clientWidth
               },100)
               // $()
          },
          watch:{
               buyBool(){
                    if (!this.buyBool){
                         this.propsNum = 1
                    }
               }
          },
          components:{
               HeaderPage
          }
     }
</script>

<style scoped>
     .buy-button{
          height: 3rem;
          text-align: center;
          padding: 0 2rem;
          background-color: #53cc8f;
          position: absolute;
          right: 0;
          bottom: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 0.5rem;
     }
     .wishing ul li > div p{
          font-size: 1.3rem;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
          margin-bottom: 1.5rem;

     }
     .wishing ul li > div{
          margin-left: 1rem;
          float: left;
          width: 22rem;
          position: relative;
          height: 100%;
     }
     .wishing ul li > img{
          width: 10rem;
          height: 10rem;
          float: left;
     }
     .wishing ul li{
          height: 12rem;
          width: 100%;
          margin-top: 1rem;
          padding: 1rem;
          overflow: hidden;
          background-color: #ffffff;
          border-radius: 0.5rem;
     }
     .wishing{
          width: 35rem;
          margin: 0 auto;
     }
     .buy-shore{
          background-size: 100% 100%;
          font-size: 1.5rem;
          color: #ffffff;
     }
     .shore-buy{
          position: fixed;
          z-index: 11;
          width: 21rem;
          height: 21rem;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%);
          background-color: #ffffff;
          border-radius: 0.5rem;
     }
     .black{
          width: 100vw;
          height: 100vh;
          position: fixed;
          top: 0;
          background-color: rgba(0, 0, 0, 0.51);
          z-index: 10;
     }
     .fruits ul li {
          width:  10.9rem;
          height: 16rem;
          background-color: #ffffff;
          margin-left: 1.2rem;
          margin-bottom: 1.2rem;
          border-radius: 0.5rem;
          float: left;
          position: relative;
          box-shadow: 0 0 0.2rem rgb(244,211,156);
     }
     .fruits ul{
          overflow: hidden;
     }
     .table ul li span{
          margin-left: 0.55rem;
          font-size: 1.3rem;
     }
     .table ul li{
          width: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          height: 100%;
     }
     .table ul{
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 4.1rem;
          position: relative;
          z-index: 10;
     }
.table{
     width: 35rem;
     margin: 0 auto;
     margin-top: 1rem;
     background-color: #ebdbc3;
     border-radius: 2.05rem;
     position: relative;
}
.active-li{
     height: 100%;
     background-color: #f4d39c;
     position: absolute;
     top: 0;
     z-index: 9;
     border-radius: 2.05rem;
     left: 0;
     transition: 0.3s;
}
     .gameShop-content{
          min-height: 100vh;
          background-color: rgb(255,248,229);
     }
</style>



