                         <span>å•†å“é‡‘é¢ï¼š</span>
                         <div>
                              <span style="margin-right: 11px;">å…±<span class="num_family">{{allGoodsLength}}</span>ä»¶</span>
                              <span style="font-size: 1rem;">ï¿¥<span class="num_family" style="font-size: 1.4rem">{{ goodsAllPrice }}</span></span>
                         </div>
                    </div>
                    <div class="become_getprce">
                         <span>å¿«é€’æ— å¿§ï¼š</span>
                         <div>
                              <span style="font-size: 1rem;color: #9d9d9d">+ï¿¥</span>
                              <span class="num_family" style="font-size: 1.4rem;font-weight: 500">0.00</span>
                         </div>
                    </div>
                    <div v-if="memberPremium" class="become_getprce">
                         <span>ä¼šå‘˜ç‰¹äº«ï¼š</span>
                         <div>
                              <span style="font-size: 1rem;color: #9d9d9d">+ï¿¥</span>
                              <span class="num_family" style="font-size: 1.4rem;font-weight: 500">1.88</span>
                         </div>
                    </div>
               </div>
               <!-- å¿«æ·æ”¯ä»˜ -->
               <div class="fast_payment">
                    <h5>å¿«æ·æ”¯ä»˜:</h5>
                    <div class="pay_type">
                         <!-- ä½™é¢è¶³å¤Ÿæ—¶ -->
                         <template v-if="userInfo">
                              <section v-if="userInfo.votes / 1 >= reallyPayPrice  / 1" @click="comShore('votes')">
                                   <div>
                                        <img class="balance_icon" src="@/common/image/entcom/yuehave.png" alt="">
                                        <span>
                                             <span style="margin-right:10px;">ä½™é¢</span>
                                             <span style="color:#e00000">
                                                  (<span style="font-size:1.2rem;">ï¿¥</span><span>{{ userInfo.votes.substr(0,userInfo.votes.length - 3) }}</span><span
                                                     style="font-size:1.2rem">{{ userInfo.votes.substr(userInfo.votes.length - 3,userInfo.votes.length) }}</span>)
                                             </span>
                                        </span>
                                   </div>
                                   <img class="pat_right" src="@/common/image/entcom/payright.png" alt="">
                              </section>
                              <!-- ä½™é¢ä¸å¤Ÿæ—¶ -->
                              <section v-if="userInfo.votes / 1 < reallyPayPrice  / 1">
                                   <div>
                                        <img class="balance_icon" src="@/common/image/entcom/noneprice.png" alt="">
                                        <span class="noe_votes">
                                             <span style="margin-right:10px;">ä½™é¢ä¸è¶³</span>
                                             <span>
                                                  (<span style="font-size:1.2rem;">ï¿¥</span><span>{{ userInfo.votes.substr(0,userInfo.votes.length - 3) }}</span><span
                                                     style="font-size:1.2rem">{{ userInfo.votes.substr(userInfo.votes.length - 3,userInfo.votes.length) }}</span>)
                                             </span>
                                        </span>
                                   </div>
                              </section>
                         </template>
                         <section @click="comShore('alipay')">
                              <div>
                                   <img class="alipay_icon" src="@/common/image/entcom/zhifubao.png" alt="">
                                   <span>æ”¯ä»˜å®</span>
                              </div>
                              <img class="pat_right" src="@/common/image/entcom/payright.png" alt="">
                         </section>
                         <section @click="comShore('wechat')">
                              <div>
                                   <img class="alipay_icon" src="@/common/image/entcom/wechaticon.png" alt="">
                                   <span>å¾®ä¿¡æ”¯ä»˜</span>
                              </div>
                              <img class="pat_right" src="@/common/image/entcom/payright.png" alt="">
                         </section>
                         <!-- èŠ±å‘— -->
                         <section class="last_section" @click="checkedQi = !checkedQi">
                              <div>
                                   <img class="alipay_icon" src="@/common/image/entcom/checkicon.png" alt="">
                                   <span>èŠ±å‘—æ”¯ä»˜</span>
                              </div>
                              <img :class="checkedQi? 'alipay_num' : ''" class="pat_right"
                                   src="@/common/image/entcom/payright.png" alt="">
                         </section>
                         <ul v-if="checkedQi">
                              <li @click="comShore('alipay',3)">
                                   <div>
                                        <span class="how_qi">ï¿¥{{ thrExpect }}*3æœŸ</span>
                                        <span class="qi_price">(æ‰‹ç»­è´¹ï¿¥2.3/æœŸ)</span>
                                   </div>
                                   <img src="@/common/image/entcom/payright.png" alt="">
                              </li>
                              <li @click="comShore('alipay',6)">
                                   <div>
                                        <span class="how_qi">ï¿¥{{ sixExpect }}*6æœŸ</span>
                                        <span class="qi_price">(æ‰‹ç»­è´¹ï¿¥4.5/æœŸ)</span>
                                   </div>
                                   <img src="@/common/image/entcom/payright.png" alt="">
                              </li>
                              <li @click="comShore('alipay',12)">
                                   <div>
                                        <span class="how_qi">ï¿¥{{ twelveExpect }}*12æœŸ</span>
                                        <span class="qi_price">(æ‰‹ç»­è´¹ï¿¥7.5/æœŸ)</span>
                                   </div>
                                   <img src="@/common/image/entcom/payright.png" alt="">
                              </li>
                         </ul>
                    </div>
               </div>
               <div class="entcom-footer">
                    <div v-if="memberPremium" class="footer_etprice">
                         <span>æœ¬å•èµšï¿¥<span class="num_family">{{ rebatePrice }}</span>å…ƒ</span>
                    </div>
                    <div class="sent_order">
                         <div>
                              <div class="goods_price_footer" style="display: flex;align-items: center">
                                   <span class="practical_price" style="font-size: 1.5rem">å®žä»˜:</span>
                                   <span class="price">ï¿¥<span style="font-size: 1.5rem">{{ reallyPayPrice.substring(0,reallyPayPrice.length - 3) }}</span>{{ reallyPayPrice.substring(reallyPayPrice.length - 3,reallyPayPrice.length) }}</span>
                              </div>
                         </div>
                         <button class="shore" @click="comShore('all')">
                              æäº¤è®¢å•
                         </button>
                         <!--<span class="shore" @click="comShore('all')">æäº¤è®¢å•</span>-->
                         <!--<span class="shore" @click="">ç»§ç»­æ”¯ä»˜</span>-->
                    </div>
               </div>
<!--               <div class="hiddenBlack" v-if="showAlert2"></div>-->
<!--               &lt;!&ndash;åº•éƒ¨å¼¹å‡ºçœå¸‚åŒº&ndash;&gt;-->
<!--               <van-popup v-model="cityShow" position="bottom" :style="{ height: '40%' }">-->
<!--                    <van-area :value="cityArr.length == 0? '' : cityArr[2].code" @cancel="clearCtity"-->
<!--                              @confirm="shorePlace" :area-list="areaList"/>-->
<!--               </van-popup>-->
          </div>
     </div>
</template>

<script>
     import storage from '@/common/js/storage.js'
     import area from '@/common/js/area.js'
     import InstallmentPage from '../../components/InstallmentPage'
     import HeaderPage from "../../components/HeaderPage"
     import {Dialog} from 'vant';



     export default {
          name: "EntcomPage",
          data() {
               return {
                    // èŠ±å‘—åˆ†æœŸçš„å¼€å…³
                    checkedQi: false,
                    areaList: area,
                    cityShow: false,
                    //ä¼šå‘˜ç‰¹æ•ˆå¼€å…³
                    memberPremium: false,
                    //çœ‹ä¸åˆ°ä»·æ ¼çš„å¼€å…³
                    nonePrice: false,
                    showAlert2: false,
                    checked: false,
                    comInfo: null,
                    //å¤‡æ³¨
                    remark: "",
                    //skuä¿¡æ¯
                    addressList: [],
                    address: "",
                    disableBtn: true,
                    timeNum: 0,
                    orderNo: '',
                    skuCom: null,
                    //é—¨åº—ä¿¡æ¯
                    storeInfo: {},
                    billInfo: {},
                    //å®šåˆ¶çš„ä¿¡æ¯
                    customization: null,
                    remarkBoole: false,
                    //æ”¶è´§äººæ‰‹æœº
                    userPhone: null,
                    //æ”¶è´§äººè¯¦ç»†åœ°å€
                    addressDetail: null,
                    //çœå¸‚åŒº
                    addressCity: "",
                    //æ”¶è´§äººå§“å
                    addrensName: null,
                    errormessage: '',
                    cityArr: [],
                    allPrice: null,
                    warningRedBool: true,
                    userInfo: null,
                    scrollTop:90,
                    rule:null,
                    goodsListInfo:null,
               //     ä¸€å…±ä»¶æ•°
                    allGoodsLength:0,
                    goodsAllPrice:0,
                    //å®žé™…ä»˜æ¬¾
                    reallyPayPrice:0
               }
          },
          methods: {
               // ä¸¹ä¸¹çœæ´»åŠ¨
               _everyS(){
                    if(this.userInfo.is_agent == 1 || this.userInfo.vip_status == 1){
                         return
                    }
                    var obj = {
                         name:"1.88ä¸´æ—¶ä¼šå‘˜ç¦åˆ©"
                    }
                    this.postObj(obj,window.PATHS.MEMBERVIP_RULE,(result) => {
                         this.rule = result.data
                    })
               },
               //æŸ¥çœ‹æµ·å¤–è§„åˆ™
               tocustor(){
                    location.href = "app://view/"+ window.HOST +"/pages/customs"
               },
               //ç‚¹å‡»åœ°å€
               showCity() {
                    this.cityShow = true
               },
               //åœ°å€å–æ¶ˆ
               clearCtity() {
                    this.cityShow = false
               },
               //åœ°å€ç¡®å®š
               shorePlace(arr) {
                    this.addressCity = ""
                    arr.forEach((item, index) => {
                         this.addressCity += item.name
                         if (index != arr.length - 1) {
                              this.addressCity += '/'
                         }
                    })
                    this.cityArr = arr
                    console.log(arr)
                    this.cityShow = false
               },
               //èšç„¦æ‰‹æœºå·
               focuPhone() {
                    this.errormessage = ''
               },
               //æ‰‹æœºå·è¯†åˆ«
               malePhone() {
                    if (this.userPhone.length == 0) {
                         this.errormessage = ''
                    } else {
                         if (!(/^1[3456789]\d{9}$/.test(this.userPhone))) {
                              this.errormessage = "æ‰‹æœºå·æœ‰è¯¯"
                         }
                    }
               },
               //è¿”å›žä¸Šä¸€é¡µ
               toBack() {
                    if (this.$route.query.commodity_json) {
                         location.href = window.EQUITMENTBACK
                    } else {
                         window.history.go(-1)
                    }
               },
               //ç¡®è®¤ç”¨æˆ·æ”¯ä»˜æ˜¯å¦æˆåŠŸ
               shorePay() {
                    this.showAlert2 = false
                    this.getAddress()
                    this.postObj({order_no: this.orderNo}, window.PATHS.ORDER_GETORDER, (data) => {
                         if (data.code == 1) {
                              if (data.data.pay_status == 1) {
                                   if (data.data.order_status == 2) {
                                        if (this.comInfo.extra_table == 'goods_group') {
                                             this.$toast.center("æ‹¼å›¢å¤±è´¥")
                                        }
                                   } else {
                                        this.$store.commit('setOrderNo', this.orderNo)
                                        location.replace("#/paysuccess")
                                        return
                                   }
                              } else {
                                   this.$store.commit('setOrderNo', this.orderNo)
                                   location.replace("#/paydefeated")
                              }
                         } else {
                              this.$toast.center(data.info)
                         }
                    })
               },
               toPlace() {
                    location.href = "#/place"
               },
               //ä¿å­˜åœ°å€
               saveplace() {
                    if (this.addressList.length == 0) {
                         var setData = {};
                         setData.user_name = this.addrensName
                         setData.user_phone = this.userPhone
                         setData.user_address = this.addressDetail
                         setData.isdefault = 1;
                         setData.provinceName = this.cityArr[0].name;
                         setData.cityName = this.cityArr[1].name;
                         setData.countryName = this.cityArr[2].name;
                         setData.detailInfo = " ";
                         setData.nationalCode = '86';
                         this.postObj(setData, window.PATHS.ADDRESS_SAVE_INFO, (result) => {
                         })
                    }
               },
               // æ´»åŠ¨ä¸‹è®¢å•
               activeAddOrder(ruleId,callBack){
                    var obj = {
                         rule_id:ruleId
                    }
                    this.postObj(obj,window.PATHS.MEMBERVIP_ADDORDER,(result) => {
                         if(result.code == 1){
                              callBack(result.data)
                         }
                    })
               },
               // åˆå¹¶è®¢å•
               orderGentHe(arr,callBack){
                    var obj = {
                         order_nos:arr
                    }
                    this.postObj(obj,window.PATHS.ORDER_MERGEORDER,(result) => {
                         if(result.code == 1){
                              callBack(result.data)
                         }
                    })
               },
               //ç¡®è®¤æ”¯ä»˜
               comShore(type, hb_fq_num) {
                    //åœ°å€
                    var shopOrder = {
                         uuid:this.goodsListInfo.uuid
                    }
                    if (this.address) {
                         shopOrder.receiver_name = this.address.user_name
                         shopOrder.receiver_phone = this.address.user_phone
                         shopOrder.receiver_address = this.address.provinceName + " " + this.address.cityName + " " + this.address.countryName + " " + this.address.user_address
                    } else {
                         if (this.addrensName && this.userPhone && this.cityArr.length != 0 && this.addressDetail) {
                              shopOrder.receiver_name = this.addrensName
                              shopOrder.receiver_phone = this.userPhone
                              shopOrder.receiver_address = this.cityArr[0].name + " " + this.cityArr[1].name + " " + this.cityArr[2].name + " " + this.addressDetail
                         } else {
                              this.$toast.center("è¯·å®Œå–„æ”¶è´§ä¿¡æ¯")
                              return
                         }
                    }
                    if (this.disableBtn) {
                         this.disableBtn = false
                         //ä¿å­˜åœ°å€
                         // this.saveplace()
                         //æäº¤è®¢å•ä¿¡æ¯
                         this.postObj(shopOrder, window.PATHS.shoppingCar.SHOPSGOODSCART_SUBMITPREORDER, (data) => {
                              console.log(data)
                              if (data.code == 1) {
                                   var url = ""
                                   var orderNoArr = []
                                   data.data.forEach(item => {
                                        if (item.status == 0){
                                             orderNoArr.push(item)
                                        }
                                   })
                                   if (orderNoArr.length == 0){
                                        this.$toast.center("æ”¯ä»˜æˆåŠŸ")
                                        return
                                   //     è¿›è¡Œæ”¯ä»˜æˆåŠŸé€»è¾‘
                                   }

                                   if (orderNoArr.length == 1 && !this.memberPremium){
                                   //æ™®é€šå•ä¸ªè®¢å•æ”¯ä»˜
                                        this.orderNo = orderNoArr[0].order_no
                                        url = "app://shoporder/pay/" + orderNoArr[0].order_no + '/' + orderNoArr[0].price + '?type=' + type
                                        if (hb_fq_num) {
                                             url += '&hb_fq_num=' + hb_fq_num
                                        }
                                        location.href = url
                                   }
                                   else if(this.memberPremium){
                                        this.activeAddOrder(this.rule.aid,(val) => {
                                             var neworderArr = [val.order_no]
                                             orderNoArr.forEach(item => {
                                                  neworderArr.push(item.order_no)
                                             })
                                                       this.orderGentHe(neworderArr,(val2) => {
                                                            this.orderNo = val2.order_no
                                                            url = "app://shoporder/pay/" + val2.order_no + '/' + val2.pay_price + '?type=' + type
                                                            if (hb_fq_num) {
                                                                 url += '&hb_fq_num=' + hb_fq_num
                                                            }
                                                            location.href = url
                                                       })
                                                  })
                                   }
                                   else{
                                        var neworderArr = []
                                        orderNoArr.forEach(item => {
                                             neworderArr.push(item.order_no)
                                        })
                                        this.orderGentHe(neworderArr,(val2) => {
                                             this.orderNo = val2.order_no
                                             url = "app://shoporder/pay/" + val2.order_no + '/' + val2.pay_price + '?type=' + type
                                             if (hb_fq_num) {
                                                  url += '&hb_fq_num=' + hb_fq_num
                                             }
                                             location.href = url
                                        })
                                   }
                              }
                         })
                    }

               },
               toAddPlace() {
                    location.href = '#/newplace'
               },
               //èŽ·å–åœ°å€
               getAddress() {
                    //     èŽ·å–æ”¶è´§åœ°å€
                    this.postObj({}, window.PATHS.ADDRESS_GET_LIST, (data) => {
                         if (data.code == 1) {
                              this.addressList = data.data
                              if (this.$store.state.address != null && this.addressList.length != 0) {
                                   this.address = this.$store.state.address;
                              } else {
                                   this.address = this.addressList[0] || "";
                                   if (this.address == "") {
                                        Dialog.alert({
                                             message: 'æ‚¨è¿˜æ²¡æœ‰æ”¶è´§åœ°å€ï¼Œè¯·ç‚¹å‡»æ·»åŠ ',
                                             confirmButtonText: 'æ·»åŠ ',
                                             confirmButtonColor: '#f55056'
                                        }).then(() => {
                                             this.$refs.inpName.focus()
                                        });
                                   }
                              }
                         }
                    })
               },
               scroll(){
                    this.scrollTop = this.$refs.entCom.scrollTop
               },
               updateData(){
                    this.remark = ""
                    this.memberPremium = false
                    this.rule = null
               },
               getOrderGoodsList(){
                    this.goodsListInfo = this.$store.state.shoppingCartAddOrder
                    this.goodsListInfo.order.forEach(item1 => {
                         var allPrice = 0
                         item1.goods.forEach(item => {
                              this.allGoodsLength++
                             var price = (item.price * 1000 * item.number)
                              allPrice+=price
                         })
                         allPrice = (allPrice / 1000).toFixed(2)
                         item1.allPrice = allPrice
                    })
                    //è®¡ç®—å•†å“æ€»ä»·
                    var newAllPrice = 0
                    this.goodsListInfo.order.forEach(item => {
                         newAllPrice+=(item.allPrice * 1000)
                    })
                    this.goodsAllPrice = (newAllPrice / 1000).toFixed(2)
                    //è®¾ç½®å®žä»˜æ¬¾æ•°æ®
                    this.reallyPayPrice = (newAllPrice / 1000).toFixed(2)
               },
          },
          mounted() {
               // åŽŸç”Ÿè°ƒç”¨jsæ–¹æ³•æŸ¥è¯¢è®¢å•æ˜¯å¦æ”¯ä»˜æˆåŠŸ
               window.orderDone = () => {
                    this.shorePay()
               }
          },
          created() {
               this.getOrderGoodsList()
               this.getAddress()
               // è°ƒå–ç”¨æˆ·è¯¦æƒ…
               this.getUserInfo((data) => {
                    this.userInfo = data
                    this._everyS()
               })
          },
          computed: {
               // åˆ†ä¸‰æœŸ
               thrExpect() {
                    return this.stagePrice(this.reallyPayPrice, 3, 2.3)
               },
               // åˆ†å…­æœŸ
               sixExpect() {
                    return this.stagePrice(this.reallyPayPrice, 6, 4.5)
               },
               // åˆ†åäºŒæœŸ
               twelveExpect() {
                    return this.stagePrice(this.reallyPayPrice, 12, 7.5)
               },
          //     æœ¬å•èµšçš„ä»·é’±
               rebatePrice(){
                    var that = this
                    var newPrice = 0
                    this.goodsListInfo.order.forEach(item1 => {
                         item1.goods.forEach(item => {
                              newPrice += item.rebate_price * 1000 * item.number
                         })
                    })
                    return(newPrice / 1000).toFixed(2)
               }
          },
          activated() {
               this.getUserInfo((data) => {
                    this.userInfo = data
                    this._everyS()
               })
               if (this.$route.meta.keepLoadBool) {
                    this.disableBtn = true
                    this.checkedQi = false
               }else{
                    if ( this.$refs.entCom) {
                         this.$refs.entCom.scrollTop = this.scrollTop
                    }
               }
          },
          beforeRouteLeave(to, from, next) {
               if (to.name == 'place') {
                    from.meta.keepLoadBool = false
               } else {
                    this.updateData()
                    from.meta.keepLoadBool = true
               }
               next()
          },
          beforeRouteEnter(to,from,next){
               if (from.name == 'place'){
                    next((vm) => {
                         vm.getAddress()
                    })
               }else{
                    next()
               }
          },
          watch:{
               memberPremium(newVal,oleVal){
                    if(newVal){
                         storage.set("entAllPrice",this.reallyPayPrice )
                         this.reallyPayPrice  = (this.reallyPayPrice *1000 + this.rule.price*1000)/1000
                         this.reallyPayPrice+=''
                    }else{
                         this.reallyPayPrice = storage.get("entAllPrice")
                    }
               }
          },
          components: {
               InstallmentPage,
               HeaderPage
          },
     }
</script>





