<template>
     <!--æˆ‘çš„è¯„ä»·-->
     <div id="my_comment">
          <HeaderPage>
               <span slot="headerTitle">æˆ‘çš„è¯„ä»·</span>
          </HeaderPage>
          <ul class="comment_list">
               <li v-for="(item,index) in commentList" :key="index + 'commentins'">
                    <div class="comment_content">
                         <div class="user_info left_right">
                              <div class="user_thumb">
                                   <img class="same_img" :src="item.headimgurl" alt="">
                              </div>
                              <div class="user-text top_botom_center">
                                   <span style="font-weight: 600">{{ item.nickname }}</span>
                                   <span style="color: #888888">{{ item.create_time }}</span>
                              </div>
                         </div>
                         <div class="grade left_right">
                              <span>ç»¼åˆè¯„åˆ†</span>
                              <van-rate
                                      v-model="item.score / 2"
                                      allow-half
                                      void-icon="star"
                                      void-color="#9d9d9d"
                                      color="#f55056"
                                      size="14"
                              />
                         </div>
                         <p>{{ item.content }}</p>
                         <div class="comment_img_list left_right">
                              <div v-for="(imgitem,imgIndex) in item.pictures" :key="imgIndex + 'imgins'" class="img_parent">
                                   <img class="same_img" :src="imgitem" alt="">
                              </div>
                         </div>
                         <div class="chase_comment" v-if="item.is_add == 1">
                              <h4>ç”¨æˆ·{{ item.format_add_time }}è¿½å“</h4>
                              <p>{{ item.content2 }}</p>
                              <div class="comment_img_list left_right">
                              <div v-for="(imgitem,imgIndex) in item.pictures2" :key="imgIndex + 'imgins2'" class="img_parent">
                                   <img class="same_img" :src="imgitem" alt="">
                              </div>
                              </div>
                         </div>
                    </div>
                    <div class="goods_info left_right">
                         <div class="goods_img">
                              <img class="same_img" :src="item.thumb" alt="">
                         </div>
                         <div class="goods_text_info top_bottom_between">
                             <div>
                                  <p>{{ item.title }}</p>
                                  <span style="color: #9d9d9d">{{ item.specs }}</span>
                             </div>
                              <span class="price">ï¿¥299</span>
                         </div>
                    </div>
                    <!--è¿½è¯„-->
                    <div class="chase">
                    </div>
                    <div class="comment_footer">
                         <span @click="toDetail(item.goods_id)" class="buy_again">å†æ¬¡è´­ä¹°</span>
                         <span v-if="item.is_add != 1" @click="comment(item.order_no,true)" class="chase_botton">å†™è¿½è¯„</span>
                    </div>
               </li>
          </ul>
     </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
     export default {
          name: "MyComment",
          data(){
               return {
                    page:1,
                    user_id:null,
                    commentList:[]
               }
          },
          methods:{
               comment(order_no,bool){
                    this.postObj({order_no},window.PATHS.SHOPS_ORDER_GET_INFO,(result) => {
                         if (result.code == 1){
                              if (result.data.goods.length > 1){
                                   this.$store.commit('setCommentOrder',order_no)
                                   location.href = '#/comment'
                              } else {
                                   if (bool){
                                        this.$router.push({name:'commenteeild',params:{comInfo:result.data.goods[0],order_no,bool}})
                                   } else {
                                        this.$router.push({name:'commenteeild',params:{comInfo:result.data.goods[0],order_no}})
                                   }
                              }
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //åˆ°ä¸Šå¹³è¯¦æƒ…é¡µ
               toDetail(good_id){
                    this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
               },
               getCommentList(){
                    var obj = {
                         page:this.page,
                         user_id:this.user_id
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_COMMENT_GET_LIST,(result) => {
                         if (result.code == 1){
                              result.data.forEach((item,index) => {
                                   this.commentList.push(item)
                              })
                              console.log(this.commentList)
                         }else{
                              this.$toast.center(result.info)
                         }
                    })

               },
               getUserInfo(){
                    var obj = {}
                    this.postObj(obj,window.PATHS.MEBER_GET_INFO,(result) => {
                         if (result.code == 1){
                              this.user_id = result.data.user_id
                              this.getCommentList()
                         }else{
                              this.$toast.center(result.info)
                         }
                         console.log(result)
                    })
               }
          },
          created(){
               this.getUserInfo()
          },
          components:{
               HeaderPage
          }
     }
</script>

<style scoped>
     .chase_comment h4{
          color: #f55056;
     }
     .chase_comment{
          font-size: 1.3rem;
          border-top: solid 1px #f6f6f6;
          margin-top: 15px;
          padding-top: 15px;
     }
     .chase_botton{
          color: #ffffff;
          background-color: #f55056;
     }
     .buy_again{
          color: #9d9d9d;
          border: solid 1px #9d9d9d;
     }
     .comment_footer span{
          font-size: 1.5rem;
          width: 7.5rem;
          line-height: 2.9rem;
          text-align: center;
          display: inline-block;
          border-radius: 1.8rem;
          margin-left: 20px;
     }
     .comment_footer{
          text-align: right;
          padding: 15px 13px;
          background-color: #ffffff;
     }
     .price{
          font-weight: 500;
     }
     .goods_img{
          width: 8.5rem;
          height: 8.5rem;
          overflow: hidden;
          position: relative;
          display: flex;
          flex-shrink: 0;
     }
     .goods_text_info p{
          font-weight: 500;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;

     }
     .goods_text_info{
          margin-left: 10px;
          height: 100%;
          font-size: 1.3rem;
     }
     .goods_info{
          padding: 16px 13px;
          background-color: #f6f6f6;
          height: 11.5rem;
     }
     .comment_img_list{
          margin-top: 10px;
     }
     .img_parent{
          position: relative;
          overflow: hidden;
          width: 11rem;
          height: 11rem;
          margin-right: 10px;
     }

     .comment_content p{
          margin-top: 15px;
          font-size: 1.3rem;
     }
     .van-rate{
          position: relative;
          top: 3px;
     }
     .grade span{
          font-size: 1.3rem;
          margin-right: 10px;
     }
     .grade{
          margin-top: 16px;
     }
     .user-text{
          margin-left: 6px;
          font-size: 1.3rem;
     }
     .user_thumb{
          width: 4rem;
          height: 4rem;
          border-radius: 50%;
          overflow: hidden;
          position: relative;
     }
     .comment_content{
          padding: 15px 13px;
          background-color: #ffffff;
     }
     .comment_list li{
          margin-bottom: 10px;
     }
     .comment_list{
          margin-top: 5px;
     }
#my_comment{
     min-height: 100vh;
     background-color: #f6f6f6;
}
</style>



