<template>
     <!--æ¸¸æˆå¥–åŠ±è®¢å•-->
     <div class="gameorder-content">
          <div class="header">
               <HeaderPage>å¥–åŠ±è®¢å•</HeaderPage>
               <div class="table">
                    <ul>
                         <li @click="changeTable(0,$event)" class="active-table">å…¨éƒ¨</li>
                         <li @click="changeTable(1,$event)">æœªå‘è´§</li>
                         <li @click="changeTable(2,$event)">å·²å‘è´§</li>
                         <li @click="changeTable(3,$event)">å·²ç¡®è®¤</li>
                    </ul>
               </div>
          </div>
          <div class="piulty-content">
               <ul >
                    <li v-for="(item,index) in orderList">
                         <div class="shop-info">
                              <img :src=item.shop_thumb alt="" style="width: 3rem;height: 3rem;border-radius: 50%">
                              <span>{{ item.shop_name }}</span>
                         </div>
                         <div style="display: flex;justify-content: flex-start">
                              <img :src=item.thumb alt="" style="width: 8.5rem;height: 8.5rem;">
                              <div style="width: 22rem;margin-left: 1.5rem;display: flex;flex-direction: column;justify-content: space-between;position: relative">
                                   <p style="font-size: 1.3rem;width: 100%;">{{ item.title }}</p>
                                   <div style="font-size: 1.3rem;color: #9d9d9d;margin-top: 0.5rem">
                                        <span>{{ item.receiver_address }}</span>
                                   </div>
                                   <div style="font-size: 1.3rem;color: #9d9d9d;margin-top: 0.5rem;width: 68%;">
                                        <span>
                                             {{ item.logistics_name }}
                                        </span>
                                        <span style="font-size: 1rem;">
                                             {{ item.logistics_sn }}123648956321458963
                                        </span>
                                   </div>
                                   <div style="font-size: 1.3rem;display: flex;justify-content: flex-end;position: absolute;bottom: 0;right: 0;">
                                        <span v-if="item.status == 0">æœªå‘è´§</span>
                                        <span @click="order_no = item.order_no,show = true" v-if="item.status == 1" style="width: 6rem;height: 3rem;display: flex;justify-content: center;align-items: center;background-color: #e7204f;border-radius: 0.5rem;color: #ffffff">ç¡®è®¤æ”¶è´§</span>
                                        <span v-if="item.status == 2">å·²ç¡®è®¤</span>
                                   </div>
                              </div>
                         </div>
                    </li>
               </ul>
          </div>
          <!--å¼¹å‡ºæ‰“åˆ†-->
               <van-popup v-model="show">
                    <h5 style="text-align: center;font-size: 1.6rem">è¯·å¯¹è¯¥è®¢å•æ‰“åˆ†</h5>
                    <van-rate
                            v-model="value"
                            allow-half
                            void-icon="star"
                            void-color="#eee"
                    />
                    <div style="position: absolute;bottom: 0;width: 100%;display: flex;justify-content: space-between;font-size: 1.3rem;color: #ffffff">
                         <span @click="show = false" style="width: 50%;;text-align: center;line-height: 4rem;background-color: red">å–æ¶ˆ</span>
                         <span @click="shoreGet" style="width: 50%;;text-align: center;line-height: 4rem;background-color: blue">ç¡®è®¤</span>
                    </div>
               </van-popup>
     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     export default {
          name: "GameOrder",
          data(){
               return {
                    page:1,
                    orderList:null,
                    status:-1,
                    show:false,
                    value:5,
                    order_no:null
               }
          },
          methods:{
               //ç¡®è®¤æ”¶è´§
               shoreGet(){
                    var obj = {
                         order_no:this.order_no,
                         score:this.value * 2
                    }
                    this.postObj(obj,window.PATHS.GAME.GAMESTREE_SHIPPINGORDER,(result) => {
                         if (result.code == 1){
                              this.$toast.center("æ”¶è´§æˆåŠŸï¼")
                              this.show = false
                         } else{
                              this.$toast.center(result.info)
                              this.show = false
                         }
                    })
               },
               changeTable(index,ev){
                 $(ev.currentTarget).addClass("active-table").siblings().removeClass("active-table")
                    if (index == 0){
                         this.status = -1
                    }
                    if (index == 1){
                         this.status = 0
                    }
                    if (index == 2){
                         this.status = 1
                    }
                    if (index == 3){
                         this.status = 2
                    }
                    this.getOrder()
               },
               getOrder(){
                    var obj = {
                         status:this.status,
                         page: this.page
                    }
                    this.postObj(obj,window.PATHS.GAME.GAMESTREE_GETORDERLIST,(result) => {
                         console.log(result)
                         if (result.code == 1){
                              this.orderList = result.data
                         }else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          },
          mounted(){
               this.getOrder()
          },
          components:{
               HeaderPage,
          }
     }
</script>

<style scoped>
     .shop-info span{
          font-size: 1.3rem;
          color: #e7204f;
          margin-left: 1rem;
     }
     .shop-info{
          display: flex;
          align-items: center;
          margin-bottom: 0.5rem;
     }
     .van-icon{
          font-size: 3rem;
     }
     .van-rate{
          display: flex;
          justify-content: center;
          margin-top: 3rem;
     }
     .van-popup{
          width: 70%;
          height: 30%;
          padding-top: 2rem;
     }
     .active-table{
          color: #cf3857;
     }
     .header{
          position: fixed;
          top: 0;
          width: 100%;
          z-index: 10;
     }
     .piulty-content ul li p{
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
     }
     .piulty-content ul li{
          background-color: #ffffff;
          margin-top: 1rem;
          padding: 1.5rem 1.7rem;
     }
     .piulty-content{
          width: 35.1rem;
          margin: 0 auto;
     }
     .table ul li{
          height: 100%;
          display: flex;
          align-items: center;
     }
     .table ul{
          width: 100%;
          height: 4rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 1.1rem;
     }
     .table{
          font-size: 1.5rem;
     }
     .header{
          background-color: #ffffff;
     }
.gameorder-content{
     min-height: 100vh;
     background-color: #f6f6f6;
     color: #212121;
     padding-top: 8.4rem;
     padding-bottom: 1rem;
}
</style>



