<script>
     import HeaderPage from '../components/HeaderPage'
     import BuyShow from "../components/shop/BuyShow"

     export default {
          name: "FlagshipShop",
          data() {
               return {
                    buyShowBool:false,
                    goodSort: ["ç»¼åˆ", "é”€é‡", "æ–°å“", "ä»·æ ¼"],
