                    order_bay: "desc",
                    //å¤§tableé€‰é¡¹
                    bigTableType:"home",
                    tableListType:["home","goods","video","buyShow"],
                    userInfo:null,
                    sortIndex:0,
                    moreBool:true,
                    loadBool:true,
                    videoList:[],
                    buyShowList:[],
                    page:{
                         buyShowPage:1,
                         goodsListPage:1,
                         homeGoodsPage:1,
                         videoPage:1
                    },
                    padTop:0,
                    soltHeihgt:0,
                    homeGoodsList:[],
                   scrollTopNum:0
               }
          },
          methods: {
               //åˆ°å•†å“è¯¦æƒ…é¡µ
               toDetails(good_id) {
                    this.$router.push({name: 'shopgoods', query: {comId: good_id}})
               },
               //åˆ°å®¢æœé¡µé¢
               toMessage(){
                    var obj = {
                         shopname:this.comInfo.shop.name,
                         shopthumb:this.comInfo.shop.thumb
                    }
                    obj = JSON.stringify(obj)
                    obj = this.getBaseStr(obj)
                    location.href = "app://message/"+ this.comInfo.shop_id + "/" + obj
               },
               //æ’­æ”¾è§†é¢‘
               playVideo(item) {
                    item.count++
                    location.href = "app://play/video/?" + item.aid
               },
               //å…³é—­ä¹°å®¶ç§€è¯¦æƒ…
               closeBuyShow(){
                    this.buyShowBool = false
               },
               //ä¹°å®¶ç§€æŸ¥çœ‹è¯¦æƒ…
               showBuyShow(item){
                    this.buyShowBool = true
                    this.$store.commit("setShopBuyShow",item)
               },
               //ä¹°å®¶ç§€ç‚¹èµž
               likeBuyShow(aid,index){
                    var obj = {
                         comment_id:aid
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_COMMENT_STR,(result) => {
                         if (result.code == 1){
                              this.buyShowList[index].is_star = this.buyShowList[index].is_star == 1? 0 : 1
                         }
                    })
               },
               //ä¹°å®¶ç§€æ•°æ®
               getBuyShow(){
                    var obj = {
                         page:this.page.buyShowPage,
                         is_pic:1,
                         shop_id:this.$route.query.shop_id
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_COMMENT_GET_LIST,(result) => {
                         if (result.code == 1){
                              if (result.data.length != 0){
                                   this.buyShowList = this.buyShowList.concat(result.data)
                                   setTimeout(() => {
                                        this.moreBool = true
                                   })
                              } else{
                                   this.moreBool = false
                                   this.loadBool = false
                              }
                         } else{
                              tihs.$toast.center(result.info)
                         }
                    })
               },
               //èŽ·å–åº—é“ºè§†é¢‘
               getVideoList() {
                    var that = this
                    var obj = {
                         shop_id: this.shopInfo.aid,
                         page: this.page.videoPage
                    }
                    this.postObj(obj, window.PATHS.VIDEOS_GET_LIST, (result) => {
                         if (result.code == 1) {
                              if (result.data.length != 0){
                                   this.videoList = this.videoList .concat(result.data)
                                   setTimeout(() => {
                                        that.moreBool = true
                                   },500)
                              } else{
                                   that.moreBool = false
                                   that.loadBool = false
                              }
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //åˆ°æœç´¢é¡µé¢
               toSearch(){
                    this.$router.push({name: 'search', query: {shop_id: this.shopInfo.aid}})
               },
               toShopInfo(){
                    this.$store.commit("getShopInfo",this.shopInfo)
                    location.href = "#/shopinfo"
               },
               //åˆ†äº«åº—é“º
               shareShop(){
                    var uid = this.userInfo? this.userInfo.id : 0
                    var obj = {
                         title:this.shopInfo.name,
                         content:"",
                         url:window.HOST + "/shops/info?shop_id=" + this.shopInfo.aid + "&uid=" + uid,
                         imgsrc:this.shopInfo.thumb
                    }
                    this.shareSame(obj)
               },
               //å…³æ³¨å–æ¶ˆå…³æ³¨
               likeShop(){
                    var obj = {
                         shop_id: this.shopInfo.aid
                    }
                    this.postObj(obj,window.PATHS.SHOPS_STAR,(result) => {
                         if (result.code == 1){
                              if (result.data.status == 1){
                                   this.$toast.center("å·²å…³æ³¨")
                                   this.shopInfo.is_star = 1
                              }else {
                                   this.shopInfo.is_star = 0
                              }
                         }
                    })
               },
               bigTable(index){
                    this.$refs.shopScro.scrollTop = 0
                    this.loadBool = true
                    this.moreBool = true
                    this.bigTableType = this.tableListType[index]
                    if (index == 1){
                         this.$nextTick(() => {
                              this.soltHeihgt = this.$refs.soltDiv.clientHeight + 10
                         })
                    }
                    if (index == 2){
                         this.getVideoList()
                    }
                    if (index == 3){
                         this.buyShowList = []
                         this.getBuyShow()
                    }
               },
               //æŽ’åº
               sort(index) {
                    this.page.goodsListPage = 1
                    this.loadBool = true
                    this.sortIndex = index
                    this.comList = []
                    $(this.$refs.sort[index]).addClass("sort_active").siblings().removeClass("sort_active")
                         if (index == 0) {
                              this.order_way = "all"
                              this.order_bay = "desc"
                              this.getComList()
                         }
                         if (index == 1) {
                              this.order_way = "sales"
                              this.order_bay = "desc"
                              this.getComList()
                         }
                         if (index == 2){
                              this.order_way = "all"
                              this.order_bay = "desc"
                              this.getComList()
                         }
                         if (index == 3){
                              this.order_way = "price"
                              this.order_bay = this.order_bay == "desc"? "asc" : "desc"
                              this.getComList()
                         }
               },
               //èŽ·å–åº—é“ºå…¨éƒ¨å•†å“
               getComList() {
                    var that = this
                    var obj = {
                         shop_id: this.shopInfo.aid,
                         page: this.page.goodsListPage,
                         order_way: this.order_way,
                         order_bay: this.order_bay
                    }
                    this.postObj(obj, window.PATHS.SHOPS_GOODS_GET_LIST, (result) => {
                         if (result.code == 1) {
                              if (result.data.length == 0){
                                   setTimeout(() => {
                                        that.moreBool = false
                                        that.loadBool = false

                                   },500)
                              }else{
                                   this.comList = this.comList.concat(result.data)
                                   that.moreBool = true
                              }
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //     èŽ·å–åº—é“ºä¿¡æ¯
               getShopInfo(){
                    var obj = {
                         shop_id:this.$route.query.shop_id
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GETINFO,(result) => {
                         if (result.code == 1){
                             this.shopInfo = result.data
                              console.log(this.shopInfo)
                              this.userInfo = result.user
                              this.getComList()
                         }else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          //     èŽ·å–é¦–é¡µå•†å“åˆ—è¡¨
               getHomeGoods(){
                    var that = this
                    var obj = {
                         shop_id:this.$route.query.shop_id,
                         page:this.page.homeGoodsPage
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_GET_LIST,(result) => {
                         if (result.code == 1){
                              this.homeGoodsList = this.homeGoodsList.concat(result.data)
                              console.log(this.homeGoodsList)
                              setTimeout(() => {
                                   if (result.data.length == 0){
                                        that.moreBool = false
                                        that.loadBool = false
                                   } else{
                                        that.moreBool = true
                                   }

                              },500)
                         } else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               scroll(){
                    var maxScro = this.$refs.shopScro.scrollHeight - window.innerHeight
                    var scrollTop = this.$refs.shopScro.scrollTop
                    this.scrollTopNum = scrollTop
                    if (maxScro - window.innerHeight <= scrollTop){
                         if (this.moreBool){
                              this.moreBool = false
                              if (this.bigTableType == 'home'){
                                   this.page.homeGoodsPage++
                                   this.getHomeGoods()
                              }
                              if (this.bigTableType == 'goods') {
                                   this.page.goodsListPage++
                                   this.getComList()
                              }
                              if (this.bigTableType == 'video') {
                                   this.page.videoPage++
                                   this.getVideoList()
                              }
                              if (this.bigTableType == 'buyShow') {
                                   this.page.buyShowPage++
                                   this.getBuyShow()
                              }
                         }
                    }
               },
          //     ç¼“å­˜éœ€è¦é‡æ–°èµ‹å€¼
               anewBgan(){
                    this.buyShowBool = false
                    this.order_way = "all"
                    this.order_bay = "desc"
                    this.bigTableType = 'home'
                    this.userInfo = null
                    this.sortIndex = 0
                    this.comList = []
                    this.homeGoodsList = []
                    this.buyShowList = []
                    this.videoList = []
                    this.moreBool = true
                    this.loadBool = true
                    this.page.buyShowPage = 1
                    this.page.goodsListPage = 1
                    this.page.homeGoodsPage = 1
                    this.page.videoPage = 1
                    this.shopInfo = null

               },
          },
          mounted() {
          },
          created() {
          },
          activated(){
               if (this.$refs.shopScro){
                    this.$refs.shopScro.scrollTop = this.scrollTopNum
               }

               if (this.$route.meta.keepLoadBool) {
                    this.getHomeGoods()
                    this.getShopInfo()

               }
          },
          beforeRouteLeave(to,from,next){
               if (to.name == "search" || to.name == "shopinfo"){
                    from.meta.keepLoadBool = false
               }else{
                    from.meta.keepLoadBool = true
                    this.anewBgan()

               }
               next()
          },
          watch:{
               shopInfo(oldval,newval){
                    if (oldval){
                         this.$nextTick(() => {
                              this.padTop = this.$refs.shopHeader.clientHeight
                         })
                    }
               }
          },
          // watch:{
          //      buyShowBool(newVal,oldVal){
          //           if (newVal){
          //                location.href = "app://rightrow/close"
          //           }else{
          //                location.href = "app://rightrow/open"
          //           }
          //      }
          // },
          components: {
               HeaderPage,
               BuyShow
          }
     }
</script>




