                                                  <span style="color: #f24367">é€€æ¬¾</span>
                                             </template>
                                             <!--é€€è´§é€€æ¬¾-->
                                             <template v-if="item.refund_type == 2">
                                                  <img src="@/common/image/aftersale/huanhuo@2x.png" alt="">
                                                  <span style="color: #f3ab64">é€€è´§é€€æ¬¾</span>
                                             </template>
                                             <!--æ¢è´§-->
                                             <template v-if="item.refund_type == 3">
                                                  <img src="@/common/image/aftersale/huanhuo@2x (2).png" alt="">
                                                  <span style="color: #f24367">æ¢è´§</span>
                                             </template>
                                        </div>
                                   </div>
                                   <div class="goods_info">
                                        <div class="goods_thumb" :style="{backgroundImage: 'url('+ item.thumb +')'}"></div>
                                        <div class="goods_title">
                                             <p>{{ item.title }}</p>
                                             <span style="font-size: 1.3rem">{{ item.specs }}</span>
                                             <span v-if="item.refund_type == '1' || item.refund_type == '2'">é€€æ¬¾:ï¿¥{{ item.refund_price }}å…ƒ</span>
                                        </div>
                                   </div>
                                   <div class="after_waring">
                                        <!--é€€æ¬¾-->
                                        <template v-if="item.refund_type == 1">
                                             <span v-if="item.status == '0'">å·²æäº¤é€€æ¬¾ç”³è¯·</span>
                                             <span v-if="item.status == '5'">è¯¥å”®åŽå·²æ’¤é”€</span>
                                             <span v-if="item.status == '6'">å•†å®¶å·²åŒæ„ç”³è¯·ï¼Œç•™æ„é€€æ¬¾åˆ°è´¦</span>
                                             <span v-if="item.status == '2'">å•†å®¶æ‹’ç»é€€æ¬¾</span>
                                        </template>
                                        <!--é€€è´§é€€æ¬¾-->
                                        <template v-if="item.refund_type == 2">
                                             <span v-if="item.status == '0'">å·²æäº¤é€€è´§é€€æ¬¾ç”³è¯·</span>
                                             <span v-if="item.status == '1'">å•†å®¶å·²åŒæ„ç”³è¯·ï¼Œè¯·å¡«å†™ç‰©æµä¿¡æ¯</span>
                                             <span v-if="item.status == '2'">å•†å®¶æ‹’ç»é€€æ¬¾é€€è´§</span>
                                             <span v-if="item.status == '3'">æ‚¨å·²ç»å‘å‡ºè´§ç‰©ç­‰å¾…å•†å®¶ç¡®è®¤</span>
                                             <span v-if="item.status == '5'">è¯¥å”®åŽå·²æ’¤é”€</span>
                                             <span v-if="item.status == '6'">å•†å®¶å·²æ”¶åˆ°è´§ç‰©ï¼Œç•™æ„é€€æ¬¾åˆ°è´¦</span>
                                        </template>
                                        <!--æ¢è´§-->
                                        <template v-if="item.refund_type == 3">
                                             <span v-if="item.status == '0'">å·²æäº¤æ¢è´§ç”³è¯·</span>
                                             <span v-if="item.status == '1'">å•†å®¶å·²åŒæ„æ¢è´§ç”³è¯·ï¼Œè¯·å¡«å†™æ‚¨çš„å‘è´§ä¿¡æ¯</span>
                                             <span v-if="item.status == '2'">å•†å®¶æ‹’ç»é€€æ¬¾é€€è´§</span>
                                             <span v-if="item.status == '3'">ç­‰å¾…å•†å®¶ç¡®è®¤æ”¶åˆ°è´§ç‰©</span>
                                             <span v-if="item.status == '5'">è¯¥å”®åŽå·²æ’¤é”€</span>
                                             <span v-if="item.status == '6'">å•†å®¶å·²æ”¶åˆ°è´§ç‰©ï¼Œç­‰å¾…æ–°çš„å•†å“åˆ°æ‰‹</span>
                                        </template>
                                   </div>
                                   <div class="fing_detail">
                                        <span v-if="item.status == 0 || item.status == 1 || item.status == 3" @click="closeShen(item.aid,index)">æ’¤é”€ç”³è¯·</span>
                                        <span @click="toAfterDetail(item)" style="margin-left: 10px;">æŸ¥çœ‹è¯¦æƒ…</span>
                                   </div>
                              </div>
                         </li>
                    </ul>
               </div>
               <h3 class="more" v-if="!moreBool">å·²åŠ è½½å…¨éƒ¨</h3>
          </van-pull-refresh>
     </div>
</template>

<script>
     import { Dialog } from 'vant';
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "AfterDetail",
          data(){
               return{
                    afterList:[],
                    moreBool:true,
                    page:1,
                    scrollTop:0,
                    isLoading:true
               }
          },
          methods:{
               //æ’¤é”€ç”³è¯·
               closeShen(aid,index){
                    Dialog.confirm({
                         title: 'æ’¤é”€å”®åŽ',
                         message: 'ç¡®å®šè¦æ’¤é”€è¯¥å”®åŽå—ï¼Ÿ'
                    }).then(() => {
                         var obj = {
                              refund_id:aid
                         }
                         this.postObj(obj,window.PATHS.aftersale.SHOPSREFUND_REVOKEORDER,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("æ’¤é”€æˆåŠŸ")
                                   this.afterList[index].status = 5
                              }
                         })
                    }).catch(() => {
                         // on cancel
                    });
               },
               //åˆ·æ–°
               onRefresh() {
                    var that = this
                    this.isLoading = true
                    this.page = 1
                    setTimeout(() => {
                         that.$toast('åˆ·æ–°æˆåŠŸ');
                         that.isLoading = false;
                         that.getAfterDetail((data) => {
                              that.afterList = data
                              console.log(that.afterList)
                         })
                    }, 1000);
               },
               //åˆ°å”®åŽè¯¦æƒ…
               toAfterDetail(val){
                    this.$router.push({name:"afterdetail",query:{refund_id:val.aid}})
               },
               getAfterDetail(backfun){
                    var that = this
                    if (this.$route.query.isyuan){
                         var obj = {
                              page:this.page
                         }
                    } else{
                         var obj = {
                              order_no:this.$route.query.order_no,
                              order_goods_id:this.$route.query.order_goods_id,
                              page:this.page
                         }
                    }
                    this.postObj(obj,window.PATHS.aftersale.SHOPSREFUND_GETORDERLIST,(result) => {
                         if (result.code == 1){
                              if (result.data.length != 0){
                                   if (backfun){
                                        backfun(result.data)
                                   }
                                   this.afterList = this.afterList.concat(result.data)
                                   setTimeout(() => {
                                        that.moreBool = true
                                   },1000)
                              }else{
                                   setTimeout(() => {
                                        that.moreBool = false
                                   },1000)
                              }
                         }
                    })
               },
               scroll(){

                    this.scrollTop = this.$refs.afterList.scrollTop
                    var height = this.$refs.afterList.scrollHeight
                    var scrollTop = this.$refs.afterList.scrollTop
                    if (scrollTop <= 1){
                         this.$refs.afterList.scrollTop = 0
                    }
                    var windoHeight = window.innerHeight * 2
                    this.scrollMore(scrollTop,height,window.innerHeight,() => {
                         if (this.moreBool){
                              this.moreBool = false
                              this.page++
                              this.getAfterDetail()
                         }
                    })
               },
               //åˆå§‹åŒ–æ•°æ®
               initialize(){
                    this.afterList = []
                    this.moreBool = true
                    this.page = 1
                    this.scrollTop = 0
               },
          },
          created() {
               this.getAfterDetail()
          },
          activated(){
               if (this.$refs.afterList) {
                    this.$refs.afterList.scrollTop = this.scrollTop
               }
               if (this.$route.meta.keepLoadBool){
                    this.getAfterDetail()
               }
          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to,from,next){
               if (to.name == "afterdetail"){
                    from.meta.keepLoadBool = false
               }else{
                    this.initialize()
                    from.meta.keepLoadBool = true
               }
               next()
          }
     }
</script>





