<script>
     import {Dialog} from 'vant';
     import storage from "@/common/js/storage.js"
     import HeaderPage from "../components/HeaderPage"

     export default {
          name: "OrderPage",
          data() {
               return {
                    opacityhid: false,
                    showOk: false,
                    orderList: [],
                    tableLi: ['å…¨éƒ¨', 'å¾…ä»˜æ¬¾', 'å¾…åˆ†äº«', 'å¾…å‘è´§', 'å¾…æ”¶è´§', 'å·²å®Œæˆ'],
                    clearOrderBool: true,
                    radio: '-1',
                    show: false,
                    order_no: null,
                    shoreComBool: true,
                    order_status: '0,1,2,3,4',
                    top: 100,
                    logisticsUrl: '',
                    showUrl: false,
                    activeHeaderBol: 0,
                    page: 1,
                    goods_type: "",
                    moreBool: true,
                    userInfo: null,
                    skeleton: true,
                    scrollTop: 0,
                    shippingStatus: -2,
                    isLoading: false,
                    threeDianInfo:null,
                    threeDianIndex:-1,
                    scrollBool:true
               }
          },
          methods: {
               //åˆ°åº—é“ºé¡µé¢
               toShop(shopId){
                    this.$router.push({name:"shop",query:{shop_id:shopId}})
               },
               // åˆ°å¼€å…·å‘ç¥¨
               toBill(){
                    console.log(this.threeDianInfo)
                    this.$router.push({
                         name:"bill",
                         params:{
                              orderInfo:this.threeDianInfo
                         }
                    })
               },
               showOrder(order_no,index) {
                    Dialog.confirm({
                         title: 'ç¡®è®¤æ”¶è´§',
                         message: 'ç¡®è®¤å·²ç»æ”¶åˆ°è´§ç‰©ï¼Œé¿å…è´¢è´§ä¸¤ç©º!',
                    }).then(() => {
                         this.affirm(order_no,index)
                    }).catch(() => {
                         // on cancel
                    });
               },
               onRefresh() {
                    setTimeout(() => {
                         this.$toast('åˆ·æ–°æˆåŠŸ');
                         this.isLoading = false;
                         this.page = 1
                         this.getOrderList((data) => {
                              this.orderList = data
                         })
                    }, 500);
               },
               //åˆ é™¤æ— ç”¨çš„è®¢å•
               clearOrder(val,index) {
                    Dialog.confirm({
                         title: 'åˆ é™¤è®¢å•',
                         message: 'ç¡®è®¤è¦åˆ é™¤è¯¥è®¢å•å—'
                    }).then(() => {
