                         })
                    }).catch(() => {
                         // on cancel
                         this.opacityhid = false
                    });

               },
               //é‚€è¯·å¥½å‹æ‹¼å•
               shareTeam(item) {
                    console.log(item)
                    var obj = {
                         title: item.goods[0].title,
                         content: "æ‹¼å›¢ä¼˜æƒ ",
                         url: window.HOST + "/goods/group?uid=" + this.userInfo.id + "&goods_id=" + item.goods[0].goods_id + "&period_id=" + item.goods[0].period_id,
                         imgsrc: item.goods[0].thumb,
                         nickname: this.userInfo.nickname,
                         headimgurl: this.userInfo.headimgurl,
                    }
                    this.getComInfo(item.goods[0].goods_id, (data) => {
                         if (data) {
                              obj.shop_price = data.shop_price,
                                  obj.market_price = data.market_price,
                                  obj.sales = data.sales,
                                  obj.needPersor = data.extra_data.number - data.extra_data.current,
