                    Dialog.confirm({
                         title: 'ç¡®å®šåˆ é™¤å—ï¼Ÿ',
                         confirmButtonColor: "#9d9d9d"
                    }).then(() => {
                         this.$loading()
                         var obj = {
                              aids: aid + ''
                         }
                         this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCARY_DELETEID, (result) => {
                              if (result.code == 1) {
                                   this._getCratList()
                                   setTimeout(() => {
                                        this.$loading.close()
                                   }, 1000)
                              }
                         })
