                    });
               },
               //æ¸…ç©ºå¤±æ•ˆå®è´
               _notesListClear(){
                    Dialog.confirm({
                         title: 'ç¡®å®šè¦æ¸…ç©ºå¤±æ•ˆçš„å®è´å—ï¼Ÿ',
                         confirmButtonColor: "#f55056"
                    }).then(() => {
                         var newArr = []
