                              newArr.push(item.aid)
                         })
                         var obj = {
                              aids:newArr.join(",")
                         }
                         this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCARY_DELETEID,(result) => {
                              if (result.code == 1){
                                   this.notesList = []
                              }
                         })
                    }).catch(() => {
                    });
               },
               //ç»“ç®—
               _accounts() {
                    var newArr = []
                    this.carList.forEach(item => {
                         item.goods.forEach(items => {
                              if (items.check_goods) {
                                   newArr.push(items.aid)
                              }
                         })
                    })
                    if (newArr.length == 0) {
                         this.$toast.center("è¯·é€‰æ‹©æ‚¨è¦ç»“æŸçš„å•†å“...")
                    } else {
                         this.$loading("ç»“ç®—ä¸­")
                         var obj = {
                              aids: newArr.join(",")
                         }
                         this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCART_ADDPREORDER, (result) => {
                              if (result.code == 1) {
                                   this.$store.dispatch("SetshoppingCartAddOrder", result.data)
                                   console.log(this.$store.state.shoppingCartAddOrder)
                                   setTimeout(() => {
                                        location.href = "#/shoppingent"
                                        this.$loading.close()
                                   }, 1000)
                              }
                         })
                    }

               },
               //åˆ é™¤å•†å“
               _clearCarGoods() {
                    if (this.checked) {
                         //æ¸…ç©º
                         Dialog.confirm({
                              title: 'ç¡®å®šè¦æ¸…é™¤è´­ç‰©è½¦å—',
                              confirmButtonColor: "#9d9d9d"
                         }).then(() => {
                              var obj = {
                                   aids: "all"
                              }
                              this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCARY_DELETEID, (result) => {
                                   if (result.code == 1) {
                                        this.$loading("æ¸…é™¤ä¸­")
                                        setTimeout(() => {
                                             this.carList = []
                                             this.$loading.close()
                                        }, 1000)
                                   }
                              })
                         }).catch(() => {
                         });
                    } else {
                         // //     åˆ é™¤é€‰ä¸­
                         Dialog.confirm({
                              title: 'ç¡®å®šè¦åˆ é™¤é€‰ä¸­çš„å•†å“å—',
                              confirmButtonColor: "#9d9d9d"
                         }).then(() => {
                              this.$loading("åˆ é™¤ä¸­")
                              var newArr = []
                              this.carList.forEach(item => {
                                   item.goods.forEach((item1) => {
                                        if (item1.check_goods) {
                                             newArr.push(item1.aid)
                                        }
                                   })
                              })
                              newArr = newArr.join(",")
                              var obj = {
                                   aids: newArr
                              }

                              this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCARY_DELETEID, (result) => {
                                   if (result.code == 1) {
                                        this._getCratList()
                                        setTimeout(() => {
                                             this.$loading.close()
                                        }, 1000)
                                   }
                              })
                         }).catch(() => {
                         });
                    }

               },
               //ç‚¹å‡»å®Œæˆ
               _clearCheck() {
                    this.checked = false
                    this.manageBool = !this.manageBool
                    this.carList.forEach(item => {
                         item.shop.check_shop = false
                         item.goods.forEach(item1 => {
                              item1.check_goods = false
                         })
                    })
                    this._computePrice()
               },
               //è®¡ç®—æ€»ä»·
               _computePrice() {
                    this.allPrice = 0
                    this.allGoodsNum = 0
                    this.carList.forEach(item => {
                         item.goods.forEach(item1 => {
                              if (item1.check_goods) {
                                   this.allGoodsNum++
                                   var num_price = (item1.price * 100 * item1.number) / 100
                                   this.allPrice += num_price
                              }
                         })
                    })
               },
               //å…¨é€‰
               check_all() {
                    this.carList.forEach((item1) => {
                         item1.shop.check_shop = this.checked
                         item1.goods.forEach(item2 => {
                              item2.check_goods = this.checked
                         })
                    });
                    this._computePrice()
               },
               //é€‰æ‹©åº—é“º
               check_list(i) {
                    this.carList[i].goods.forEach(item1 => {
                         item1.check_goods = this.carList[i].shop.check_shop
                    })
                    this.abc();
                    this._computePrice()
               },
               abc() {
                    var that = this;
                    var aaa = that.carList.filter(item2 => {
                         return item2.shop.check_shop == true
                    })
                    aaa.length == that.carList.length ? that.checked = true : that.checked = false
               },
               //é€‰æ‹©å•†å“
               click_input(i) {
                    var checklist = this.carList[i].goods.filter((item1) => {
                         return item1.check_goods == true
                    })
                    this.carList[i].shop.check_shop = checklist.length == this.carList[i].goods.length ? true : false
                    this.abc();
                    this._computePrice()
               },
               _numAdd(index1, index2) {
                    this.carList[index1].goods[index2].number++
                    this.$loading()
                    var obj = {
                         aid: this.carList[index1].goods[index2].aid,
                         number: this.carList[index1].goods[index2].number
                    }
                    this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCART_UPDATENUMBER, (result) => {
                         this.$loading.close()
                         this.carList[index1].goods[index2].number = result.data.number
                         if (result.data.status == 1) {
                              this._computePrice()
                         }
                         if (result.data.status == 2) {
                              this.$toast.center("å•†å“å·²å¤±æ•ˆ")
                         }
                         if (result.data.status == 3) {
                              this.$toast.center("åº“å­˜ä¸è¶³")
                         }
                    })
               },
               //å‡
               _numJian(index1, index2) {
                    if (this.carList[index1].goods[index2].number <= 1) {
                         return
                    } else {
                         this.$loading()
                         this.carList[index1].goods[index2].number--
                         var obj = {
                              aid: this.carList[index1].goods[index2].aid,
                              number: this.carList[index1].goods[index2].number
                         }
                         this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCART_UPDATENUMBER, (result) => {
                              this.$loading.close()
                              this.carList[index1].goods[index2].number = result.data.number
                              if (result.data.status == 1) {
                                   this._computePrice()
                              }
                              if (result.data.status == 2) {
                                   this.$toast.center("å•†å“å·²å¤±æ•ˆ")
                              }
                              if (result.data.status == 3) {
                                   this.$toast.center("åº“å­˜ä¸è¶³")
                              }
                         })
                    }
               },
               scroll() {
                    this.scrollTop = this.$refs.showpcarcontent.scrollTop
                    var height = this.$refs.showpcarcontent.scrollHeight
                    var scrollTop = this.$refs.showpcarcontent.scrollTop
                    var windowHeight = window.innerHeight
                    this.scrollMore(scrollTop, height, windowHeight, () => {
                         if (this.scrollBool) {
                              this.scrollBool = false
                              if (this.moreBool) {
                                   this.goodsPage++
                                   this.getGoodsList()
                              }
                         }
                    })
               },
               //èŽ·å–çŒœä½ å–œæ¬¢å•†å“åˆ—è¡¨
               getGoodsList() {
                    var obj = {
                         page: this.goodsPage,
                    }
                    this.postObj(obj, window.PATHS.SHOPS_GOODS_GET_LIST, (result) => {
                         if (result.code == 1) {
                              if (result.data.length != 0) {
                                   this.goodList = this.goodList.concat(result.data)
                              } else {
                                   this.moreBool = false
                              }
                              setTimeout(() => {
                                   this.scrollBool = true
                              }, 500)
                         }
                    })
               },
               toDetails(aid) {
                    this.$router.push({name: 'DetailsPage', query: {comId: aid}})
               },
               //èŽ·å–è´­ç‰©è½¦
               _getCratList() {
                    var obj = {}
                    this.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCART_GETLIST, (result) => {
                         this.notesList = result.data.notes
                         this.carList = result.data.lists.map((item1) => {
                              item1.shop.check_shop = false
                              item1.goods = item1.goods.map((item2) => {
                                   item2.check_goods = false
                                   return item2
                              })
                              return item1
                         })
                         console.log(this.carList)
                    })
               },
               //èŽ·å–æ”¶è´§åœ°å€
               _getAddress() {
                    //     èŽ·å–æ”¶è´§åœ°å€
                    this.postObj({}, window.PATHS.ADDRESS_GET_LIST, (data) => {
                         if (data.code == 1) {
                              this.addressList = data.data
                         }
                    })
               },
          },
          mounted() {
          },
          watch:{
               carList(newVal,oldVla){
                    var num = 0
                    newVal.forEach((item) => {
                         item.goods.forEach((item2) => {
                              num++
                         })
                    })
                    this.carGoodsNum = num
               }
          },
          created() {
               this.goodswidth = (window.innerWidth - 35.6)  / 2
               this.getGoodsList()
               //æŒ‚è½½åœ¨mainä¸­èŽ·å–ç”¨æˆ·ä¿¡æ¯çš„æ–¹æ³•
               this.getUserInfo((data) => {
                    if (data) {
                         this.userInfo = data
                         console.log(this.userInfo, "ç”¨æˆ·ä¿¡æ¯")
                    } else {
                         this.userInfo = null
                    }
               })
          },
          activated() {
               //æŒ‚è½½åœ¨mainä¸­èŽ·å–ç”¨æˆ·ä¿¡æ¯çš„æ–¹æ³•
               this.getUserInfo((data) => {
                    if (data) {
                         this.userInfo = data
                    } else {
                         this.userInfo = null
                    }
               })
               this._getCratList()
               this._getAddress()
               this.$nextTick(() => {
                    this.$refs.showpcarcontent.scrollTop = this.scrollTop
               })
          },
          beforeRouteLeave(to, from, next) {
               this.checked = false
               this.allPrice = 0
               this.allGoodsNum = 0
               this.manageBool = false
               next()
          },
          components: {
               HeaderPage,
               VlogGoods
          },
     }
</script>





