     }
     .updata_img{
          width: 80px;
          height: 80px;
          position: relative;
          img{
               width: 100%;
               height: 100%;
          }
          span{
               position: absolute;
               left: 50%;
               transform: translateX(-50%);
               bottom: 2px;
               font-size: 1rem;
               color: #9d9d9d;
          }
     }
     .comment_name{
          padding: 16px 12px;
          border-top: solid 1px #f6f6f6;
