          align-items: center;
          margin-top: 1.5rem;
          >span{
               margin-right: 10px;
               font-size: 1.3rem;
          }
     }
     .goods_info{
          margin-left: 1rem;
          overflow: hidden;
          flex-grow:1;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
     }
     .header{
          position: fixed;
          top: 0;
          left: 0;
          z-index: 20;
          width: 100%;
     }
     .right_fen{
          margin-left: 1rem;
          color: #9d9d9d;
     }
     .comment_content{
          padding-top: 4.4rem;
     }
</style>

<template>
     <!--è¯„è®ºä¸»é¡µ-->
     <div class="comment steep_padding" v-if="comInfo">
          <div class="header">
               <HeaderPage><span slot="headerTitle" v-if="!zhuiBool">å‘è¡¨è¯„ä»·</span><span slot="headerTitle" v-if="zhuiBool">è¿½åŠ è¯„ä»·</span></HeaderPage>
          </div>
          <div class="comment_content">
               <div class="com-info">
                    <div class="goods_thumb" :style="{backgroundImage:'url('+ comInfo.thumb +')'}"></div>
                    <div class="goods_info">
                         <div>
                              <p>{{ comInfo.title }}</p>
                              <span>{{ comInfo.specs }}</span>
                         </div>
                         <div v-if="!zhuiBool" class="fenList" style="margin-top: 0">
                              <span style="color: #212121">ç»¼åˆè¯„åˆ†</span>
                              <van-rate
                                      v-model="value"
                                      allow-half
                                      void-icon="star"
                                      void-color="#eee"
                                      color="#f55056"
                                      size="2.1rem"
                              />
                              <span class="right_fen">{{ value*2 }}åˆ†</span>
                         </div>
                    </div>

               </div>
               <div class="comment-text">
                    <textarea style="width: 100%;height: 21.7rem;border: none;font-size: 1.3rem;padding: 1rem 1.5rem;color: #9d9d9d;height: 14rem;" v-model="txt" placeholder="è¯·è¾“å…¥200ä»¥å†…"></textarea>
               </div>
               <div class="pictre_list">
                    <van-uploader
                            v-model="img"
                            multiple
                            :max-count="3"
                            accept="image/*"
                    >
                         <div  class="updata_img">
                              <img src="@/common/image/commentImg/usdateImg.png" alt="">
                              <span>{{ img.length }}/3</span>
                         </div>

                    </van-uploader>
               </div>
               <!--åŒ¿åè¯„ä»·-->
               <div class="comment_name" v-if="!zhuiBool">
                    <van-checkbox icon-size="13" checked-color="#f55056" v-model="commentName" style="font-size: 11px;">
                         <span style="color: #9d9d9d">åŒ¿åè¯„ä»·</span>
                    </van-checkbox>
               </div>
               <!--è¯„åˆ†-->
               <div class="comment_shop" v-if="!zhuiBool">
                    <h4>åº—é“ºè¯„ä»·</h4>
                    <div class="fenList">
                         <span>æœåŠ¡è¯„åˆ†</span>
                         <van-rate
                                 v-model="severFen"
                                 allow-half
                                 void-icon="star"
                                 void-color="#eee"
                                 color="#f55056"
                                 size="2.1rem"
                         />
                         <span class="right_fen">{{ severFen*2 }}åˆ†</span>
                    </div>
                    <div class="fenList">
                         <span>ç‰©æµè¯„åˆ†</span>
                         <van-rate
                                 v-model="logisticsFen"
                                 allow-half
                                 void-icon="star"
                                 void-color="#eee"
                                 color="#f55056"
                                 size="2.1rem"
                         />
                         <span class="right_fen">{{ logisticsFen*2 }}åˆ†</span>
                    </div>
                    <div class="fenList">
                         <span>è´¨é‡è¯„åˆ†</span>
                         <van-rate
                                 v-model="qualityFen"
                                 allow-half
                                 void-icon="star"
                                 void-color="#eee"
                                 color="#f55056"
                                 size="2.1rem"
                         />
                         <span class="right_fen">{{ qualityFen*2 }}åˆ†</span>
                    </div>
               </div>
               <span @click="sendComment" class="goot_sumit">å‘å¸ƒè¯„ä»·</span>
          </div>
     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     import storage from '@/common/js/storage.js'
     //å›¾ç‰‡åŽ‹ç¼©
     import lrz from 'lrz'

     export default {
          name: "CommentEeild",
          data() {
               return {
                    comInfo: null,
                    value: 5,
                    txt: "",
                    dialogImageUrl: '',
                    dialogVisible: false,
                    img: [],
                    zhuiBool: null,
                    newImgArr:[],
                    commentName:false,
                    severFen:0,
                    logisticsFen:0,
                    qualityFen:0
               }
          },
          methods: {
               //åŽ‹ç¼©å›¾ç‰‡è½¬æˆbase64
               dealImage(path, type, obj, callback) {
                    var img = new Image();
                    img.src = path;
                    img.onload = function () {
                         var that = this;
                         // é»˜è®¤æŒ‰æ¯”ä¾‹åŽ‹ç¼©
                         var w = that.width, h = that.height;
                         if (obj.width && obj.width < w ){
                              w = obj.width;
                              h = w / that.width * that.height
                         }
                         if (obj.height && obj.height < h ) {
                              h = obj.height;
                              w = h / that.height * that.width
                         }
                         //ç”Ÿæˆcanvas
                         var canvas = document.createElement('canvas'),
                             ctx = canvas.getContext('2d');
                         canvas.width = w;
                         canvas.height = h;
                         ctx.drawImage(that, 0, 0, w, h);

                         // é»˜è®¤å›¾ç‰‡è´¨é‡ä¸º0.7
                         var quality = 0.3;
                         if (obj.quality && obj.quality <= 1 && obj.quality > 0) {
                              quality = obj.quality;
                         }
                         // å›žè°ƒå‡½æ•°è¿”å›žbase64çš„å€¼
                         var base64 = canvas.toDataURL(type);
                         callback(base64);
                    }
               },
               //å°†base64è½¬æˆæ–‡ä»¶
               toFile(dataurl, filename) {
                    var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
                        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
                    while (n--) {
                         u8arr[n] = bstr.charCodeAt(n);
                    }
                    return new File([u8arr], filename, {type: mime});
               },
               //ä¸Šä¼ å›¾ç‰‡åˆ°åŽå°
               sendImg(file){
                    var that = this
                    this.postFile(file, (result) => {
                         if (result.code == 1) {
                              this.newImgArr.push(result.data.id)
                              if (this.newImgArr.length == this.img.length){
                                   this.haveImgComment()
                              }
                         } else {
                              that.$toast.center(result.info)
                         }
                    })
               },
               sendComment() {
                    var that = this
                    if (this.txt != ""){
                         this.$loading("æäº¤ä¸­")
                         if (this.img.length != 0){
                              this.img.forEach((item,index) => {
                                   // ä¸ç”¨åŽ‹ç¼©
                                   if (!(item.file.size >=2097152)) {
                                        that.sendImg(item.file)
                                   }else{
                                        //     åŽ‹ç¼©
                                        var reader = new FileReader();
                                        reader.readAsDataURL(item.file);
                                        reader.onload = function (e) {
                                             var image_base64 = this.result;
                                             that.dealImage(image_base64, item.file.type, {
                                                  width: 2000,
                                                  height:2000,
                                                  quality: 0.7
                                             }, function (base64Codes) {
                                                  var data = that.toFile(base64Codes, item.file.name)
                                                  that.sendImg(data)
                                             });
                                        }
                                   }
                              })

                         }else{
                              //æ²¡æœ‰å›¾ç‰‡æ˜¯è¯„è®º
                              this.noImgComment()
                         }
                    }else{
                         this.$toast.center("è¯·å¡«å†™æ‚¨æƒ³è¯„è®ºçš„å†…å®¹å§")
                    }

               },
              // æ— å›¾è¯„ä»·
              noImgComment(){
                   //è¯„ä»·
                   if (!this.zhuiBool) {
                        var obj = {}
                        obj.order_no = this.$route.params.order_no
                        obj.order_goods_id = this.comInfo.aid
                        obj.content = this.txt
                        obj.score = this.value * 2
                        obj.is_anon = this.commentName? 1:0
                        obj.goods_score = this.qualityFen * 2
                        obj.service_score = this.severFen * 2
                        obj.logistics_score = this.logisticsFen * 2
                        this.postObj(obj, PATHS.SHOPS_GOODS_COMMENT_ADD_INFO, (result) => {
                             if (result.code == 1) {
                                  //æ¸…é™¤è¯„è®ºåˆ—è¡¨æ•°æ®ï¼Œä½¿å…¶åˆ·æ–°
                                  storage.remove("commentindexInfo")
                                  this.$toast.center('è¯„ä»·æˆåŠŸ')
                                  setTimeout(() => {
                                       window.history.go(-1)
                                  }, 1500)
                             } else {
                                  this.$toast.center(result.info)
                             }
                        })
                        //     è¿½è¯„
                   } else {
                        var obj = {}
                        obj.comment_id = this.comInfo.comment_id
                        obj.content = this.txt
                        console.log(obj)
                        this.postObj(obj, PATHS.SHOPS_GOODS_COMMENT_ADD_INFO_TO, (result) => {
                             console.log(result)
                             if (result.code == 1) {
                                  //æ¸…é™¤è¯„è®ºåˆ—è¡¨æ•°æ®ï¼Œä½¿å…¶åˆ·æ–°
                                  storage.remove("commentindexInfo")
                                  this.$toast.center('è¿½è¯„æˆåŠŸ')
                                  setTimeout(() => {
                                       window.history.go(-1)
                                  }, 1500)
                             } else {
                                  this.$toast.center(result.info)
                             }
                        })
                   }
              },
          //     æœ‰å›¾è¯„ä»·
               haveImgComment(){
                    //è¯„ä»·
                    if (!this.zhuiBool) {
                         var obj = {}
                         obj.order_no = this.$route.params.order_no
                         obj.order_goods_id = this.comInfo.aid
                         obj.content = this.txt
                         obj.score = this.value * 2
                         obj.is_anon = this.commentName? 1:0
                         obj.goods_score = this.qualityFen * 2
                         obj.service_score = this.severFen * 2
                         obj.logistics_score = this.logisticsFen * 2
                         var paths = []
                         this.newImgArr.forEach((item, index) => {
                              paths.push(item)
                         })
                         obj.pictures = paths.join(',')
                         console.log(obj)
                         this.postObj(obj, PATHS.SHOPS_GOODS_COMMENT_ADD_INFO, (result) => {
                              if (result.code == 1) {
                                   //æ¸…é™¤è¯„è®ºåˆ—è¡¨æ•°æ®ï¼Œä½¿å…¶åˆ·æ–°
                                   storage.remove("commentindexInfo")
                                   this.$toast.center('è¯„ä»·æˆåŠŸ')
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   }, 1500)
                              } else {
                                   this.$toast.center(result.info)

                              }
                         })
                         //     è¿½è¯„
                    } else {
                         var obj = {}
                         obj.comment_id = this.comInfo.comment_id
                         obj.content = this.txt
                              var paths = []
                              this.newImgArr.forEach((item, index) => {
                                   paths.push(item)
                              })
                              var pictures = paths.join(',')
                              obj.pictures = pictures
                         this.postObj(obj, PATHS.SHOPS_GOODS_COMMENT_ADD_INFO_TO, (result) => {
                              console.log(result)
                              if (result.code == 1) {
                                   this.$toast.center('è¿½è¯„æˆåŠŸ')
                                   setTimeout(() => {
                                        location.href = "#/order"
                                   }, 1500)
                              } else {
                                   this.$toast.center(result.info)
                              }
                         })
                    }
               },
          },
          mounted() {
               this.zhuiBool = this.$route.params.bool
               this.comInfo = this.$route.params.comInfo
               if (this.$route.params.zhuiBool) {
                    this.zhuiBool = this.$route.params.zhuiBool
               }
          },
          watch:{
               // newImgArr(newold,oldval){
               //      if (newold.length == this.img.length) {
               //
               //           console.log(this.img.length)
               //      }
               // }
               img(newVal,oldVal){
                    console.log(newVal)
               }
          },
          components: {
               HeaderPage
          }
     }
</script>




