               submitObj() {
                    var obj = {
                         order_no: this.orderInfo.order_no
                    };
                    // æ‰‹æœºå·éªŒè¯
                    if (this.mobile != "") {
                         var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
                         if (!myreg.test(this.mobile)) {
                              this.$toast.center("è¯·è¾“å…¥æ­£ç¡®çš„æ‰‹æœºå·");
                              return;
                         } else {
                              obj.mobile = this.mobile;
                         }
                    } else {
                         this.$toast.center("è¯·è¾“å…¥æ‰‹æœºå·");
                         return
                    }
                    // é‚®ç®±éªŒè¯
                    if (this.email != "") {
                         var reg = new RegExp(
                             "^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"
                         );
                         if (reg.test(this.email)) {
                              obj.email = this.email;
                         } else {
                              this.$toast.center("è¯·è¾“å…¥æ­£ç¡®çš„é‚®ç®±");
                              return;
                         }
                    }
                    // ä¸ªäººæäº¤
                    if (this.radioType == "ä¸ªäºº") {
                         obj.name = "ä¸ªäºº";
                    }
                    //å…¬å¸æäº¤
                    if (this.radioType == "å•ä½"){
                         if (this.duty != ''){
                              obj.taxpayer_id = this.duty
                         }else{
                              this.$toast.center("è¯·è¾“å…¥çº³ç¨Žäººè¯†åˆ«ç ")
                              return
                         }
                         if (this.unitName != ''){
                              obj.name = this.unitName
                         } else{
                              this.$toast.center("è¯·è¾“å…¥å•ä½åç§°")
                              return
                         }
                         //æ¬¡è¦
                         if (this.bank_name != ''){
                              obj.bank_name = this.bank_name
                              if (this.bank_no == ''){
                                   this.$toast.center("è¯·è¾“å…¥é“¶è¡Œå¡å·")
                                   return
                              }
                         }
                         if (this.bank_no != ''){
                              var bank_new = this.bank_no.replace(/\s*/g, '');
                              var regExp = /^([1-9]{1})(\d{15}|\d{18})$/;
                              if (regExp.test(bank_new)){
                                   obj.bank_no = bank_new
                                   if (this.bank_name == ''){
                                        this.$toast.center("è¯·è¾“å¼€æˆ·è¡Œåç§°")
                                   }
                              }else{
                                   this.$toast.center("é“¶è¡Œå¡å·æ ¼å¼ä¸æ­£ç¡®")
                                   return
                              }

                         }
                         if (this.address != ''){
