               ["bank_no"](newval){
                    // å››ä½ç©ºä¸€æ ¼
                    this.$nextTick(() => {
                         this.bank_no = newval.replace(/\s/g,'').replace(/....(?!$)/g,'$& ');
                    });
               }
          },
          beforeRouteLeave(to,from,next){
