<template>
     <!--è¯„ä»·-->
     <div class="comment-content" v-if="comInfo">
          <div class="header">
               <HeaderPage>è¯„ä»·æ™’å•</HeaderPage>
          </div>
          <ul>
               <li v-for="(item,index) in comInfo.goods">
                    <img :src=item.thumb alt="" style="width: 30%;height: auto; border-radius: 10px; float: left" />
                    <div style="float: left;">
                         <p>{{ item.title }}</p>
                         <div style="display: flex;justify-content: flex-end;margin-top: 2rem">
                              <span v-if="item.comment_id == 0" @click="tocomment(item,comInfo.order_no,false)" style="margin-right: 1rem">è¯„ä»·</span>
                              <span v-if="item.comment_id != 0 && item.is_addcom != 1"  @click="tocomment(item,comInfo.order_no,true)" style="background: none;color: #000;border: solid 1px #e7204f;margin-right: 1rem">è¿½è¯„</span>
                              <span v-if="item.is_addcom == 1"  style="background: none;color: #000;border: solid 1px #e7204f;margin-right: 1rem">å·²è¿½è¯„</span>
                         </div>
                    </div>
               </li>
          </ul>
     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     export default {
          name: "CommentPage",
          data(){
               return {
                    comInfo:null
               }
          },
          methods:{
               tocomment(comInfo,order_no,bool){
                    console.log(bool)
                    if (bool){
                         this.$router.push({name:'commenteeild',params:{comInfo,order_no,zhuiBool:bool}})
                    } else {
                         this.$router.push({name:'commenteeild',params:{comInfo,order_no}})
                    }
               }
          },
          mounted(){
          //     è®¢å•å·æŸ¥è¯¢ä¿¡æ¯
               this.postObj({order_no:this.$store.state.commentOrder_no},window.PATHS.SHOPS_ORDER_GET_INFO,(result) => {
                    if (result.code == 1){
                         this.comInfo = result.data
                         console.log(this.comInfo)
                    } else {
                         this.$toast.center(result.info)
                    }
               })

          },
          components:{
               HeaderPage
          }
     }
</script>

<style scoped>
     ul li > div span{
          display: block;
          line-height: 2.9rem;
          width: 7.5rem;
          border-radius: 0.5rem;
          font-size: 1.5rem;
          text-align: center;
          background-color: #e7204f;
          right: 1rem;
          bottom: -5rem;
          color: #ffffff;
     }
     ul li > div p{
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
     }
     ul li > div{
          position: relative;
          margin-top: 1rem;
          font-size: 1.3rem;
          padding-left: 1rem;
          width: 70%;
          height: 100%;
     }
     ul li{
          background-color:#ffffff;
          width: 95%;
          margin: 0 auto;
          margin-top: 1rem;
          border-radius: 10px;
          overflow: hidden;
     }
     .comment-content{
          overflow: scroll;
          height: 100%;
          background-color: #f6f6f6;
     }
</style>



