<style scoped lang="scss">
     .progress-text span:last-of-type{
          margin-right: 1rem;
     }
     .progress-text span:first-of-type{
          margin-left: 1rem;
          color: white;
     }


     .boosPrice{
          position: absolute;
          display: block;
          font-size: 1.1rem;
          color: white;
          background: rgba(0, 0, 0, 0.31);
          text-align: center;
          width: 100%;
          bottom:0;
          line-height: 2.7rem;
          border-bottom-left-radius: 1rem;
          border-bottom-right-radius: 1rem;
     }
     .join{
          position: absolute;
          display: block;
          left: 0;
          top: 1rem;
          font-size: 1.1rem;
          color: white;
          background-color: rgba(0, 0, 0, 0.31);
          text-align: center;
          width: 4rem;
          line-height: 1.6rem;
          border-top-right-radius: 0.8rem;
          border-bottom-right-radius: 0.8rem;
     }
     .team-content ul li .li-right > span{
          display: block;
          position: absolute;
          width: 7.6rem;
          line-height: 3rem;
          text-align: center;
          right: 0;
          bottom: 0rem;
          background-color: #f55056;
          border-radius: 1.5rem;
          color: #ffffff;
          font-size: 1.4rem;
     }
     .team-content ul li .li-right p{
          line-height: 2rem;
          font-size: 1.3rem;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
          font-weight: 500;

     }
     .team-content ul li .li-right{
          position: relative;
          margin-left: 1rem;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          flex-grow: 1;
     }
     .team-content ul li .li-left{
          position: relative;
          width: 10rem;
          height: 10rem;
          border-radius: 5px;
          overflow: hidden;
          display: flex;
          flex-shrink: 0;
     }
     .team-content ul li{
          padding: 15px;
          background-color: white;
          display: flex;
          justify-content: flex-start;
          margin-bottom: 1rem;
          border-radius: 5px;
     }
     .team-content ul{
          padding: 1.2rem;
     }
     .van-nav-bar{
          line-height: 4.4rem;
          height: 4.4rem;
     }
     .van-nav-bar__title{
          font-size: 1.7rem;
     }
     .van-nav-bar__arrow{
          font-size: 2.5rem;
          color: #000;
     }
     .team-content{
          background-color: #f6f6f6;
          height: 100vh;
          overflow-y: scroll;
          padding-top: 4.4rem;
     }
     .progress{
          font-size: 1-rem;
          color: white;
          background-color: #f55056;
          width: 80%;
          border-radius: 0.8rem;
          display: flex;
          justify-content: space-between;
          padding: 0 5px;
     }
     .shop_prcie{
          margin-right: 10px;
          span{
               font-family: æ•°å­—ä¸“ç”¨;
               font-size: 1.1rem;
               color: #f55056
          }
     }
     .pinend{
          display: flex;
          align-items: center;
          img{
               width: 1.3rem;
               height: 1rem;
               margin-right: 3px;
          }
          span{
               font-size: 1.2rem;
               color: #e42f07;
          }
     }
     .team_head{
          width: 100%;
          position: fixed;
          top: 0;
          background-color: #ffffff;
          z-index: 10;
     }
     .header_title{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: 1.7rem;
     }
     .team_content{
          padding-top: 4.4rem;
     }
</style>
<template>
  <!--æ‹¼å›¢é¡µé¢-->
  <div class="team-content steep_padding">
       <!--å ä½å›¾-->
       <template v-if="skeleton">
            <div class="zhanwei team_zhanwei" style="background-image: url('@/common/image/team/zhanwei.png')">
                 <div class="head">
                      <van-nav-bar
                              title="æ‹¼å›¢"
                              left-arrow
                              @click-left="onClickLeft"
                      />
                 </div>
            </div>
       </template>
       <div class="team_head">
            <HeaderPage>
                 <span slot="headerTitle">æ‹¼å›¢</span>
                 <div slot="headerRight" @click="toSearch">
                      <img src="@/common/image/publicImg/search.png" alt="" style="width: 1.5rem;height: 1.5rem;">
                 </div>
            </HeaderPage>
       </div>
       <div class="team_content">
            <ul v-if="comInfo.length != 0">
                 <li v-for="(item,index) in comInfo":key="index + 'justnm'" @click="toDetailsPage(item.aid)">
                      <div class="li-left">
                           <img class="same_img" :src="item.thumb" />
                      </div>
                      <div class="li-right">
                           <p>{{ item.title }}</p>
                           <div class="progress">
                                <!--<span>è¿˜å·®{{ item.extra_data.number - item.extra_data.current }}äººæˆå›¢</span>-->
                                <span style="font-weight:600">{{ item.extra_data.number }}äººå›¢</span>
                                <span style="font-weight:600" v-if="item.rebate_price > 0">å›¢é•¿ä½£é‡‘ï¿¥{{ item.rebate_price }}</span>
                           </div>
                           <div>
                                <div>
                                  <span class="shop_prcie">
                                       <span>ï¿¥</span>
                                       <span style="font-size: 1.8rem">{{ item.shop_price.substring(0,item.shop_price.length-3) }}</span>
                                       <span>{{ item.shop_price.substring(item.shop_price.length-3,item.shop_price.length) }}</span>
                                  </span>
                                     <span style="text-decoration: line-through;font-size: 1.2rem;color: #888888;">ï¿¥{{ item.market_price }}</span>
                                </div>
                                <div class="pinend">
                                     <img src="@/common/image/HomeImg/mail/mail_ico_Number.png"/>
                                     <span><span class="num_family">{{ item.sales }}</span>äººå·²æ‹¼</span>
                                </div>
                           </div>
                           <span>å‘èµ·æ‹¼å›¢</span>
                      </div>
                 </li>
            </ul>
       </div>

  </div>
</template>


<script>
     import HeaderPage from "../components/HeaderPage"
  export default {
    name: "ZeroPage",
       data(){
         return{
              comInfo:[],
              skeleton:true
         }
       },
    methods:{
         //åˆ°æœç´¢é¡µé¢
         toSearch(){
              location.href = "#/search";
         },
      //   åˆ°å•†å“è¯¦æƒ…é¡µ
         toDetailsPage(good_id){
              this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
         },
      onClickLeft(){
        window.history.go(-1)
      }
    },
       mounted() {
            //èŽ·å–æ‹¼å›¢æ•°æ®
            var obj1 = {
                 goods_type:'goods_group',
                 page:1
            }
            this.postObj(obj1,window.PATHS.SHOPS_GOODS_GET_LIST,(result) => {
                 if (result.code == 0){
                      this.$toast.center(result.info)
                      return
                 }
                 this.comInfo = result.data
                 this.skeleton = false
                 console.log(this.comInfo)
            })
       },
       components:{
            HeaderPage
       }
  }
</script>



