<style scoped lang="scss">
     .active_table {
          font-size: 1.5rem;
          font-weight: 600;
     }

     .flexd_top_relode img {
          width: 3.5rem;
          height: 3.5rem;
     }

     .flexd_top_relode {
          position: fixed;
          right: 1.4rem;
          bottom: 8.3rem;
     }

     .van-pull-refresh {
          margin-top: -12rem;
          min-height: 100vh;
     }
     .like img {
          width: 1.9rem;
          height: 1.8rem;
     }

     .like {
          align-items: center;
          right: 0;
          bottom: 1.3rem;
          display: flex;
          justify-content: flex-end;
     }

     .goods_info {
          width: 20rem;
          position: relative;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
     }


     .good_list p {
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
          margin-top: 13px;
          font-size: 1.2rem;
          color: #9d9d9d;
     }

     .good_list h6 {
          font-size: 1.5rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          word-break: break-all;

     }

     .good_img img {
          width: 100%;
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          min-height: 100%;
     }

     .good_img {
          width: 12rem;
          height: 12rem;
          position: relative;
          overflow: hidden;
          border-radius: 0.5rem;
          display: flex;
          flex-shrink: 0;
     }

     .good_list ul {
          background-color: #ffffff;
          padding: 17.5px 9.5px;
          border-radius: 0.5rem;
     }

     .good_list {
          padding: 0 1.2rem;
          margin-top: 1rem;
     }

     .banner {
          margin-top: -10rem;
     }

     .mustbuygoods {
          height: 100vh;
          overflow-y: scroll;
          background-color: #f6f6f6;
     }
     .header_table ul li {
          margin-right: 2.5rem;
          display: inline-block;
     }
     .header_table ul {
          display: inline-block;
          font-size: 1.3rem;
          white-space: nowrap;
          color: #ffffff;
          padding-left: 1.5rem;
     }
     .header_table {
          overflow-x: scroll;
          width: 100%;
          height: 5rem;
          display: flex;
          align-items: center;
     }
     .left_back {
          width: 4rem;
          line-height: 100%;
          text-align: left;
     }
     .header_titles > span {
          color: #ffffff;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          font-size: 1.7rem;
     }

     .header_titles {
          height: 4.4rem;
          padding: 0 1.2rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: relative;
     }

     .header {
          background-image: url("~@/common/image/mustbuy/background@2x.png");
          background-size: 100% auto;
          background-position: 0 0;
          background-repeat: no-repeat;
          overflow: hidden;
          position: fixed;
          width: 100%;
          top: 0;
          z-index: 20;
     }
     .must_zhanwe{
          background-image: url("~@/common/image/mustbuy/zhanwei.png");
     }
     .good_list ul li:first-child{
          margin-top: 0;
     }
     .good_list ul li {
          display: flex;
          justify-content: space-between;
          width: 100%;
          height: 12rem;
          margin-top: 10px;
          .good_img{
               width: 12rem;
               height: 100%;
               img{

               }
          }

     }
     .go_buy{
          width: 7rem;
          height: 2.9rem;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.3rem;
          background-image: linear-gradient(to right, #ff8689,#f55056);
          color: #ffffff;
          border-radius: 1.45rem;
          margin-right: 10px;

     }
</style>
<template>
     <div class="mustbuygoods">
          <!--å¿…ä¹°å¥½è´§-->
          <!--å ä½å›¾-->
          <template v-if="skeleton">
               <div class="zhanwei must_zhanwe">
                    <div class="header">
                         <div class="header_titles">
                              <span>å¿…ä¹°å¥½è´§</span>
                              <div onclick="javascript:history.go(-1)" class="left_back">
                                   <img src="@/common/image/publicImg/whiteLeft.png" alt=""
                                        style="width: 0.75rem;height: 1.4rem;">
                              </div>
                         </div>
                    </div>
               </div>
          </template>
          <template v-if="!skeleton">
               <div class="header">
                         <HeaderPage :colorType="'white'">
                              <span slot="headerTitle">å¿…ä¹°å¥½è´§</span>
                              <div slot="headerRight" @click="toSearch" class="header_right">
                                   <img style="width: 1.4rem;height: 1.3rem;" src="@/common/image/publicImg/nav_ico_search.png" alt="">
                              </div>
                         </HeaderPage>
                    <div class="header_table">
                         <ul>
                              <li
                                      ref="tableLi"
                                      @click="clkTable(index,item.name)"
                                      :class="index == tabIns? 'active_table' : ''"
                                      v-for="(item,index) in tagList"
                                      :key="index + 'tagins'"
                              >
                                   {{ item.name }}
                              </li>
                         </ul>
                    </div>
               </div>
               <img src="@/common/image/mustbuy/background@2x.png" alt="" style="width: 100%;">
               <van-pull-refresh class="steep_padding" v-model="isLoading" @refresh="onRefresh">
                    <swiper :options="swiperOption1" v-if="homeMes != null">
                         <swiper-slide v-for="(baneeritem,bannerindex) in homeMes.sider" :key="bannerindex + 'baneings'">
                              <div style="display: flex;justify-content: center">
                                   <img @click="toSqueezePage(baneeritem.url)" :src="baneeritem.cover"
                                        style="width: 34.4rem;height: 13.1rem;border-radius: 0.5rem"/>
                              </div>
                         </swiper-slide>
                         <div class="swiper-pagination" slot="pagination"></div>
                    </swiper>
                    <div class="good_list" v-if="goodsList.length != 0">
                         <ul>
                              <li @click.stop="toDetail(item.goods_id)" v-for="(item,index) in goodsList" :key="index + 'goodsIndex'">
                                   <div class="good_img">
                                        <img :src="item.thumb" alt="">
                                   </div>
                                   <div class="goods_info">
                                        <div>
                                             <h6>{{ item.title }}</h6>
                                             <p>{{ item.content }}</p>
                                        </div>
                                        <div class="like">
                                             <span class="go_buy">åŽ»è´­ä¹°</span>
                                             <div @click.stop="like(item.aid,index)">
                                                  <img v-if="item.is_star == 0" src="@/common/image/mustbuy/collection_nor@2x.png" alt="">
                                                  <img v-if="item.is_star == 1" src="@/common/image/mustbuy/collection_pre@2x.png" alt="">
                                             </div>
                                        </div>
                                   </div>
                              </li>
                         </ul>
                    </div>
               </van-pull-refresh>
               <!--æ‚¬æµ®çš„è¿”å›žé¡¶éƒ¨ï¼Œä¸Žåˆ·æ–°-->
               <div class="flexd_top_relode">
                    <img @click="scrollTo(0)" src="@/common/image/mustbuy/mail_ico_Roofplacement.png" alt=""><br/>
                    <img @click="onRefresh" src="@/common/image/mustbuy/refresh@2x.png" alt="">
               </div>
          </template>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "MustbuyGoods",
          data() {
               return {
                    skeleton:true,
                    homeMes: null,
                    tagList:[],
                    isLoading: false,
                    goodsList: [],
                    goodsListPage: 1,
                    tabIns:0,
                    tag:"",
                    is_recommand:"",
                    is_hot:"",
                    swiperOption1: {
                         zoom: false,
                         pagination: {
                              el: '.swiper-pagination'
                         },
                         speed: 500,
                         loop: true,
                         autoplay: 2000
                    },

               }
          },
          methods: {
               //æ”¶è—å¿…ä¹°å¥½è´§
               like(find_id,index){
                    var obj = {
                         find_id
                    }
                    this.postObj(obj,window.PATHS.mustBuy.SHOPSGOODSFIND_STAR,(result) => {
                         if (result.code == 1){
                              if (this.goodsList[index].is_star == 0){
                                   this.goodsList[index].is_star = 1
                                   this.$toast.center("å·²æ”¶è—")
                              } else{
                                   this.goodsList[index].is_star = 0
                              }

                         }
                    })
               },
               clkTable(index,name){
                    this.tabIns = index
                    var liLeft =  this.$refs.tableLi[index].offsetLeft
                    var evWidth = this.$refs.tableLi[index].clientWidth / 2
                    var windWidth = document.body.offsetWidth / 2
                    this.tableScroll = liLeft - windWidth + evWidth
                    $(".header_table").animate({
                         scrollLeft: liLeft - windWidth + evWidth
                    },500)
                    this.goodsList = []
                    if (name == "å…¨éƒ¨"){
                         this.tag = ""
                         this.is_recommand = ""
                         this.is_hot = ""
                    } else if (name == 'çƒ­é—¨') {
                         this.tag = ""
                         this.is_recommand = ""
                         this.is_hot = 1
                    }
                    else if (name == 'æŽ¨è') {
                         this.tag = ""
                         this.is_recommand = 1
                         this.is_hot = ""
                    }else{
                         this.tag = name
                         this.is_recommand = ""
                         this.is_hot = ""
                    }
                    this.getGoodsList()
               },
               //èŽ·å–å¿…ä¹°å¥½è´§æ ‡ç­¾åˆ—è¡¨
               gettags(){
                    var obj = {}
                    this.postObj(obj,window.PATHS.mustBuy.SHOPSGOODSFIND_TAGS,(result) => {
                         if (result.code == 1){
                              this.tagList = result.data
                              this.tagList.unshift({name:"æŽ¨è"})
                              this.tagList.unshift({name:"çƒ­é—¨"})
                              this.tagList.unshift({name:"å…¨éƒ¨"})
                         }
                    })
               },
               //èŽ·å–ä¸Šå¹³åˆ—è¡¨
               getGoodsList() {
                    var obj = {
                         page: this.goodsListPage,
                         tag:this.tag,
                         is_recommand:this.is_recommand,
                         is_hot:this.is_hot
                    }
                    this.postObj(obj, window.PATHS.mustBuy.SHOPSGOODSFIND_GETGODSLIST, (result) => {
                         if (result.code == 1) {
                               this.skeleton = false
                              this.goodsList = this.goodsList.concat(result.data)
                              console.log(result.data)
                         }
                    })
               },
               //åˆ°è¯¦æƒ…
               toDetail(good_id) {
                    this.$router.push({
                         name:"DetailsPage",
                         query:{
                              comId: good_id
                         }
                    })
               },
               //ç‚¹å‡»å¹³æ»‘æ»‘åŠ¨
               scrollTo(index) {
                    var htbody1 = document.documentElement
                    var htbody2 = document.body
                    if (index == 0) {
                         $(".mustbuygoods").animate({scrollTop: 0}, 400);
                    }
               },
               //åˆ°æœç´¢é¡µé¢
               toSearch() {
                    location.href = "#/search"
               },
               onRefresh() {
                    this.goodsListPage = 1
                    this.goodsList = []
                    this.getHomeMes()
                    this.getGoodsList()
                    setTimeout(() => {
                         this.$toast('åˆ·æ–°æˆåŠŸ');
                         this.isLoading = false;
                    }, 500);
               },
               //     èŽ·å–é¦–é¡µæ•°æ®
               getHomeMes() {
                    var obj = {}
                    this.postObj(obj, window.PATHS.HOME_VESIONONE, (result) => {
                         if (result.code == 1) {
                              this.homeMes = result.data
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          },
          created() {
               this.getHomeMes()
               this.getGoodsList()
               //     èŽ·å–æ ‡ç­¾åˆ—è¡¨
               this.gettags()
          },
          activated(){
               if (this.$route.meta.keepLoadBool) {
                    console.log("hehe")
                    this.getHomeMes()
                    this.getGoodsList()
                    //     èŽ·å–æ ‡ç­¾åˆ—è¡¨
                    this.gettags()
               }
          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to,from,next){
               if (to.name == '/'){
                    this.goodsList = []
                    this.skeleton = true
                    from.meta.keepLoadBool = true
               } else{
                    from.meta.keepLoadBool = false
               }
               next()
          },

     }
</script>





