
<style scoped lang="scss">
     .overFooter{
          font-size: 1.3rem;
     }
     .list{
          min-height: 100vh;
     }
     .footer_loading{
          margin-top: 10px;
          padding-bottom: 20px;
          text-align: center;
     }
     .foot_dele{
          height: 100%;
          width: 14rem;
          background-color: #f55056;
          color: #ffffff;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.7rem;
     }
     .chickAll{
          font-size: 13px;
     }
     .footer{
          position: fixed;
          bottom: 0;
          height: 4.9rem;
          width: 100%;
          background-color: #ffffff;
          padding-left: 13px;
          display: flex;
          align-items: center;
          justify-content: space-between;
     }
     .van-button--normal{
          height: 100%;
     }
     .time {
          color: #9d9d9d;
          font-size: 15px;
          position: relative;
     }
     .price {
          font-size: 18px;
          color: #f55056;
          font-weight: 500;
     }

     .goods_miaoshu img {
          width: 1.3rem;
          height: 0.3rem;
     }

     .goods_miaoshu p {
          font-size: 13px;
          font-weight: 500;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;

     }

     .goods_miaoshu > div {
          display: flex;
          justify-content: space-between;
          align-items: center;
     }

     .goods_miaoshu {
          display: flex;
          flex-direction: column;
          margin-left: 5px;
          justify-content: space-between;
          width: 100%;
     }

     .van-checkbox {
          overflow: unset;
     }

     .goods_content {
          display: flex;
          justify-content: flex-start;
     }

     .van-checkbox {
          margin-right: 10px;
     }

     .goods_img {
          width: 10rem;
          height: 10rem;
          position: relative;
          overflow: hidden;
          border-radius: 5px;
          display: flex;
          flex-shrink: 0;
     }
     .van-cell:not(:last-child)::after{
          display: none;
     }

     .list ul li {
          border-bottom: solid 1px #f6f6f6;
          background-color: #ffffff;
     }

     .header_left img {
          width: 1.25rem;
          height: 2.2rem;
     }

     .header_title {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          font-size: 1.7rem;
     }

     .foter_header {
          z-index: 10;
          width: 100%;
          position: fixed;
          top: 0;
          background-color: #ffffff;
     }

     #footprint {
          height: 100vh;
          overflow-y: scroll;
          background-color: #f6f6f6;
     }
</style>

<template>
     <!--æµè§ˆè¶³è¿¹-->
     <div id="footprint" ref="footerPrint" class="steep_padding" @scroll="scroll">
          <div class="foter_header">
               <HeaderPage>
                    <span slot="headerTitle">æµè§ˆè¶³è¿¹</span>
                    <div slot="headerRight">
                         <span @click="footerBool = !footerBool" v-if="!footerBool" style="font-size: 1.3rem">ç®¡ç†</span>
                         <span @click="footerBool = !footerBool, result=[] , checked = false" v-if="footerBool" style="font-size: 1.3rem">å®Œæˆ</span>
                    </div>
               </HeaderPage>


          </div>
          <div style="padding-top: 4.4rem">
               <van-checkbox-group v-model="result" ref="checkboxGroup">
                    <div class="list">
                         <ul>
                              <li v-for="(item,index) in historyList" :key="index + 'hislis'">
                                   <van-swipe-cell ref="history">
                                        <van-cell>
                                             <span class="time">{{ item.update_time.substring(0,10) }}</span>
                                             <div class="goods_content">
                                                  <van-checkbox :name="item.aid" v-if="footerBool" checked-color="#f55056"></van-checkbox>
                                                  <div @click="toDetail(item.goods_id)" class="goods_img">
                                                       <img class="same_img" :src="item.thumb" alt="">
                                                  </div>
                                                  <div  @click="toDetail(item.goods_id)" class="goods_miaoshu">
                                                       <p>{{ item.title }}</p>
                                                       <div>
                                                            <span class="price">ï¿¥{{ item.shop_price }}</span>
                                                            <div @click.stop="showShopClear(index)" style="height: 3rem;width: 3rem;">
                                                                 <img src="@/common/image/publicImg/threedian.png" alt="">
                                                            </div>
                                                       </div>
                                                  </div>
                                             </div>
                                        </van-cell>
                                        <template slot="right">
                                             <van-button @click.stop="cleanOneHistory(item.aid,index)" square type="danger" text="åˆ é™¤"/>
                                        </template>
                                   </van-swipe-cell>
                              </li>
                         </ul>
                    </div>
               </van-checkbox-group>
               <div class="footer_loading">
                    <van-loading size="20" v-if="goodsLoadBool">åŠ è½½ä¸­...</van-loading>
                    <span v-if="!goodsLoadBool" class="overFooter">å·²ç»æ»‘åˆ°åº•äº†</span>
               </div>
               <div class="footer" v-if="footerBool">
                    <div style="display: flex;align-items: center">
                         <div @click="choosAll">
                              <van-checkbox checked-color="#f55056" v-model="checked"><span class="chickAll">å…¨é€‰</span></van-checkbox>
                         </div>
                         <span @click="clearAll" style="font-size: 1.1rem;color: #f55056">ä¸€é”®æ¸…é™¤è¶³è¿¹</span>
                    </div>
                    <span @click="footerDelete" class="foot_dele">åˆ é™¤</span>
               </div>
          </div>
     </div>
</template>

<script>
     import Vue from 'vue';
     import HeaderPage from "../components/HeaderPage"
     import { Dialog } from 'vant';
     export default {
          name: "FootprintPage",
          data() {
               return {
                    historyList: [],
                    page: 1,
                    keyTime: "",
                    result: [],
                    checked:false,
                    footerBool:false,
                    goodsLoadBool:true,
                    scrollGetBool:true,
                    footLoadMaxBool:true,
                    scrollTop:0
               }
          },
          methods: {
               scroll(){
                    var height = this.$refs.footerPrint.scrollHeight
                    var scrollTop = this.$refs.footerPrint.scrollTop
                    this.scrollTop = this.$refs.footerPrint.scrollTop
                    var windowHeight = window.innerHeight
                    this.scrollMore(scrollTop,height,windowHeight,(result) => {
                         if (this.scrollGetBool) {
                              this.scrollGetBool = false
                              if (this.goodsLoadBool){
                                   this.page++
                                   this.getHistoryList()
                              }
                         }
                    })
               },
               //åˆ°å•†å“è¯¦æƒ…é¡µ
               toDetail(good_id){
                    this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
               },
               //æ»‘åŠ¨å‡ºçŽ°åˆ é™¤çš„ç‚¹å‡»äº‹ä»¶
               cleanOneHistory(aid,index){
                    var obj = {
                         type:'id',
                         value:aid
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_DEL_HISTORY,(result) => {
                         if (result.code == 1){
                              this.historyList.splice(index, 1);
                              this.$refs.history.forEach((item,index) => {
                                   item.close()
                              })
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               showShopClear(index){
                    this.$refs.history[index].open('right')
               },
               //ä¸€é”®æ¸…ç©º
               clearAll(){
                    Dialog.confirm({
                         message: 'ç¡®è®¤æ¸…ç©ºæ‰€æœ‰è¶³è¿¹ï¼ï¼ï¼'
                    }).then(() => {
                         var obj = {
                              type:"all",
                         }
                         this.postObj(obj,window.PATHS.SHOPS_GOODS_DEL_HISTORY,(result) => {
                              if (result.code == 1){
                                   this.historyList = []
                              }else{
                                   this.$toast.center(result.info)
                              }
                         })
                    }).catch(() => {
                         // on cancel
                    });

               },
               //åˆ é™¤å¤šä¸ª
               footerDelete(){
                    var obj = {
                         type:"ids",
                         value:""
                    }
                    if (this.result.length == 1){
                         obj.value = this.result[0]
                    } else{
                         obj.value = this.result.join(",")
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_DEL_HISTORY,(result) => {
                         if (result.code == 1){
                              for (var i = 0;i < this.result.length;i++){
                                  this.historyList.forEach((item,index) => {
                                       if (item.aid == this.result[i]){
                                            this.historyList.splice(index, 1);
                                       }
                                  })
                              }
                         }else{
                              this.$toast.center(result.info)
                         }
                    })

               },
               //å…¨é€‰
               choosAll(){
                    this.checked = !this.checked
                    this.$refs.checkboxGroup.toggleAll(this.checked);
               },
               checkAll() {
                    // console.log(this.$refs)
                    this.$refs.checkboxGroup.toggleAll()
               },
               //ç­›é€‰æ—¥æœŸ
               hisListDate() {
                    this.historyList.forEach((item, index) => {
                         if (index == 0) {
                              this.keyTime = item.update_time.substring(0, 10)
                         } else {
                              if (item.update_time.substring(0, 10) == this.keyTime) {
                                   this.historyList[index].update_time = ""
                              } else {
                                   this.keyTime = item.update_time.substring(0, 10)
                              }
                         }
                    })
               },
               getHistoryList() {
                    var obj = {
                         page: this.page
                    }
                    this.postObj(obj, window.PATHS.SHOPS_GOODS_HISTORY, (result) => {
                         if (result.code == 1) {
                              if (result.data.length != 0){
                                   this.historyList = this.historyList.concat(result.data)
                              } else{
                                   this.goodsLoadBool = false
                              }
                              setTimeout(() => {
                                   this.scrollGetBool = true
                              },500)
                         }
                    })
               },
          },
          mounted(){
          },
          created() {
               this.getHistoryList()
          },
          activated(){
               if (this.$refs.footerPrint){
                    this.$refs.footerPrint.scrollTop = this.scrollTop
               }
          },
          components:{
               HeaderPage
          },
     }
</script>




