<style scoped lang="scss">
     .shopUserRemake{
          background-color: #f6f6f6;
          border-radius: 0.5rem;
          padding: 1rem;
          color: #6e6d6d;
          font-size: 1.4rem;
          margin-top: 1rem;
     }
     .commentList li{
          background-color: #ffffff;
          padding: 1rem 1.2rem;
          margin-bottom: 1rem;
          display: flex;
          justify-content: space-between;
     }
     .topComment{
          padding-top: 2rem;
          overflow: hidden;
     }
     .topComment li{
          float: left;
          color: #000;
          padding: 0 0.5rem;
          background-color: #f4e0e5;
          font-size: 1.5rem;
          margin-left: 1rem;
          margin-bottom: 1rem;
          line-height: 3.4rem;
          border-radius: 0.5rem;
     }
     .header{
          background-color: #ffffff;
          position: fixed;
          width: 100%;
          top: 0;
          left: 0;
     }
     .commentList-content{
          background-color: #f6f6f6;
          color: #212121;
          height: 100vh;
          overflow-y: scroll;
     }
     .comment_list_usimg{
          background-size: cover;
          width: 3rem;
          height: 3rem;
          border-radius: 50%;
     }
     .comment_list_content{
          padding-top: 4.4rem;
     }
     .comment_list_right{
          width: 31rem;
          margin-left: 1rem;
          .comment_tile_goods_info{
               display: flex;
               justify-content: space-between;
               align-items: center;
               width: 100%;
               >div{
                    h5{
                         font-size: 1.3rem;
                         letter-spacing: 1px;
                    }
                    span{
                         font-size: 1.2rem;
                         color: #9d9d9d;
                    }
               }
               img{
                    width: 1.4rem;height: 0.3rem
               }
          }
          >p{
               font-size: 1.4rem;
               margin-top: 1.3rem;
               letter-spacing: 1px;
          }
          .pictures_one{
               display: flex;
               justify-content: flex-start;
               margin-top: 1rem;
               >div{
                    width: 8.5rem;
                    height: 8.5rem;
                    display: inline-block;
                    overflow: hidden;
                    background-size: cover;
                    margin-right: 1rem;
                    border-radius: 0.5rem;
               }
               >div:last-child{
                    margin-right: 0;
               }
          }
          .comment_pictr{
               >span{
                    font-size: 1.4rem;
                    color: #f55056;
                    display: block;
                    margin-top: 2rem;
               }
               >p{
                    font-size: 1.4rem;
                    margin-top: 1.4rem;
               }
          }
     }
</style>

<template>
     <!--å•†å“è¯„ä»·åˆ—è¡¨-->
     <div class="commentList-content steep_padding">
          <div class="header">
               <HeaderPage>
                    <span slot="headerTitle">å•†å“è¯„ä»·</span>
               </HeaderPage>


          </div>
          <div class="comment_list_content">
               <ul v-if="false" class="topComment" style="width: 100%;height: 12.9rem;">
                    <li>å…¨éƒ¨(32)</li>
                    <li>è¿½è¯„(32)</li>
                    <li>è¯„ä»·(23)</li>
                    <li>å¥½è¯„(11)</li>
                    <li>æœ‰å›¾(11)</li>
                    <li>å·®è¯„(22)</li>
               </ul>
               <ul class="commentList">
                    <li @click="toCommentDetails(item.aid)" v-for="(item,index) in commentList" :key="index + 'skio'">
                         <div class="comment_list_usimg" :style="{backgroundImage:'url('+ item.headimgurl +')'}"></div>
                         <div class="comment_list_right">
                              <div class="comment_tile_goods_info">
                                   <div>
                                        <h5>{{ item.nickname }}</h5>
                                        <span>{{ item.create_time }}&nbspè§„æ ¼:{{ item.specs }}</span>
                                   </div>
                              </div>
                              <p>{{ item.content }}</p>
                              <div class="pictures_one" v-if="item.pictures.length != 0">
                                   <div
                                        v-for="(imgItem,imgIndex) in item.pictures"
                                        :key="imgIndex + 'img'"
                                        :style="{backgroundImage:'url('+ imgItem +')'}"
                                   >
                                   </div>
                              </div>
                              <!--è¿½è¯„-->
                              <div class="comment_pictr" v-if="item.is_add == 1">
                                   <span>ç”¨æˆ·{{ item.format_add_time }}è¿½è¯„</span>
                                   <p>{{ item.content2 }}</p>
                                   <div class="pictures_one" v-if="item.pictures.length != 0">
                                   <div
                                        v-for="(imgItem,imgIndex) in item.pictures2"
                                        :key="imgIndex + 'img'"
                                        :style="{backgroundImage:'url('+ imgItem +')'}"
                                   >
                                   </div>
                              </div>
                              </div>
                              <!--åº—å®¶å›žå¤-->
                              <div class="shopUserRemake" v-if="false">
                                   <div style="display: flex;align-items: center">
                                        <img src="@/common/image/publicImg/kefu.png" style="width: 2rem;height: 2rem;"/>
                                        <span style="margin-left: 1rem">æŽŒæŸœå›žå¤</span>
                                   </div>
                                   <p style="margin-top: 1.2rem">æ„Ÿè°¢æ‚¨çš„æ”¯æŒæ„Ÿè°¢æ‚¨çš„æ”¯æŒæ„Ÿè°¢æ‚¨çš„æ”¯æŒæ„Ÿè°¢æ‚¨çš„æ”¯æŒæ„Ÿè°¢æ‚¨çš„æ”¯æŒæ„Ÿè°¢æ‚¨çš„æ”¯æŒæ„Ÿè°¢æ‚¨çš„æ”¯æŒæ„Ÿè°¢æ‚¨çš„æ”¯æŒ</p>
                              </div>
                              <div style="margin-top: 1rem;color: #9d9d9d;font-size: 1.3rem;display: flex;justify-content: flex-end;align-items: center ">
                                   <div style="display: flex;justify-content: flex-end;">
                                        <div style="display: flex;align-items: center;margin-right: 2rem;">
                                             <img src="@/common/image/publicImg/comment.png" style="width: 1.6rem;height: 1.6rem;"/>
                                             <span style="margin-left: 0.5rem">å›žå¤</span>
                                        </div>
                                        <div style="display: flex;align-items: center;" @click.prevent.stop="clickGood(index)">
                                             <img v-if="item.is_star == 0" src="@/common/image/publicImg/zan.png" style="width: 1.7rem;height: 1.7rem;"/>
                                             <img v-if="item.is_star == 1" src="@/common/image/publicImg/redzan.png" style="width: 1.7rem;height: 1.7rem;"/>
                                             <span style="margin-left: 0.5rem;text-align: center;text-align: left">{{ item.star }}</span>
                                        </div>
                                   </div>
                              </div>
                         </div>
                    </li>
               </ul>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
     export default {
          name: "CommentList",
          data(){
               return{
                    page:1,
                    commentList:null,
               }
          },
          methods:{
               //åˆ°è¯„è®ºè¯¦æƒ…é¡µé¢
               toCommentDetails(aid){
                    this.$router.push({name:"commentdetails",query:{comment_id:aid}})
               },
               //ç‚¹èµžè¯„è®º
               clickGood(index){
                    this.postObj({comment_id:this.commentList[index].aid},window.PATHS.SHOPS_GOODS_COMMENT_STR,(result) => {
                    })
                    if (this.commentList[index].is_star == 1) {
                         if (this.commentList[index].star <= 999) {
                              this.commentList[index].star--
                         }
                         this.commentList[index].is_star = 0
                    }else {
                         if (this.commentList[index].star <= 999) {
                              this.commentList[index].star++
                         }
                         this.commentList[index].is_star = 1
                    }
               },
               getCommentList(){
                    var obj = {
                         page:this.page,
                         goods_id:this.$route.query.goods_id
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_COMMENT_GET_LIST,(result) => {
                         if (result.code == 1){
                              this.commentList = result.data
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               goBack(){
                    window.history.go(-1)
               },
          },
          created(){
               this.getCommentList()
          },
          activated(){
               if (this.$route.meta.keepLoadBool) {
                    this.commentList = null
                    this.getCommentList()
               }
          },
          mounted() {
          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to,from,next){
               if (to.name == "DetailsPage" || to.name == "shopgoods"){
                    from.meta.keepAlive = true
                    from.meta.keepLoadBool = true
               }
               if (to.name == "commentdetails") {
                    from.meta.keepAlive = true
                    from.meta.keepLoadBool = false
               }
               next()
          },
     }
</script>





