<style scoped lang="scss">
     .imgAlert {
          position: fixed;
          top: 0;
          background-color: #000;
          z-index: 101;
     }

     .footer > div {
          width: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
     }
     .footer {
          width: 100%;
          height: 5rem;
          position: fixed;
          bottom: 0;
          background-color: #ffffff;
          display: flex;
          justify-content: space-between;
          font-size: 1.4rem;
          border-top: solid 1px #eeeeee;
     }


     .van-divider--hairline {
          margin: 1rem 0;
     }

     .commentDetails-content {
          padding-bottom: 5rem;
          background-color: #f6f6f6;
          height: 100vh;
          position: relative;
          overflow-y: scroll;
          color: rgb(33, 33, 33);
     }

     .header {
          position: fixed;
          width: 100%;
          top: 0;
          left: 0;
          background-color: #ffffff;
          z-index: 100;
     }

     .same_div {
          padding: 15px 12px;
          background-color: #ffffff;
          margin-top: 7px;
     }

     .goods_info {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          margin-top: 0;

          .goods_img {
               background-size: cover;
               width: 7.5rem;
               height: 7.5rem;
               border-radius: 5px;
               flex-shrink: 0;
          }

          p {
               margin-left: 10px;
               font-size: 1.2rem;
               line-height: 2rem;
          }
     }

     .report {
          font-size: 1.3rem;
          text-align: right;
     }

     .user_comment {
          padding-top: 5px;

          .cooment_user {
               display: flex;
               align-items: flex-start;

               .cooment_user_img {
                    width: 3.5rem;
                    height: 3.5rem;
                    border-radius: 50%;
                    background-size: cover;
               }

               .fen {
                    margin-left: 10px;
                    padding-top: 0.6rem;

                    h4 {
                         font-size: 1.7rem;
                    }
               }
          }
     }
  .comment_img_list {
               display: flex;
               align-items: center;
               justify-content: flex-start;
               margin-top: 13px;

               .comment_img_list_item {
                    width: 11.2rem;
                    height: 11.2rem;
                    background-size: cover;
                    border-radius: 7px;
                    margin-right: 7px;
               }
          }
     .comment_content {
          margin-top: 8px;

          p {
               font-size: 1.4rem;

          }



          .goods_sacpe {
               margin-top: 15px;
               display: flex;
               align-items: center;
               justify-content: space-between;

               .sacpe {
                    color: #9d9d9d;
               }

               span {
                    font-size: 1.3rem;
               }

               .zhan_comment {
                    .zhiu_text {
                         color: #f55056;
                         margin-left: 20px;
                    }

                    img {
                         width: 1.1rem;
                         height: 1.2rem;
                    }
               }
          }
     }
     .reply_list{
          position: relative;
          margin-top: 2rem;
          .replay_head{
               width: 3.5rem;
               height: 3.5rem;
               background-size: cover;
               border-radius: 50%;
          }
          .replay_info_title{
               display: flex;
               align-items: center;
               justify-content: space-between;
               >div{
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    span{
                         margin-left: 1rem;
                         font-size: 1.4rem;
                    }
               }
               .replay_time{
                    font-size: 1rem;
               }
          }
          p{
               margin-top: 2rem;
               font-size: 1.4rem;
               padding-left: 4.5rem;
          }
          .replay_report{
               position: absolute;
               font-size: 1.3rem;
               right: 0;
               top: -2rem;
          }
     }
     .foot_infoIn{
          position: fixed;
          bottom: 0;
          width: 100%;
          background-color: #ffffff;
          padding: 1rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          box-shadow: 2px 0 10px 2px #ededed;
          z-index: 100;
          input{
               background-color: #f6f6f6;
               border: none;
               height: 3.4rem;
               width: 90%;
               border-radius:2rem;
               padding-left: 1.5rem;
               font-size: 1.4rem;
               box-shadow: none;
               -webkit-appearance: none;
               margin-right: 1rem;
          }
          .submit_btn{
               font-size: 1.7rem;
               line-height: 3.4rem;
               background-color: #f55056;
               border-radius: 1.7rem;
               width: 7.15rem;
               text-align: center;
               color: #ffffff;
               flex-shrink: 0;
          }
     }
     .addComment_content{
          .addComment_content_title{
               display: flex;
               align-items: center;
               justify-content: space-between;
               h5{
                    font-size: 1.4rem;
                    font-weight: 600;
               }
               span{
                    font-size: 1rem;
               }
          }
          p{
               font-size: 1.4rem;
               margin-top: 4rem;
          }
     }
     .score_comment{
          display: flex;
          align-items: center;
     }
     .comment_wuliu{
          font-size: 1.3rem;
          position: relative;
          top: 0.6rem;
          margin-left: 1rem;
          color: #f55056;
     }
     .comment_detail_content{
          padding-top: 4.4rem;
     }
</style>

<template>
     <!--è¯„è®ºè¯¦æƒ…-->
     <div ref="commentDetail" class="commentDetails-content steep_padding" v-if="commentInfo != null" @scroll="scroll">
          <!--<div class="header">-->
          <!--<div style="width: 3rem;" @click="goBack">-->
          <!--<img src="@/common/image/publicImg/nav_ico_Return_black.png" style="width: 1rem;height: 1.8rem;"/>-->
          <!--</div>-->
          <!--<span style="position: absolute;left: 50%;transform: translateX(-50%);font-size: 1.5rem">è¯„è®ºè¯¦æƒ…</span>-->
          <!--</div>-->
          <div class="header">
               <HeaderPage>
                    <span slot="headerTitle">è¯„è®ºè¯¦æƒ…</span>
               </HeaderPage>
          </div>
          <div class="comment_detail_content">
               <div class="goods_info same_div">
                    <div class="goods_img" :style="{backgroundImage:'url('+ commentInfo.thumb +')'}"></div>
                    <p>{{ commentInfo.title }}</p>
               </div>
               <div class="user_comment same_div">
                    <div class="report">
                         <span @click="toReport('goods_comment',commentInfo.aid)">ä¸¾æŠ¥</span>
                    </div>
                    <div class="cooment_user">
                         <div class="cooment_user_img" :style="{backgroundImage: 'url('+ commentInfo.headimgurl +')'}"></div>
                         <div class="fen">
                              <h4>{{ commentInfo.nickname }}</h4>
                              <div class="score_comment">
                                   <van-rate
                                           v-model="value"
                                           allow-half
                                           void-icon="star"
                                           void-color="#eee"
                                           size="1.7rem"
                                           style="margin-top: 10px"
                                           readonly
                                           color="#f55056"
                                   />
                                   <span v-if="commentInfo.logistics_score <= 0 || commentInfo.service_score <= 0 || commentInfo.goods_score <= 0" @click.stop="commentscore" class="comment_wuliu">è¯„ä»·ç‰©æµ</span>
                              </div>
                         </div>
                    </div>
                    <div class="comment_content">
                         <p>{{ commentInfo.content }}</p>
                         <div class="comment_img_list">
                              <div @click="lookImg(index)" v-for="(item,index) in commentInfo.pictures" class="comment_img_list_item"
                                   :style="{ backgroundImage:'url('+ item +')'}" :key="item.aid"></div>
                         </div>
                         <div class="goods_sacpe">
                              <span class="sacpe">{{ commentInfo.specs }}</span>
                              <div class="zhan_comment">
                              <span v-if="user.nickname == commentInfo.nickname && commentInfo.content2 == ''" @click="toAddComment"
                                    class="zhiu_text">æ·»åŠ è¿½è¯„</span>
                              </div>
                         </div>
                    </div>
               </div>
               <div class="addComment_content same_div" v-if="commentInfo.is_add == 1">
                    <div class="addComment_content_title">
                         <h5>è¿½è¯„å†…å®¹</h5>
                         <span>{{ commentInfo.format_add_time }}</span>
                    </div>
                    <p>{{ commentInfo.content2 }}</p>
                    <div class="comment_img_list">
                              <div @click="lookImg(index)" v-for="(item,index) in commentInfo.pictures2" class="comment_img_list_item"
                                   :style="{ backgroundImage:'url('+ item +')'}" :key="item.aid"></div>
                         </div>
               </div>
               <!--å…¨éƒ¨å›žå¤-->
               <div class="same_div" style="padding-bottom: 10rem">
                    <div>
                         <span style="font-size: 1.3rem">å›žå¤({{ commentInfo.replay }})</span>
                    </div>
                    <div style="min-height: 70vh;position: relative">
                         <div v-if="replayList.length == 0" style="position: absolute;top: 50%;left:50%;transform: translate(-50%,-50%);font-size: 1.3rem;color: rgb(157,157,157);">æ²™å‘è¿˜ç©ºç€ï¼Œå¿«æ¥æŠ¢æ²™å‘~</div>
                         <ul v-if="replayList.length != 0">
                              <li class="reply_list" v-for="(item,index) in replayList" :key="index + 'repins'">
                                   <div class="replay_info_title">
                                        <div>
                                             <div class="replay_head" :style="{backgroundImage:'url('+ item.headimgurl +')'}"></div>
                                             <span class="replay_name">{{ item.nickname }}</span>
                                        </div>
                                        <span class="replay_time">{{ item.create_time }}</span>
                                   </div>
                                   <p>{{ item.content }}</p>
                                   <span @click="toReport('goods_comment_replay',item.aid)" class="replay_report">ä¸¾æŠ¥</span>
                              </li>
                         </ul>
                    </div>
               </div>
               <!--åº•éƒ¨è¾“å…¥æ¡†åŠ¨ç”»-->
               <div class="foot_infoIn">
                    <input id="myInput" v-model="inpValue" type="text" placeholder="è¯´ç‚¹ä»€ä¹ˆå§~">
                    <span @click="sendMes" class="submit_btn">å‘å¸ƒ</span>
               </div>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
     import Vue from 'vue';
     import {ImagePreview} from 'vant';

     Vue.use(ImagePreview);

     export default {

          name: "CommentDetails",
          data() {
               return {
                    commentInfo: null,
                    page: 1,
                    inpBool: false,
                    inpValue: "",
                    replayList: [],
                    value: 3,
                    user: null,
                    scrollMoreBool:true,
                    moreBool:true
               }
          },
          methods: {
               //å›¾ç‰‡é¢„è§ˆ
               lookImg(index){
                    this.pictureLook(this.commentInfo.pictures,index)
               },
               toReport(type,article_id){
                    this.$router.push({
                         name:"commentreport",
                         query:{
                              type,
                              article_id
                         }
                    })
               },
               //åˆ°è¯„ä»·ç‰©æµ
               commentscore(){
                    var info = this.commentInfo
                    info.comment_id = info.aid
                    this.$router.push({name:"commentscore",params:{info}})
               },
               //è¿½è¯„
               toAddComment() {
                    this.commentInfo.comment_id = this.commentInfo.aid
                    this.$router.push({
                         name: 'commenteeild',
                         params: {comInfo: this.commentInfo, order_no: this.commentInfo.order_no, bool: true}
                    })
               },
               //ç‚¹å‡»å›¾ç‰‡
               toImgLook(imgArr, imgIndex) {
                    this.$store.commit("setImgArr", {imgArr, imgIndex})
                    location.href = "#/imglook"
               },
               //ç‚¹å‡»å‘é€
               sendMes() {
                    if (this.inpValue == "") {
                         this.$toast.center("è¯·è¾“å…¥æ‚¨è¦è¯„è®ºçš„å†…å®¹")
                    } else {
                         var obj = {
                              comment_id: this.commentInfo.aid,
                              content: this.inpValue
                         }
                         this.postObj(obj, window.PATHS.SHOPS_GOODS_COMMENT_REPLAY, (result) => {
                              if (result.code == 1) {
                                   this.$toast.center(result.info)
                                   this.inpValue = ""
                                   this.page = 1
                                   this.replayList = []
                                   this.getReplayList()
                              } else {
                                   this.$toast.center(result.info)
                                   this.inpValue = ""
                              }
                         })
                    }
               },
               //     ç‚¹èµž
               goodComment() {
                    this.postObj({comment_id: this.commentInfo.aid}, window.PATHS.SHOPS_GOODS_COMMENT_STR, (result) => {
                         if (result.code == 0) {
                              this.$toast.center(result.info)
                         }
                    })
                    if (this.commentInfo.is_star == 1) {
                         this.commentInfo.is_star = 0
                    } else {
                         this.commentInfo.is_star = 1
                    }
               },
               //     è¿”å›žä¸Šä¸€é¡µ
               goBack() {
                    window.history.go(-1)
               },
               //   é€šè¿‡è¯„è®ºidèŽ·å–è¯„è®ºä¿¡æ¯
               getCommentInfo() {
                    this.postObj({comment_id: this.$route.query.comment_id}, window.PATHS.SHOPS_GOODS_COMMENT_GET_INFO, (result) => {
                         if (result.code == 1) {
                              console.log(result)
                              this.user = result.user
                              console.log(this.user,"æˆ‘çš„ç”¨æˆ·ä¿¡æ¯")
                              this.commentInfo = result.data
                              this.getReplayList()
                              console.log(this.commentInfo)
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //é€šè¿‡è¯„è®ºidèŽ·å–è¯„è®ºå›žå¤åˆ—è¡¨
               getReplayList() {
                    var that = this
                    var obj = {
                         comment_id: this.commentInfo.aid,
                         page: this.page
                    }
                    this.postObj(obj, window.PATHS.SHOPS_GODS_COMMENT_REPLAY_LIST, (result) => {
                         if (result.code == 1) {
                              if (result.data.length != 0){
                                   this.replayList = this.replayList.concat(result.data)
                                   console.log(this.replayList)
                                   this.moreBool = true
                              }else{
                                   this.moreBool = false
                              }
                              setTimeout(() => {
                                   that.scrollMoreBool = true
                              },1000)
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               scroll(){
                    var windowHeight = window.innerHeight
                    var scrollTop = this.$refs.commentDetail.scrollTop
                    var height = this.$refs.commentDetail.scrollHeight
                    this.scrollMore(scrollTop,height,windowHeight,() => {
                         if (this.scrollMoreBool){
                              this.scrollMoreBool = false
                              if (this.moreBool){
                                   this.page++
                                   this.getReplayList()
                              }
                         }
                    })
               },
          },
          created(){
               this.getCommentInfo()
          },
          mounted() {

          },
          components: {
               HeaderPage
          }

     }
</script>





