<template>
     <!--ä¹°å®¶ç§€æŸ¥çœ‹-->
     <div id="BuyShow" :style="{backgroundImage:'url('+ commentInfo.pictures[0] +')'}">
          <div class="header">
               <div class="comment_header_left">
                    <div class="comment_userIMg" :style="{backgroundImage: 'url('+ commentInfo.headimgurl +')'}"></div>
                    <span>å°æ©™å­</span>
               </div>
               <span class="swiper_index">{{ index }}/{{ commentInfo.pictures.length + 1 }}</span>
               <img @click="this.$parent.closeBuyShow" src="@/common/image/shopImg/clearbuyShow.png" alt="">
          </div>
          <div class="BuyShow_content">
               <van-image-preview
                       v-model="show"
                       :images="images"
                       @change="onChange"
                       :async-close="true"
                       :show-index="false"
               >
               </van-image-preview>
          </div>
          <div class="comment_detal_footer">
               <h5>å…¥æ‰‹åŽè¿«ä¸åŠå¾…åˆ†äº«</h5>
               <p>{{ commentInfo.content }}</p>
               <div class="cooment_goods">
                    <div>
                         <div class="goods_info" @click="toDetail">
                              <div class="goods_img" :style="{backgroundImage:'url('+ commentInfo.thumb +')'}">
                                   <span class="comment_goodsprice">ï¿¥{{ commentInfo.goods_price }}</span>
                              </div>
                              <p class="comment_good_title">{{ commentInfo.title }}</p>
                         </div>
                         <div @click="likeComment" class="like_comment">
                              <img v-if="commentInfo.is_star == 1" src="@/common/image/publicImg/redzan.png" alt="">
                              <img v-if="commentInfo.is_star == 0" src="@/common/image/publicImg/zan.png" alt="">
                         </div>
                    </div>
               </div>
          </div>
     </div>
</template>

<script>
     export default {
          name: "BuyShow",
          data(){
               return {
                    commentInfo:null,
                    show: true,
                    index: 1,
                    images: []
               }
          },
          methods:{
               //åˆ°å•†å“è¯¦æƒ…
               toDetail(){
                    this.$router.push({name: 'DetailsPage', query: {comId: this.commentInfo.goods_id}})
               },
               //ç‚¹èµž
               likeComment(){
                    var obj = {
                         comment_id:this.commentInfo.aid
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_COMMENT_STR,(result) => {
                         if (result.code == 1){
                              this.commentInfo.is_star = this.commentInfo.is_star == 1? 0 : 1
                         }
                    })
               },
               onChange(index) {
                    this.index = index + 1;
               }
          },
          mounted() {
          },
          created() {
                    this.commentInfo = this.$store.state.shopBuyShow
                    this.images = this.commentInfo.pictures
                    console.log( this.commentInfo)
          }
     }
</script>

<style scoped>
     .like_comment img{
          width: 2.4rem;
          height: 2.5rem;
     }
     .cooment_goods > div{
          display: flex;
          align-items: center;
          justify-content: space-between;
     }
     .goods_info{
          display: flex;
          align-items: center;
          justify-content: flex-start;
     }
     .comment_good_title{
          color: #ffffff;
          width: 15rem;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
          font-size: 1.4rem;
          margin-left: 10px;

     }
     .comment_goodsprice{
          font-size: 1rem;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: #f55056;
          width: 100%;
          position: absolute;
          bottom: 0;
          color: #ffffff;
     }
     .goods_img{
          width: 5.5rem;
          height: 5.5rem;
          background-size: cover;
          overflow: hidden;
          border-radius: 5px;
          position: relative;
     }
     .cooment_goods{
          margin-top: 15px;
     }
     .comment_detal_footer > p{
          margin-top: 15px;
          color: #ffffff;
          font-size: 14px;
     }
     .comment_detal_footer h5{
          display: inline;
          padding: 4px 21.5px;
          background-color: #ffffff;
          border-radius: 20px;
          font-size: 12px;
     }
     .comment_detal_footer{
          position: absolute;
          bottom: 0;
          width: 100%;
          padding: 18px 12px;
          background-color: rgba(0, 0, 0, 0.11);
          z-index: 11;
     }
     .swiper_index{
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
     }
     .header{
          z-index: 11;
          background-color: rgba(0, 0, 0, 0.52);
          width: 100%;
          position: relative;
     }
     .comment_header_left span{
          font-size: 1.4rem;
          margin-left: 10px;
     }
     .comment_header_left{
          display: flex;
          align-items: center;
          position: relative;
     }
     .comment_userIMg{
          width: 3rem;
          height: 3rem;
          border-radius: 50%;
          background-size: cover;
          background-repeat: no-repeat;
          display: flex;
          flex-shrink: 0;
     }
     .header > img{
          width: 1.6rem;
          height: 1.6rem;
     }
     .header{
          position: absolute;
          color: #ffffff;
          font-size: 1.6rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
          padding: 11px;
     }
     #BuyShow >>> .van-image-preview__overlay{
          background-color: transparent !important;
     }
     .BuyShow_content{
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.81);
          position: relative;
          z-index: 10;
     }
#BuyShow{
     width: 100vw;
     height: 100vh;
     position: fixed;
     top: 0;
     left: 0;
     z-index: 100;
     background-size: cover;
     background-repeat: no-repeat;
}
</style>



