
<style scoped lang="scss">
     .comment_shop{
          background-color: #ffffff;
          padding: 8px 12px;
          padding-bottom: 35vh;
     }
     .fenList{
          font-size: 1.3rem;
          display: flex;
          align-items: center;
          margin-top: 1.5rem;
          >span{
               margin-right: 10px;
               font-size: 1.3rem;
          }
     }
     .header{
          position: relative;
          box-shadow: 2px 0 10px 0 #e3e3e3;
          .over_btn{
               position: absolute;
               font-size: 1.4rem;
               right: 1.2rem;
               top: 50%;
               transform: translateY(-50%);
          }
     }

</style>
<template>
     <!--è¯„ä»·ç‰©æµ-->
     <div id="comment_score" v-if="info != null">
          <div class="header">
               <HeaderPage>
                    <span slot="headerTitle">è¯„ä»·ç‰©æµ</span>
                    <span slot="headerRight" class="over_btn" @click="submitBtn">å®Œæˆ</span>
               </HeaderPage>
          </div>
          <div class="comment_shop">
               <div class="fenList">
                    <span>æœåŠ¡è¯„åˆ†</span>
                    <van-rate
                            v-model="service_score"
                            allow-half
                            void-icon="star"
                            void-color="#eee"
                            color="#f55056"
                            size="2rem"
                            :readonly="service_score == 0? false : true"
                    />
               </div>
               <div class="fenList">
                    <span>ç‰©æµè¯„åˆ†</span>
                    <van-rate
                            v-model="logistics_score"
                            allow-half
                            void-icon="star"
                            void-color="#eee"
                            color="#f55056"
                            size="2rem"
                            :readonly="info.logistics_score == 0? false : true"
                    />
               </div>
               <div class="fenList">
                    <span>è´¨é‡è¯„åˆ†</span>
                    <van-rate
                            v-model="goods_score"
                            allow-half
                            void-icon="star"
                            void-color="#eee"
                            color="#f55056"
                            size="2rem"
                            :readonly="info.goods_score == 0? false : true"
                    />
               </div>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"
     import storage from '@/common/js/storage.js'
     export default {
          name: "CommentScore",
          data(){
               return {
                    service_score:0,
                    logistics_score:0,
                    goods_score:0,
                    info:null
               }
          },
          methods:{
          //     æäº¤
               submitBtn(){
                    var obj  = {
                         comment_id:this.info.comment_id,
                    }
                    if (this.info.goods_score == 0){
                         obj.goods_score = this.goods_score * 2
                    }
                    if (this.info.service_score == 0){
                         obj.service_score = this.service_score * 2
                    }
                    if (this.info.logistics_score == 0){
                         obj.logistics_score = this.logistics_score * 2
                    }
                    this.postObj(obj,window.PATHS.SHOPSGOODSCOMMENT_SETSCORE,(result) => {
                         if (result.code == 1){
                              this.$toast.center("æäº¤æˆåŠŸ")
                              //æ¸…é™¤è¯„è®ºåˆ—è¡¨æ•°æ®ï¼Œä½¿å…¶åˆ·æ–°
                              storage.remove("commentindexInfo")
                              setTimeout(() => {
                                   window.history.go(-1)
                              },500)
                         }
                    })
               },
          },
          created(){
               this.info = this.$route.params.info
               this.service_score = this.info.service_score
               this.logistics_score = this.info.logistics_score
               this.goods_score = this.info.goods_score
               console.log(this.$route.params)
          },
          components:{
               HeaderPage
          }
     }
</script>





