function http_build_query(dict) {
     var dict2 = '',
         keys = Object.keys(dict).sort();

     for (var i = 0, n = keys.length, key; i < n; ++i) {
          key = keys[i];
          if (dict2) {
               dict2 = dict2 + '&' + key + '=' + dict[key];
          } else {
               dict2 = key + '=' + dict[key];
          }
     }
     return dict2
}
// èŽ·å–url
Vue.prototype.getUrl = function () {
     var paths = window.location.href;
     var newPath = paths.split('?')[0];
     var pathQuery = this.$route.query
     pathQuery.token = null;
     delete pathQuery.token;
     if (pathQuery) {
          var query = http_build_query(pathQuery)
          var newUrl = newPath += '?' + query
     } else {
          var newUrl = newPath
     }
     newUrl = newUrl.replace(/\?$/g, "")
     return newUrl
}
//èŽ·å–sign
function getSignString(dict) {
     var dict2 = '',
         keys = Object.keys(dict).sort();
     for (var i = 0, n = keys.length, key; i < n; ++i) {
          key = keys[i];
          var aljk = dict[key];
          if (typeof aljk != "object" && typeof aljk != "undefined") {
               if (dict2) {
                    dict2 = dict2 + '&' + key + '=' + dict[key];
               } else {
                    dict2 = key + '=' + dict[key];
               }
          }
     }
     return dict2
}
// èŽ·å–æ•°æ®--post
Vue.prototype.postObj = function (dict, path, callback) {
         //AppId
         dict.AppId = APP_ID
         //token
         if (window.token) {
              dict.UserToken = window.token
         }
         if (this.$route.query.token) {
              dict.UserToken = this.$route.query.token
         }
         dict.SignatureNonce = crypto.MD5(Math.random()).toString()
         //äº‹æ—¶é—´æˆ³
         dict.Timestamp = Math.round(new Date() / 1000)
         var dict2 = getSignString(dict)
         var sign = crypto.HmacSHA1(dict2, APP_SECRET).toString(crypto.enc.Base64);
         dict.Signature = sign

         this.axios.post(APP_URL + path, dict).then((result) => {
               this.$loading.close()
              var data = result.data
              if (data.code == '-201' || data.code == '-202' || data.code == '-203') {
                   location.href = 'App://login/' + this.getUrl()
                   return
              }
              if (data.code < 0) {
                   this.$toast.center(data.info)
                   return
              }
              if (data.code == 0) {
                   this.$toast.center(data.info)
                   return
              }
              callback(data)
         }).catch((err) => {
              this.$toast.center('ç½‘ç»œå¼‚å¸¸')
              console.log(err)
         })
    };

