//è½¬æ¢base64å­—ç¬¦ä¸²
Vue.prototype.getBaseStr = function (data) {
     var newObj = crypto.enc.Base64.stringify(crypto.enc.Utf8.parse(data));
     newObj = newObj.replace(/\+/g, "-")
     newObj = newObj.replace(/\=/g, "")
     newObj = newObj.replace(/\//g, "_")
     return newObj
},

//base64è§£å¯†
    Vue.prototype.decodeBase = function (data) {
         var item = data
         item = item.replace(/\-/g, "+")
         item = item.replace(/\_/g, "/")
         item = item.replace(/\=/g, "")
         return JSON.parse(Base64.Base64.decode(item))
    }

//ä¸Šä¼ æ–‡ä»¶
Vue.prototype.postFile = function (file, callback) {
     var dict = {}
     //AppId
     dict.AppId = APP_ID
     //ç”Ÿæˆéšæœºæ•°
     //token
     // if (window.token) {
     // dict.UserToken = window.token
     // }
     dict.SignatureNonce = crypto.MD5(Math.random()).toString()
     //äº‹æ—¶é—´æˆ³
     dict.Timestamp = Math.round(new Date() / 1000)
     var dict2 = getSignString(dict)
     var sign = crypto.HmacSHA1(dict2, APP_SECRET).toString(crypto.enc.Base64);
     dict.Signature = sign
     let data = new FormData()
     data.append('AppId', APP_ID)
     data.append('SignatureNonce', dict.SignatureNonce)
     data.append('Timestamp', dict.Timestamp)
     data.append('Signature', dict.Signature)
     data.append('file', file)
     this.axios.post(APP_URL + "5d57dc30d76fe", data).then((result) => {
          var data = result.data
          if (data.code < 0) {
               this.$toast.center(data.info)
               return
          }

          callback(data)
     }).catch((err) => {
          // if (err.code == '500') {
          //
          // }
          console.log(err)
     })
};

