//å°†æ—¶é—´æˆ³è½¬æ¢æˆåˆ†ç§’
Vue.prototype.changeTime = function (value) {
     var min = Math.floor(value%3600);
     var time = Math.floor(value/3600) + ":" + (Math.floor(min/60) < 10? '0'+Math.floor(min/60):Math.floor(min/60)) + ":"+ (value % 60 < 10? '0' + value % 60 : value % 60);
     return time;
}

//å°†æ—¶é—´è½¬æ¢æˆå¤©æ—¶åˆ†ç§’
Vue.prototype.changeDay = function(value){
     let result = parseInt(value)
     let d = Math.floor(result / 86400)
     let h = Math.floor(((result % 86400) / 3600)) < 10 ? '0' + Math.floor(((result % 86400) / 3600)) : Math.floor(((result % 86400) / 3600))
     let m = Math.floor(((result % 86400) % 3600) / 60) < 10 ? '0' + Math.floor(((result % 86400) % 3600) / 60) : Math.floor(((result % 86400) % 3600) / 60)
     let s = Math.floor(((result % 86400) % 3600) % 60) < 10 ? '0' + Math.floor((result % 60)) : Math.floor((result % 60))
     result = `${d}å¤©${h}:${m}:${s}`
     if(d == 0){
          result = `${h}:${m}:${s}`
     }
     return result
}

