                         }
                    })
               },
               toPlace() {
                    location.href = "#/place"
               },
               // //ç¡®è®¤æ”¯ä»˜
               comShore() {
                    if (this.disableBtn) {
                         //èŽ·å–å‘ç¥¨å†…å®¹
                         if (this.$store.state.bill != null) {
                              var data = this.$store.state.bill
                              var invoice = {
                                   name: data.name,
                                   taxpayer_id: data.taxpayer_id,
                                   address: this.address.user_address,
                                   phone: "",
                                   bank_name: "",
                                   bank_no: "",
                                   mobile: data.mobile,
                              }
                              this.billInfo = invoice
                         }

                         var skuPath = ''
                         var shopOrder = {
                              goods: []
                         }
                         //å®šåˆ¶
                         if (this.customization) {
                              shopOrder.goods.push({
                                   goods_id: this.customization.goods_id,
                                   sku_path: this.customization.sku_path,
                                   number: this.customization.number,
                              })
                         } else {
                              shopOrder.goods.push({
                                   goods_id: this.skuCom.goods_id,
                                   sku_path: this.skuCom.sku_path,
                                   number: this.skuCom.number,
                                   is_original: this.skuCom.is_original,
                                   period_id: this.skuCom.period_id ? this.skuCom.period_id : 0,
                                   share_user_id: this.skuCom.share_user_id,
                                   video_id: this.skuCom.video_id
                              })
                         }

                         shopOrder.receiver_name = this.addressList[0].user_name
                         shopOrder.receiver_phone = this.addressList[0].user_phone
                         shopOrder.receiver_address = this.addressList[0].user_address
                         shopOrder.remark = this.remark
                         shopOrder.shop_id = this.comInfo.shop_id
                         if (this.customization != null) {
                              shopOrder.is_customized = 1
                              shopOrder.remark = this.customization.remark
                         }
                         if (this.$store.state.billChooseBool) {
                              shopOrder.invoice = this.billInfo
                         }
                         console.log(shopOrder)
                         //æäº¤è®¢å•ä¿¡æ¯
                         this.postObj(shopOrder, window.PATHS.SHOPS_OREDER_ADD_ORDER, (data) => {
                              if (data.code == 1) {
                                   this.$store.commit('changeBill', null)
                                   this.$store.commit('billBool', false)
                                   this.orderNo = data.data.order_no
                                   this.disableBtn = false
                                   if (data.data.status == 1) {
                                        this.success()
                                   } else {
                                        location.href = "app://shoporder/pay/" + data.data.order_no + '/' + data.data.price
                                        var that = this
                                        setTimeout(function () {
                                             that.showAlert2 = true
                                        }, 500)
                                   }
                              } else {
                                   this.$toast.center(data.info)
                              }
                         })
                    }
               },
               success() {
                    this.showAlert = true
               },
               tobillpage() {
                    location.href = '#/bill'
               },
               toAddPlace() {
                    location.href = '#/newplace'
               },
               add() {
                    if (this.customization) {
                         console.log(this.customization.number)
                         this.customization.number++
                         return
                    }
                    if (this.comInfo.extra_table == "goods_group") {
                         return
                    }
                    if (this.skuCom.number >= this.skuCom.stock) {
                         return
                    } else {
                         this.skuCom.number++
                    }
               },
               jian() {
                    if (this.customization != null) {
                         if (this.customization.number <= 1) {
                              return
                         } else {
                              this.customization.number--
                         }
                    } else {
                         if (this.skuCom.number <= 1) {
                              return
                         } else {
                              this.skuCom.number--
                         }
                    }
               }
