     export default {
          name: "EntcomPage",
          data() {
               return {
                    // èŠ±å‘—åˆ†æœŸçš„å¼€å…³
                    checkedQi: false,
                    areaList: area,
                    cityShow: false,
                    //ä¼šå‘˜ç‰¹æ•ˆå¼€å…³
                    memberPremium: false,
                    //çœ‹ä¸åˆ°ä»·æ ¼çš„å¼€å…³
                    nonePrice: false,
                    showAlert2: false,
                    checked: false,
                    comInfo: null,
                    //å¤‡æ³¨
                    remark: "",
                    //skuä¿¡æ¯
                    addressList: [],
                    address: "",
                    disableBtn: true,
                    timeNum: 0,
                    orderNo: '',
                    skuCom: null,
                    //é—¨åº—ä¿¡æ¯
                    storeInfo: {},
                    billInfo: {},
                    //å®šåˆ¶çš„ä¿¡æ¯
                    customization: null,
                    remarkBoole: false,
                    //æ”¶è´§äººæ‰‹æœº
                    userPhone: null,
                    //æ”¶è´§äººè¯¦ç»†åœ°å€
                    addressDetail: null,
                    //çœå¸‚åŒº
                    addressCity: "",
                    //æ”¶è´§äººå§“å
