<style scoped lang="scss">
     #bill_detail{
          height: 100vh;
          overflow-y: scroll;
          background-color: #f6f6f6;
     }
.bill_detail_header{
     position: fixed;
     top: 0;
     left: 0;
     width: 100%;
     background-color: #ffffff;
     box-shadow: 0 2px 7px 0 #e1e1e1;
}
.bill_detail_content{
     padding: 0 1.2rem;
     padding-top: 5.3rem;
     font-size: 1.3rem;
}
     .same_idv{
          border-radius: 0.7rem;
          margin-bottom: 0.7rem;
          background-color: #ffffff;
          padding: 1.65rem 1.1rem;
     }
     .bill_detail__chirld{
          display: flex;
          align-items: center;
          justify-content: space-between;
     }
     .bill_status_right{
          display: flex;
          align-items: center;
          img{
               width: 0.6rem;
               margin-left: 0.5rem;
               height: 1.1rem;
          }
     }
     .order_info{
          >div:last-child{
               margin-bottom: 0;
          }
          >div{
               margin-bottom: 1.4rem;
          }

     }
     .footer{
          padding: 1rem 1.2rem;
          background-color: #ffffff;
          width: 100%;
          position: fixed;
          bottom: 0;
          text-align: right;
          span{
               font-size: 1.5rem;
               display: inline-flex;
               align-items: center;
               justify-content: center;
               width: 8.5rem;
               height: 2.9rem;
               color: #9d9d9d;
               border: solid 1px #9d9d9d;
               border-radius: 1.45rem;
               margin-left: 1.5rem;
          }
     }
     .emil_inp{
          width: 90%;
          display: block;
          margin: 1.6rem auto;
          height: 4rem;
          border-radius: 0.5rem;
          border: none;
          background-color: #f6f6f6;
          text-align: center;
     }
     #bill_detail >>> .van-dialog__cancel{
