     }
</style>

<template>
     <!--å‘ç¥¨è¯¦æƒ…-->
     <div id="bill_detail" class="steep_padding">
          <div class="bill_detail_header">
               <HeaderPage>
                    <span slot="headerTitle">å‘ç¥¨è¯¦æƒ…</span>
               </HeaderPage>
          </div>
          <div class="bill_detail_content">
               <div  class="same_idv bill_detail__chirld">
                    <span>å‘ç¥¨çŠ¶æ€</span>
                    <div @click="toBillPage" v-if="!billInfo" class="bill_status_right">
                         <span>
                              <span>æœªå¼€ç¥¨</span>
                              <span style="color: #f55056">å¼€å…·å‘ç¥¨</span>
                         </span>
                         <img src="@/common/image/publicImg/rightRed.png" alt="">
                    </div>
                    <span v-if="billInfo && billInfo.status == 0">ç­‰å¾…å•†å®¶å¼€ç¥¨</span>
                    <span v-if="billInfo && billInfo.status == 1">å·²å¼€ç¥¨</span>
               </div>
               <div class="same_idv order_info">
                   <div class="bill_detail__chirld">
                        <span>è®¢å•çŠ¶æ€</span>
                        <span>å·²å®Œæˆ</span>
                   </div>
                    <div class="bill_detail__chirld">
                         <span>è®¢å•ç¼–å·</span>
                         <span>{{ orderInfo.order_no }}</span>
                    </div>
                    <div class="bill_detail__chirld">
                         <span>ä¸‹å•æ—¶é—´</span>
                         <span>{{ orderInfo.create_time }}</span>
                    </div>
               </div>
               <div v-if="billInfo && billInfo.status >= 0" class="same_idv order_info">
                    <div class="bill_detail__chirld">
                         <span>å‘ç¥¨ç±»åž‹</span>
                         <span>ç”µå­å‘ç¥¨</span>
                    </div>
                    <div class="bill_detail__chirld">
                         <span>å‘ç¥¨å†…å®¹</span>
                         <span>å•†å“æ˜Žç»†</span>
                    </div>
                    <div class="bill_detail__chirld">
                         <span>å‘ç¥¨æŠ¬å¤´</span>
                         <span>{{ billInfo.name }}</span>
                    </div>
               </div>
          </div>
          <div v-if="billInfo && billInfo.status == 1" class="footer">
               <span @click="lokBill">æŸ¥çœ‹å‘ç¥¨</span>
               <span @click="showEmil = true">å‘é€é‚®ç®±</span>
          </div>
          <van-dialog
                  v-model="showEmil"
                  title="å‘é€è‡³"
                  show-cancel-button
                  confirmButtonColor="#9d9d9d"
                  cancelButtonColor="#9d9d9d"
                  @confirm="confirm"
                  :beforeClose="beforeClose"
          >
               <input class="emil_inp" v-model="emailVal" type="email">
          </van-dialog>
     </div>
</template>

<script>
     import { Dialog } from 'vant';
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "BillDetail",
          data(){
               return{
                    showEmil:false,
                    orderInfo:null,
                    billInfo:null,
                    emailVal:""
               }
          },
          methods:{
               beforeClose(action, done){
                    if (action === "confirm") {
                         done(false)
                    }else{
                         done()
                    }
               },
               confirm(){
                    if (this.emailVal != ""){
                         var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
                         if (reg.test(this.emailVal)) {
                              var obj = {
                                   order_no:this.orderInfo.order_no,
                                   email:this.emailVal
                              }
                              this.postObj(obj,window.PATHS.BILL.ORDER_INVOICEEMAIL,(result) => {
                                   if (result.code == 1){
                                        this.$toast.center("å‘ç¥¨å·²å‘é€è‡³æ‚¨çš„é‚®ç®±ï¼Œè¯·æ³¨æ„æŸ¥æ”¶")
                                        setTimeout(() => {
                                             this.showEmil = false
                                             this.emailVal = ""
                                        },1500)
                                   }
                              })
                              console.log('ä¸Šä¼ ');
                         }else{
                              this.$toast.center('è¯·è¾“å…¥æ­£ç¡®çš„é‚®ç®±æ ¼å¼')
                         }
                    } else{
                         this.$toast.center("è¯·è¾“å…¥æ‚¨çš„é‚®ç®±åœ°å€")
                    }
               },
          //     æŸ¥çœ‹å‘ç¥¨
               lokBill(){
                    location.href = "app://view/" + this.billInfo.fileid
               },
          //     å¼€å…·å‘ç¥¨
               toBillPage(){
                    this.$router.push({
                         name:"bill",
                         params:{
                              orderInfo:this.orderInfo
                         }
                    })
               },
          //     èŽ·å–å‘ç¥¨è¯¦æƒ…
               getBillDetail(){
                    var obj = {
                         order_no:this.orderInfo.order_no
                    }
                    this.postObj(obj,window.PATHS.BILL.ORDER_GETINVOICEORDER,(result) => {
                         if (result.code == 1){
                              this.billInfo = result.data
                              console.log(this.billInfo,"å‘ç¥¨ä¿¡æ¯")
                         }
                    })
               },
          },
          created(){
               this.orderInfo = this.$store.state.orderBillInfo
               this.getBillDetail()
          },
          components:{
               HeaderPage
          }
     }
</script>





