

<style scoped lang="scss">
     ul li{
          padding: 1rem;
          border-top: solid 1px #f6f6f6;
     }
     .askevent-footer > span{
          width: 6.7rem;
          line-height: 2.9rem;
          text-align: center;
          background-color: #f55056;
          font-size: 1.7rem;
          color: white;
          border-radius: 2rem;
     }
     .askevent-footer > input{
          border: none;
          background-color: #f6f6f6;
          padding: 1rem 0;
          padding-left: 1.5rem;
          border-radius: 2rem;
          flex-grow: 1;
          margin-right: 1rem;
          height: 2.9rem;
     }
     .askevent-footer{
          position: fixed;
          bottom: 0;
          background-color: white;
          width: 100%;
          display: flex;
          padding: 1rem 1.2rem;
          justify-content: space-between;
          align-items: center;
     }
     .askDetails-content{
          background-color: #f6f6f6;
          height: 100vh;
          overflow-y: scroll;
     }
     .askevery-header{
          position: fixed;
          top: 0;
          width: 100%;
          z-index: 15;
          background-color: #f6f6f6;
     }
     .proplms{
          padding: 1rem;
          .ask_title{
               overflow: hidden;
               margin-bottom: 1rem;
               display: flex;
               align-items: center;
               .wen_te_dan{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 1.5rem;
                    height: 1.5rem;
                    background-color: #f55056;
                    border-radius: 0.3rem;
                    color: #ffffff;
                    font-size: 1rem;
                    float: left
               }
               p{
                    font-size: 1.3rem;
                    margin-left: 0.5rem;
                    width: 30rem;
                    font-weight: 600;
               }
          }
          >span{
               font-size: 1.1rem;
               color: #9d9d9d
          }
     }
     .replay_list{
          li{
               .list_title_info{
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    .headimg{
                         width: 3rem;
                         height: 3rem;
                         border-radius: 50%;
                         background-size: cover;
                    }
               }
               p{
                    margin-left: 3.5rem;
                    font-size: 1.4rem;
                    margin-top: 1rem;
               }
               .like_no_like{
                    text-align: right;
                    margin-top: 1rem;
                    img{
                         width: 1.1rem;
                         height: 1.2rem;
                    }
               }
          }
     }
     .all_part{
          width: 35.1rem;
          margin: 0 auto;
          background-color: #ffffff;
          margin-top: 1rem;
          border-radius: 0.7rem;
     }
     .more_footer{
          margin-top: 1rem;
          text-align: center;
          margin-bottom: 7rem;
          font-size: 1.3rem;
          color: #9d9d9d;
     }
     .ask_detali_rel_content{
          padding-top: 4.4rem;
     }
</style>
<template>
     <!--é—®é¢˜è¯¦æƒ…é¡µ-->
     <div class="askDetails-content steep_padding" v-if="askContent != null">
               <div class="askevery-header">
                    <HeaderPage>
                         <span slot="headerTitle">é—®é¢˜è¯¦æƒ…</span>
                    </HeaderPage>
               </div>
          <div class="ask_detali_rel_content">
               <div class="all_part">
                    <div class="proplms">
                         <div class="ask_title">
                              <span class="wen_te_dan">é—®</span>
                              <p>{{ askTitle }}</p>
                         </div>
                         <span>{{ AskCreatTime }}</span>
                    </div>
                    <ul class="replay_list">
                         <li v-for="(item,index) in askResult" :key="index + 'ins'">
                              <div class="list_title_info">
                                   <div class="headimg" :style="{backgroundImage:'url('+ item.headimgurl +')'}"></div>
                                   <div style="margin-left: 0.5rem">
                                        <div>
                                             <span style="font-size: 1.4rem;">{{ item.nickname }}</span>
                                             <span style="font-size: 1.2rem;display: inline-block;width: 3.6rem;line-height: 1.6rem;text-align: center;background-color: #e38ed9;color: #ffffff;border-radius: 0.5rem;margin-left: 0.5rem">å·²ä¹°</span>
                                        </div>
                                        <span style="font-size: 1.2rem;color: #9d9d9d">{{ item.create_time }}</span>
                                   </div>
                              </div>
                              <p style="">{{ item.content }}</p>
                              <div class="like_no_like"  v-if="item.is_star == 0">
                                   <img @click="likeAnswer(item.aid,index)" src="@/common/image/AskImg/nonelike.png" alt="">
                              </div>
                              <div class="like_no_like" v-if="item.is_star == 1" >
                                   <img @click="likeAnswer(item.aid,index)" src="@/common/image/AskImg/like.png" alt="">
                              </div>
                         </li>
                    </ul>
               </div>
               <div class="more_footer">
                    <span @click="moreList" v-if="moreBool">æŸ¥çœ‹æ›´å¤šå›žå¤</span>
                    <span v-if="!moreBool">æ²¡æœ‰æ›´å¤šå›žå¤äº†</span>
               </div>
               <div class="askevent-footer">
                    <input type="text" v-model="askValue" placeholder="å›žå¤è¯¥é—®é¢˜" style="font-size: 1.2rem">
                    <span @click="sendAsk">å›žå¤</span>
               </div>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
     export default {
          name: "AskDetails",
          data(){
               return {
                    askTitle:null,
                    page:1,
                    askResult:[],
                    AskCreatTime:null,
                    askValue:"",
                    askContent:null,
                    goodsInfo:null,
                    moreBool:true,
               }
          },
          methods:{
               //æŸ¥çœ‹æ›´å¤š
               moreList(){
                    this.page++
                    this.getAskInfo()

               },
               //é—®é¢˜ç­”æ¡ˆæ”¶è—
               likeAnswer(answer_id,index){
                    this.postObj({answer_id},window.PATHS.SHOPS_GOODS_PROBLEM_ANSWER_STAR,(result) => {
                         if (result.code == 1){
                              this.askResult[index].is_star == 0? this.askResult[index].is_star = 1 : this.askResult[index].is_star = 0
                         } else {
                              this.$toast(result.info)
                         }
                    })
               },
               // å›žå¤ä¿¡æ¯
               sendAsk(){
                    if (this.askValue == ""){
                         this.$toast.center("è¯·è¾“å…¥æ‚¨è¦å›žç­”çš„å†…å®¹")
                         return
                    }
                    var obj = {
                         problem_id:this.askContent.aid,
                         content:this.askValue
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_PROBLEM_ADD_ANSWER,(result) => {
                         if (result.code == 1){
                              this.$toast.center("å›žå¤æˆåŠŸ")
                              this.page = 1
                              this.askValue = ""
                              this.getAskInfo((data) => {
                                   this.askResult = data
                                   console.log(data)
                              })
                         }
                    })
               },
               //èŽ·å–å½“å‰é—®é¢˜çš„å›žç­”
               getAskInfo(callBack){
                    var obj = {
                         page:this.page,
                         problem_id:this.askContent.aid
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOOD_PROBLEM_ANSWER_LIST,(result) => {
                         if (result.code == 1){
                              if (result.data.length != 0){
                                   if (callBack){
                                        callBack(result.data)
                                   } else{
                                        this.askResult = this.askResult.concat(result.data)
                                   }
                                   console.log(this.askResult,"å›žå¤åˆ—è¡¨")
                              } else{
                                   this.moreBool = false
                              }
                         }
                    })
               },
          },
          mounted() {

          },
          created() {
               this.askContent = this.$store.state.askContent
               this.getAskInfo()

               //èŽ·å–ä»Žvueä¼ é€’è¿‡æ¥çš„é—®é¢˜åˆ›å»ºæ—¶é—´
               this.AskCreatTime = this.askContent.create_time
               console.log(this.AskCreatTime)
               //èŽ·å–ä»Žvueä¼ é€’è¿‡æ¥çš„é—®é¢˜title
               this.askTitle = this.askContent.title
          },
          components:{
               HeaderPage
          }
     }
</script>




