<template>
     <!--æŸ¥çœ‹è¯ç…§ä¿¡æ¯-->
     <div class="checkpapers-cntent" v-if="shopInfo != null">
          <div class="header" style="position: relative;display: flex;justify-content: space-between;align-items: center">
               <div>
                    <img src="@/common/image/shopImg/whiteLeft.png" style="width: 1.1rem;height: 1.9rem;" @click="goBack"/>
                    <span style="position: absolute;left: 50%;top: 50%; transform: translate(-50%,-50%);font-size: 1.7rem;color: #ffffff;">åº—é“ºåç§°</span>
               </div>
          </div>
          <h4 style="text-align: center;color: #ffffff;font-size: 1.5rem;font-weight: 500;margin-top: 2.5rem">è¾“å…¥éªŒè¯ç æŸ¥çœ‹è¯ç…§ä¿¡æ¯</h4>
          <div style="width: 35.1rem;margin: 0 auto;display: flex;justify-content: space-between;margin-top: 2.4rem">
               <input v-model="inpValue" type="text" placeholder="è¯·è¾“å…¥éªŒè¯ç " style="width: 21.1rem;border: none;border-radius: 0.5rem;padding-left: 2rem;font-size: 1.5rem;">
               <span style="display: inline-block;line-height: 4.4rem;width: 12rem;text-align: center;font-size: 1.5rem;background-color: #ffffff;;border-radius: 0.5rem">{{ number }}</span>
          </div>
          <span @click="shore" style="width: 35.1rem;display: block;line-height: 4.4rem;text-align: center;background-color: #ffffff;font-size: 1.7rem;margin: 0 auto;margin-top: 1rem;border-radius: 0.5rem">ç¡®å®š</span>
          <div @click="goBack" style="width: 100vw;height: 100vh;position: fixed;top: 0;background-color: black" v-if="show">
               <img :src=shopInfo.business_thumb alt="" style="position: absolute;top: 50%;left: 50%;transform: translate(-50%,-50%)">
          </div>
     </div>
</template>

<script>
     export default {
          name: "CheckPapers",
          data(){
               return {
                    number:null,
                    inpValue:"",
                    show:false,
                    shopInfo:null
               }
          },
          mounted(){
               this.number = Math.floor(Math.random () * 9000) + 100;
               this.shopInfo = this.$store.state.shopInfo

          },
          methods:{
               //ç‚¹å‡»ç¡®è®¤
               shore(){
                    if (this.inpValue == this.number){
                         this.show = true

                    } else{
                         this.inpValue = ""
                         this.number = Math.floor(Math.random () * 9000) + 100;
                         this.$toast.center('éªŒè¯ç é”™è¯¯')
                    }
               },
               goBack(){
                    window.history.go(-1)
               }
          }
     }
</script>

<style scoped>
     .header{
          width: 100%;
          height: 4.4rem;
          padding: 0 1.2rem;
     }
.checkpapers-cntent{
     background: linear-gradient(#f55056, #f5cdd5);
     min-height: 100vh;
}
</style>



