
<style scoped lang="scss">
     #add_address{
          height: 100vh;
          overflow-y: scroll;
     }
     .header{
          box-shadow: 2px 0 6px 0px #f4f4f4;
     }
     .shop_detail_info{
          padding: 14px 12px;
          background-color: #ffffff;
          border-radius: 7px;
     }
     .shop_name{
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 1.5rem;
          >div{
               display: flex;
               align-items: center;
          }
     }
     .goods_info{
          display: flex;
          align-items: flex-start;
          justify-content: flex-start;
          font-size: 1.3rem;
          .goods_thumb{
               width: 8.5rem;
               height: 8.5rem;
               border-radius: 5px;
               background-size: cover;
               flex-shrink: 0;
          }
          .goods_title{
               margin-left: 10px;
               p{
                    text-overflow: -o-ellipsis-lastline;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    line-clamp: 2;
                    -webkit-box-orient: vertical;
               }
               span{
                    display: inline-block;
                    margin-top: 10px;
                    color: #9d9d9d;
               }
          }
     }
     .conptate{


     }
     .same_mast_footer{
          padding: 15px 11px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          >span{
               font-size: 1.3rem;
          }
          >div{
               display: flex;
               align-items: center;
               span{
                    font-size: 1.1rem;
                    color: #9d9d9d;
               }
          }
     }
     .order_num{
          border-top: solid 1px #f6f6f6;
          input{
               font-size: 1.1rem;
               border: none;
          }
     }
     .submit{
          display: inline-block;
          width: 85%;
          position: fixed;
          bottom: 10px;
          left: 50%;
          transform: translateX(-50%);
          background-color: #f55056;
          color: #ffffff;
          font-size: 1.5rem;
          line-height: 4rem;
          text-align: center;
          border-radius: 2rem;
     }
</style>

<template>
     <!--å”®åŽæ·»åŠ åœ°å€-->
     <div id="add_address">
          <div class="header">
               <HeaderPage>å¡«å†™ç‰©æµä¿¡æ¯</HeaderPage>
          </div>
          <div class="shop_detail_info">
               <div class="goods_info">
                    <div class="goods_thumb" :style="{backgroundImage:'url('+ afterInfo.thumb +')'}"></div>
                    <div class="goods_title">
                         <p>{{ afterInfo.title }}</p>
                         <span>{{ afterInfo.specs }}</span>
                    </div>
               </div>
          </div>
          <div class="address_info">
               <div class="conptate same_mast_footer" @click="toExpress">
                    <span>ç‰©æµå…¬å¸</span>
                    <div>
                         <span style="margin-right: 10px;">{{ express?express.name : "è¯·é€‰æ‹©" }}</span>
                         <img src="@/common/image/publicImg/huiRight.png" alt="" style="width: 0.6rem;height: 1rem;">
                    </div>
               </div>
               <div class="order_num same_mast_footer">
                    <span>ç‰©æµå•å·</span>
                    <div>
                         <input type="text" v-model="yunNum" placeholder="è¯·è¾“å…¥æ­£ç¡®çš„è¿å•å·">
                    </div>
               </div>
          </div>
          <span @click="submit" class="submit">æäº¤</span>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "AddAddress",
          data(){
               return{
                    afterInfo:null,
                    express:null,
                    yunNum:""
               }
          },
          methods:{
               submit(){
                    if (this.yunNum != "" && this.express != null){
                         var obj = {
                              refund_id:this.afterInfo.aid,
                              logistics_code:this.express.code,
                              logistics_sn:this.yunNum
                         }
                         console.log(obj)
                         this.postObj(obj,window.PATHS.aftersale.SHOPSREFUND_SHIPPINGRECEIVER,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("æäº¤æˆåŠŸ")
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },500)
                              }
                         })
                    } else{
                         this.$toast.center("è¯·å®Œå–„ç‰©æµä¿¡æ¯")
                    }
               },
               toExpress(){
                    location.href = "#/expresslist"
               }
          },
          created(){
               this.express = this.$store.state.express
               this.afterInfo = this.$store.state.afterInfoAddress
               this.yunNum = this.$store.state.after_order_yun
          },
          beforeRouteLeave(to,from,next){
               if (to.name != "expresslist"){
                    this.$store.dispatch("setExpress",null)
                    this.$store.dispatch("set_after_order_yun",null)
               }else{
                    this.$store.dispatch("set_after_order_yun",this.yunNum)
               }
               next()
          },
          components:{
               HeaderPage,
          }
     }
</script>




