<script>
     import storage from "@/common/js/storage.js";
     import { joinShoppingCar } from "@/common/js/common.js"
     export default {
          name: "VideoShop",
          data() {
               return {
                    mes: null,
                    //æ¨ªç‰ˆçš„å•†å“åˆ—è¡¨
                    comList: [],
                    //ç«–ç‰ˆçš„å•†å“åˆ—è¡¨
                    shuComList:[],
                    skuArr: [],
                    address: null,
                    skuComInfoList:[],
                    //     ç«–ç‰ˆbool
                    isVertical: true,
                    //æ¨ªç‰ˆ
                    isAcross: false,
                    //ç‚¹å‡»åŽ»è´­ä¹°å¼¹å‡ºskuçš„bool
                    buySkuBool: false,
                    // ç«–ç‰ˆåŽ»è´­ä¹°ç‚¹å‡»å‚¨å­˜çš„index
                    shuIndex:0,
                    listPage:1,
                    scrollXBool:true
                    // shareUserId:2
               }
          },
          methods: {
               //ç«–ç‰ˆçš„æ»šåŠ¨
               _shuScroll(ev){
                    if (this.scrollXBool){
                         if (ev.target.scrollTop + ev.target.offsetHeight + 10 >= ev.target.scrollHeight){
                              this.scrollXBool = false
                              this.listPage++
                              this.getComList()
                         }
                    }
               },
               //æ¨ªç‰ˆçš„æ»šåŠ¨
               _henScroll(ev){
                    if (this.scrollXBool){
                         if (ev.target.scrollLeft + 50 >= ev.target.scrollWidth - window.innerWidth){
                              console.dir(22)
                              this.scrollXBool = false
                              this.listPage++
                              this.getComList()
                         }
                    }
               },
               //åˆ°å•†å“è¯¦æƒ…é¡µ
               _toDetails(good_id){
                    this.$router.push({name: 'DetailsPage', query: {comId: good_id,video_id:this.mes}})
               },
               //ç‚¹å‡»åŽ»è´­ä¹°
               _toBuy(data, index) {
                    this.shuIndex = index
                    this.buySkuBool = true
               },
               //åˆ‡æ¢æ˜¾ç¤ºæ–¹å¼
               _change_view_type() {
                         this.isVertical = !this.isVertical
                         this.isAcross = !this.isAcross
               },
               //åŠ å…¥è´­ç‰©è½¦
               joinCar(item, index) {
                    joinShoppingCar(this,{
                              skugroup:item.skugroup,
                              skuList:item.skulist,
                              chooseSkuList:this.skuArr[index],
                              goods_id:item.goods_id,
                              number:1,
                              video_id:this.mes,
                              share_user_id:this.shareUserId,
                              extra_table:item.extra_table
                         },() => {})
                    },
               //ç«‹å³è´­ä¹°
               _henBuyNow(item, index) {
                    var chieck = 1
                    var title = []
                    //è¯·é€‰æ‹©è§„æ ¼
                    this.comList[index].skugroup.forEach((item, ins) => {
                         var index2 = this.skuArr[index][ins]
                         if (index2 == undefined) {
                              if (chieck == 1) {
                                   this.$toast.center('è¯·é€‰æ‹©' + item.title)
                              }
                              chieck = 0;
                              return
                         }
                    })
                    if (chieck == 0) {
                         return
                    }
                    var choosedSku = this.skuComInfoList[index]
                    var skuCom = {
                         goods_id:item.goods_id,
                         title:item.title,
                         thumb:choosedSku ? choosedSku.icon_id : item.thumb,
                         integral: choosedSku? choosedSku.integral : item.rebate_integral,
                         number: 1,
                         specs: choosedSku? choosedSku.title : '',
                         sku_path: choosedSku? choosedSku.path : '',
                         share_user_id: this.share_user_id,
                         // video_id: this.skucomInfo.video_id,
                         stock: choosedSku? choosedSku.stock : item.stock,
                         // period_id:this.comInfo.period_id? this.comInfo.period_id : 0,
                         is_original:0
                    }
                    console.log(skuCom)
                    this.$store.commit("setEntDetailSkuInfo",skuCom)
                    setTimeout(() => {
                         location.href = "#/ent"
                    },500)
               },
               //ç‚¹å‡»sku
               changeSku(index1, index2, index3) {
                    this.comList[index1].skugroup[index2].specs.forEach((item) => {
                         item.styleBool = false
                    })
                    this.comList[index1].skugroup[index2].specs[index3].styleBool = true
                    this.comList[index1].thumb = this.comList[index1].skugroup[index2].specs[index3].icon? this.comList[index1].skugroup[index2].specs[index3].icon : this.comList[index1].thumb
                    this.skuArr[index1][index2] = this.comList[index1].skugroup[index2].title + ':' + this.comList[index1].skugroup[index2].specs[index3].name
                    console.log(this.skuArr)
                    if (this.skuArr[index1].length == this.comList[index1].skugroup.length) {
                         var newSku = this.skuArr[index1].join(',')
                         this.comList[index1].skulist.forEach((item, index) => {
                              if (newSku == item.title) {
                                   this.comList[index1].shop_price = item.price
                                   this.skuComInfoList[index1] = item
                              }
                         })
                    }
               },
               getComList() {
                    var obj = {
                         video_id: this.mes,
                         page:this.listPage,
                         list_rows:5
                    }
                    this.postObj(obj, window.PATHS.VUDEOS_GETCARTGOODS, (result) => {
                         if (result.code == 1) {
                                   result.data.forEach((item) => {
                                        this.skuArr.push([])
                                        item.skugroup.forEach((item2) => {
                                             item2.specs.forEach((item3) => {
                                                  this.$set(item3,'styleBool',false)
                                             })
                                        })
                                   })
                                    this.comList = this.comList.concat(result.data)
                                   this.shuComList = this.shuComList.concat(JSON.parse(JSON.stringify(result.data)))
                              this.scrollXBool = true
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          },
          mounted() {
               this.mes = this.$route.query.video_id
               this.shareUserId = this.$route.query.share_user_id
               // this.mes = 278
               this.getComList()
          },
     }
</script>





