<script>
     import HeaderPage from '../components/HeaderPage'
     import Bus from '../Bus'

     export default {
          name: "ShoppingCar",
          data() {
               return {
                    showAlert2:false,
                    showAlert:false,
                    isallbool: false,
                    allPrice:0,
                    carList:{},
                    addressList:[{
                         user_name:'å§“å',
                         user_phone:'ç”µè¯',
                         user_address:'åœ°å€'
                    }],
                    comInfo:{},
                    //     å‘ç¥¨
                    billInfo:"",
                    remark:"",
                    orderNo:''
               }
          },
          methods: {
               //ç¡®è®¤ç”¨æˆ·æ˜¯å¦ä¸‹å•äº†
               shorePay(){
                    this.showAlert2 = false
                    this.postObj({order_no:this.orderNo},window.PATHS.ORDER_GETORDER,(data) => {
                         if (data.code == 1){
                              if (data.data.pay_status == 1){
                                   this.success()
                                   return
                              }else {
                                   this.$toast.center('æ”¯ä»˜å¤±è´¥')
                                   setTimeout(function () {
                                        window.history.go(-1)
                                   },1000)
                              }
                         }else {
                              this.$toast.center(data.info)
                         }
                    })
               },
               //åˆ°å‘ç¥¨é€‰æ‹©é¡µé¢
               tobillpage(){
                    location.href = '#/bill'
               },
               toPlace() {
                    location.href = "#/place"
               },
               // èŽ·å–å‘ç¥¨å†…å®¹
               getBill(){
                    if (this.$store.state.bill != null) {
                         var data = this.$store.state.bill
                         var invoice = {
                              name: data.name,
                              taxpayer_id: data.taxpayer_id,
                              address: this.addressList[0].user_address,
                              phone: "",
                              bank_name: "",
                              bank_no: "",
                              mobile: data.mobile,
                         }
                         this.billInfo = invoice
                    }
               },
               //ä¸‹å•
               onOrder(){
                    var obj = {}
                    var arr = []
                    this.carList.goods.forEach((item,index) => {
                              arr.push({
                                   goods_id:item.goods_id,
                                   sku_path:item.goods_sku_path,
                                   number:item.number,
                                   is_original:1,
                                   period_id:1,
                                   share_user_id:"",
                                   video_id:item.video_id,
                              })
                    })

                         obj.goods = arr
                         obj.receiver_name = this.addressList[0].user_name
                         obj.receiver_phone = this.addressList[0].user_phone
                         obj.receiver_address = this.addressList[0].user_address
                         obj.remark = this.remark
                         obj.shop_id = this.carList.shop_id
                         //å‘ç¥¨
                         if (this.billInfo){
                              obj.invoice = this.billInfo
                         }
                         // æäº¤è®¢å•ä¿¡æ¯
                         this.postObj(obj, window.PATHS.SHOPS_OREDER_ADD_ORDER, (data) => {
                              console.log(data)
                              if (data.code == 1){
                                   this.$store.commit('changeBill',null)
                                   this.$store.commit('billBool',false)
                                   this.orderNo = data.data.order_no
                                   if (data.data.status == 1){
                                        this.success()
                                   } else{
                                        location.href = "app://shoporder/pay/" + data.data.order_no + '/' + data.data.price
                                        var that = this
                                        setTimeout(function () {
                                             that.showAlert2 = true
                                        },500)
                                   }

                              }else {
                                   this.$toast.center(data.info)
                              }
                         })

               },
               //å¢žåŠ æ•°é‡
               add(stock,i){
                    if (this.carList.goods[i].number >= stock){
                         return
                    } else {
                         this.carList.goods[i].number++
                         this.getPrice()
                    }
               },
               //å‡å°‘æ•°é‡
               jian(stock,i){
                    if (this.carList.goods[i].number <= 1){
                         return
                    } else {
                         this.carList.goods[i].number--
                         this.getPrice()
                    }
               },
               scrollShowHeader(){
               },
               success(){
                    this.showAlert = true
               },
          //     èŽ·å–å†æ¥ä¸€å•çš„å•†å“ä¿¡æ¯
               getComInfo(){
                    this.postObj({order_no:this.$store.state.again_order},window.PATHS.SHOPS_ORDER_GET_INFO,(result) => {
                         console.log(result)
                         if (result.code == 1){
                              this.carList = result.data
                              //è®¡ç®—ä»·é’±
                              this.getPrice()
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          //     è®¡ç®—ä»·é’±
               getPrice(){
                    var price = 0
                    this.carList.goods.forEach((item,index) => {
                         price += parseInt(item.goods_price * 100) * item.number / 100
                    })
                    this.allPrice = price
               }
          },
          mounted(){
               this.getComInfo()
               this.getBill()
               //     èŽ·å–æ”¶è´§åœ°å€
               this.postObj({}, window.PATHS.ADDRESS_GET_LIST, (result) => {
                    if (result.code == 1) {
                         if (!this.$route.params.item) {
                              this.address = this.addressList[0] || "";
                         }
                         this.addressList = result.data
                    }
               })

               window.addEventListener("scroll", this.scrollShowHeader,true)
          },
          components: {
               HeaderPage
          },
     }
</script>

