<template>
     <!--åŒæ­¥å…¬ç”¨ç»„ä»¶-->
     <div class="steep_padding">
          <div :class="isBlack? 'header' : 'white_header'">
               <div @click="back" class="left">
                    <img v-if="isBlack" src="@/common/image/publicImg/left_return.png" style="width: 0.85rem;height: 1.65rem;"/>
                    <img v-if="isWhite" src="@/common/image/publicImg/whiteLeft.png" style="width: 0.85rem;height: 1.65rem;"/>
               </div>
               <span class="header_title"><slot name="headerTitle"></slot></span>
               <div class="header_right">
                    <slot name="headerRight"></slot>
               </div>
          </div>
     </div>
</template>

<script>
     export default {
          name: "PlacePage",
          props:["colorType"],
          data(){
               return{
                    isWhite:false,
                    isBlack:true
               }
          },
          methods:{
               back(){
                    if (this.$route.query.is_user) {
                         location.href = window.EQUITMENTBACK
                    } else {
                         if(this.$route.name == "paydefeated"){
                              this.$emit("sendBackBool",true)
                              return
                         }
                         window.history.go(-1)
                    }
               }
          },
          created() {
               if (this.colorType == 'white'){
                    this.isWhite = true
                    this.isBlack = false
               }
          }
     }
</script>

<style scoped>
     .header_right{
          width: unset !important;
     }
     .header_title{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%);
     }
     .steep_padding{
          width: 100%;
     }
      .left{
           height: 100%;
           display: flex;
           align-items: center;
           padding-right: 20px;
     }
      .white_header{
           width: 100%;
           height: 4.4rem;
           display: flex;
           justify-content: space-between;
           align-items: center;
           font-size: 1.7rem;
           color: #ffffff;
           position: relative;
           padding: 0 1.2rem;
      }
     .header{
          width: 100%;
          height: 4.4rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          font-size: 1.7rem;
          color: #212121;
          position: relative;
          padding: 0 1.2rem;
     }
</style>



