
<style scoped lang="scss">
     #commment_list{
          height: 100vh;
          overflow-y: scroll;
          background-color: #f6f6f6;
          padding-top: 5.4rem;
     }
     .header{
          z-index: 20;
          position: fixed;
          top: 0;
          left: 0;
          background-color: #ffffff;
          width: 100%;
     }
     .commen_list_ul li{
          background-color: #ffffff;
          padding: 15px 12px;
          border-bottom:solid 1px #f6f6f6;
          .goods_info{
               width:100%;
               display: flex;
               align-items: center;
               justify-content: flex-start;
               padding-right: 5.7rem;
          }
          .goods_img{
               width: 6rem;
               height: 6rem;
               flex-shrink: 0;
               border-radius: 5px;
               background-size: cover;
          }
          p{
               margin-left: 10px;
               text-overflow: -o-ellipsis-lastline;
               overflow: hidden;
               text-overflow: ellipsis;
               display: -webkit-box;
               -webkit-line-clamp: 2;
               line-clamp: 2;
               -webkit-box-orient: vertical;
               font-size: 1.4rem;

          }
          .li_footer_btn{
               text-align: right;
               span{
                    display: inline-block;
                    line-height: 2.9rem;
                    width: 8rem;
                    font-size: 1.3rem;
                    color: #ffffff;
                    background-color: #f55056;
                    text-align: center;
                    border-radius: 1.45rem;
                    margin-left: 15px;
               }
          }
          >p{
               margin-left: 0;
               margin-top: 17px;
               margin-bottom: 10px;
          }
     }
     .zhuiend{
          background-color: transparent !important;
          border: solid 1px #9d9d9d;
          color: #9d9d9d !important;
     }
     .comment_wuliu{
          background-color: transparent !important;
          color: #f55056 !important;
          border: solid 1px #f55056 !important;
     }
     .comment_content{
          padding-top: 5.4rem;
     }
     .van_table{
          border-radius: 1rem;
          overflow: hidden;
     }
</style>

<template>
     <!--è¯„è®ºåˆ—è¡¨-->
     <div ref="commentList" id="commment_list" class="steep_padding" @scroll="scroll" v-if="comList.length != 0 || addComList.length != 0">
          <div class="header" ref="header">
               <HeaderPage>
                    <span slot="headerTitle">æˆ‘çš„è¯„ä»·</span>
               </HeaderPage>
          </div>
          <div class="comment_content">
               <van-tabs class="van_table" v-model="tableVal" line-height="0" title-active-color="#f55056" sticky :offset-top="stickyTop">
                    <van-tab title="å¾…è¯„ä»·">
                         <ul class="commen_list_ul">
                              <li v-for="(item,index) in comList" :key="index + 'godins'">
                                   <div @click="toGoodsDetail(item.goods_id)" class="goods_info">
                                        <div class="goods_img" :style="{backgroundImage:'url('+ item.thumb +')'}"></div>
                                        <p>{{ item.title }}</p>
                                   </div>
                                   <div class="li_footer_btn">
                                        <span @click="comment(item,false)">ç«‹å³è¯„ä»·</span>
                                   </div>
                              </li>
                         </ul>
                    </van-tab>
                    <van-tab title="å·²å®Œæˆ/å¾…è¿½è¯„">
                         <ul class="commen_list_ul">
                              <li @click="commentDetail(item)" v-for="(item,index) in addComList" :key="index + 'adins'">
                                   <div class="goods_info" >
                                        <div @click.stop="toGoodsDetail(item.goods_id)" class="goods_img" :style="{backgroundImage:'url('+ item.thumb +')'}"></div>
                                        <p>{{ item.title }}</p>
                                   </div>
                                   <p>{{ item.content }}</p>
                                   <div class="li_footer_btn">
                                        <span @click.stop="commentscore(item)" v-if="item.service_score == 0 || item.goods_score == 0 || item.logistics_score == 0" class="comment_wuliu">è¯„ä»·ç‰©æµ</span>
                                        <span v-if="item.is_addcom == 0" @click.stop="comment(item,true)">ç«‹å³è¿½è¯„</span>
                                        <span v-if="item.is_addcom == 1" class="zhuiend">å·²è¿½è¯„</span>
                                   </div>
                              </li>
                         </ul>
                    </van-tab>
               </van-tabs>
          </div>
     </div>
</template>

<script>
     import storage from '@/common/js/storage.js'
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "CommentIndex",
          data(){
               return{
                    comList:[],
                    addComList:[],
                    stickyTop:0,
                    tableVal:0,
                    scrollBool:true,
                    page:{
                         comPage:1,
                         addCom:1
                    },
                    comMore:true,
                    addMore:true,
                    scrollTop:0
               }
          },
          mounted(){
          //     èŽ·å–é¡¶éƒ¨é«˜ä½Ž

          },
          methods:{
               // è¿”å›ž
               _returnBack(){
                    if(this.$route.query.is_user){
                         location.href = window.EQUITMENTBACK
                    }else{
                         window.history.go(-1)
                    }
               },
               //åˆ°è¯„ä»·ç‰©æµ
               commentscore(info){
                    this.$router.push({name:"commentscore",params:{info}})
                    // location.href = "#/commentscore"
               },
               //åˆ°å•†å“è¯¦æƒ…é¡µ
               toGoodsDetail(goods_id){
                    this.$router.push({name: 'DetailsPage', query: {comId: goods_id}})
               },
               //è¯„ä»·è¯¦æƒ…
               commentDetail(val){
                    this.$router.push({name:"commentdetails",query:{comment_id:val.comment_id}})
               },
               //ç«‹å³è¯„ä»·ä¸Žè¿½è¯„
               comment(item, bool) {
                                   if (bool) {
                                        this.$router.push({
                                             name: 'commenteeild',
                                             params: {comInfo: item,order_no:item.order_no,bool}
                                        })
                                   } else {
                                        this.$router.push({
                                             name: 'commenteeild',
                                             params: {comInfo: item,order_no:item.order_no}
                                        })
                                   }
               },
               //èŽ·å–è¿½å“
               getAddList(){
                    var that = this
                    var obj = {
                         page:this.page.addCom,
                         is_comment:1
                    }
                    this.postObj(obj,window.PATHS.SHOPSORDER_GETCOMMENTGOODS,(result) => {
                         if (result.code == 1){
                              if (result.data.length != 0){
                                   this.addComList = this.addComList.concat(result.data)
                                   console.log(this.addComList,'è¿½è¯„æ•°æ®')
                                   this.addMore = true
                              } else{
                                   this.addMore = false
                              }
                              setTimeout(() => {
                                   that.scrollBool = true
                              },1000)
                              console.log(this.addComList)
                         }
                    })
               },
          //     èŽ·å–è¯„ä»·
               getComList(){
                    var that = this
                    var obj = {
                         page:this.page.comPage,
                         is_comment:0
                    }
                    this.postObj(obj,window.PATHS.SHOPSORDER_GETCOMMENTGOODS,(result) => {
                         if (result.code == 1){
                              if (result.data.length != 0){
                                   this.comList = this.comList.concat(result.data)
                                   this.comMore = true
                              } else{
                                   this.comMore = false
                              }
                              setTimeout(() => {
                                   that.scrollBool = true
                              },1000)
                         }
                    })
               },
               scroll(){
                    this.stickyTop = this.$refs.header.clientHeight
                    this.scrollTop = this.$refs.commentList.scrollTop
                    var scrollTop = this.$refs.commentList.scrollTop
                    var height = this.$refs.commentList.scrollHeight
                    var windHeihgt = window.innerHeight
                    this.scrollMore(scrollTop,height,windHeihgt,() => {
                         if (this.scrollBool){
                              this.scrollBool = false
                              if (this.tableVal == 0){
                                   if (this.comMore){
                                        this.page.comPage++
                                   }
                                   this.getComList()
                              }
                              if (this.tableVal == 1){
                                   if (this.addMore){
                                        this.page.addCom++
                                   }
                                   this.getAddList()
                              }
                         }
                    })
               },
          },
          created() {
               var cache = storage.get("commentindexInfo")
               if (cache){
                    console.log(cache)
                    this.tableVal = cache.tableins
                    this.comList = cache.comList
                    this.addComList = cache.addComList
                    this.page.comPage = cache.page.comPage
                    this.page.addCom = cache.page.addCom
                    this.$nextTick(() => {
                         this.$refs.commentList.scrollTop = cache.scrollTop
                    })
               }else{
                    this.getComList()
                    this.getAddList()
               }
          },
          activated(){

          },
          components:{
               HeaderPage
          },

          beforeRouteLeave(to,from,next){
               if (to.name == "commenteeild" || to.name == "DetailsPage" || to.name == "commentdetails" || to.name == 'commentscore'){
                    var obj = {
                         tableins:this.tableVal,
                         comList:this.comList,
                         addComList:this.addComList,
                         page:{
                              comPage:this.page.comPage,
                              addCom:this.page.addCom
                         },
                         scrollTop:this.scrollTop
                    }
                    storage.set("commentindexInfo",obj)
                    next()
               }else{
                    this.scrollTop = 0
                    storage.remove("commentindexInfo")
                    next()
               }

          }
     }
</script>




