<template>
     <!--æ–°å¢žåœ°å€é¡µé¢-->
     <div class="newplace-content">
          <HeaderPage><span slot="headerTitle">ç¼–è¾‘åœ°å€</span></HeaderPage>
          <div class="choosePlace">
          </div>
          <div>

               <van-address-edit
                       :area-list="areaList"
                       show-set-default
                       @change-default="changeChoose"
                       @save="saveplace"
                       :address-info='boh1'
                       :show-delete="delateBool"
                       @delete="onDelete"
                       delete-button-text="åˆ é™¤æ”¶è´§åœ°å€"
               >
                    <div class="choosePlace">
                         <span>åœ°å€æ ‡ç­¾</span>
                         <div>
                              <span :class="type == 0? 'active_tab_type' : ''" @click="changeTab(0,'å®¶')">å®¶</span>
                              <span :class="type == 1? 'active_tab_type' : ''" @click="changeTab(1,'å…¬å¸')">å…¬å¸</span>
                              <span :class="type == 2? 'active_tab_type' : ''" @click="changeTab(2,'å­¦æ ¡')">å­¦æ ¡</span>
                         </div>
                    </div>
               </van-address-edit>
          </div>
     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     import area from '@/common/js/area.js'
     export default {
          name: "NewPlace",
          data(){
               return{
                    areaList:area,
                    chooseBool:0,
                    sendPlace:{},
                    type:-1,
                    detailInfo:'å®¶',
                    shouUrlGress:{},
                    boh1:{
                         name:"",
                         tel:"",
                         addressDetail:"",
                         isDefault:false,
                         areaCode:0,
                         province:"",
                         city:"",
                         county:"",
                    },
                    delateBool:false
               }
          },
          methods:{
               //åˆ é™¤åœ°å€
               onDelete(content){
                    var placeObj = {
                         aid:this.$route.params.item.aid,
                         option:'delete'
                    }
                    this.postObj(placeObj,window.PATHS.ADDRESS_SET_OPTION,(result) => {
                         if (result.code == 1){
                              if (this.$store.state.address && this.$route.params.item.aid == this.$store.state.address.aid) {
                                   console.log("åˆ é™¤äº†")
                                   this.$store.commit("setAddress",null)
                              }
                             window.history.go(-1)
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               changeTab(num,placeType){
                    this.type = num
                    this.detailInfo = placeType
               },
               changeChoose(isBool){
                    if (isBool == true){
                         this.chooseBool = 1
                    }else {
                         this.chooseBool = 0
                    }
               },
               saveplace(placeData){
                    var setData = {};
                    setData.user_name = placeData.name
                    setData.user_phone = placeData.tel
                    setData.user_address = placeData.addressDetail
                    setData.isdefault = this.chooseBool;
                    setData.provinceName =  placeData.province;
                    setData.cityName =  placeData.city;
                    setData.countryName =  placeData.county;
                    setData.detailInfo =  this.detailInfo;
                    setData.nationalCode = '86';

                    if (this.$route.params.item){
                         setData.aid = this.$route.params.item.aid
                    }
                    this.postObj(setData,window.PATHS.ADDRESS_SAVE_INFO,(result) => {
                         if (result.code == 0){
                              alert(result.info)
                              return
                         }
                         window.history.go(-1)
                    })
               }
          },
          mounted(){
               console.log(this.$route.params.item)
          //     èŽ·å–ä»Žåœ°å€è£‚å˜ç‚¹å‡»ä¼ é€’è¿‡æ¥çš„parems
               if (this.$route.params.item){
                    this.delateBool = true
                    var neobj = this.$route.params.item
                    this.boh1.name = neobj.user_name
                    this.boh1.tel = neobj.user_phone
                    this.boh1.addressDetail = neobj.user_address
                    this.boh1.province = neobj.provinceName
                    this.boh1.city = neobj.cityName
                    this.boh1.county = neobj.countryName
                    //æŸ¥æ‰¾ç¬¦åˆæ¡ä»¶å€¼å¹¶å­˜å…¥æ–°æ•°ç»„
                    for (var i in this.areaList.county_list){
                         if (this.areaList.county_list[i] == neobj.countryName) {
                              // console.log(i)
                              this.boh1.areaCode = i
                         }
                    }
                    this.boh1.isDefault = neobj.isdefault == "1"? true : false
                    if (neobj.detailInfo){
                         if (neobj.detailInfo == "å…¬å¸"){
                              this.type = 1
                         }
                         if (neobj.detailInfo == "å®¶"){
                              this.type = 0
                         }
                         if (neobj.detailInfo == "å­¦æ ¡"){
                              this.type = 2
                         }
                    }
               }
          //      shouUrlGress

          },
          components:{
               HeaderPage
          }
     }
</script>

<style scoped>
     .newplace-content >>> .van-switch--on{
          background-color: #f55056 !important;
     }
     .active_tab_type{
          color: #ffffff !important;
          background-color: #f55056;
          border: solid 1px #f55056 !important;
     }
     .newplace-content >>> .van-tag{
          margin-right: 1rem;
     }
     .choosePlace div span{
          width: 50px;
          line-height: 25px;
          text-align: center;
          display: inline-block;
          margin-left: 10px;
          color: #9d9d9d;
          border: solid 1px #9d9d9d;
          border-radius: 12.5px;
     }
     .choosePlace{
          margin-left: 16px;
          display: flex;
          justify-content: space-between;
          padding: 13px 0;
          padding-right: 12px;
          border-bottom: solid 1px #f6f6f6;
          font-size: 14px;
          align-items: center;
     }
     .newplace-content >>> .van-address-edit__buttons{
          margin-top: 2rem;
     }

</style>



