               }
               }

          }
     }
     .activeTable{
          color: #f55056;
     }
     .goods_price_share{
          display: flex;
          align-items: center;
          justify-content: space-between;
          .goods_price{
               color: #ff0000;
               span{
                    font-family: "æ•°å­—ä¸“ç”¨"
               }
          }
          .market_parice{
               font-size: 1.1rem;
               color: #9d9d9d;
               text-decoration: line-through;
               margin-left: 1.5rem;
          }
          .share_goods{
               img{
                    width: 0.8rem;
                    height: 0.85rem;
               }
               span{
                    font-size: 0.9rem;
                    color: #ff0000;
                    font-family: "æ•°å­—ä¸“ç”¨"
               }
          }
     }
     .shop_cile{
          ul{
               li{
                    background-color: #ffffff;
                    border-radius: 0.5rem;
                    padding: 1rem;
                    .shop_title_header{
                         display: flex;
                         align-items: center;
                         justify-content: space-between;
                         .shop_title_header_left{
                              display: flex;
                              align-items: center;
                               .shop_thubm{
                              width: 3rem;
                              height: 3rem;
                              border-radius: 50%;
                              background-size: cover;
                              }
                              .shop_name{
                                   font-size: 1.5rem;
                                   margin-left: 1rem;
                              }
                              img{
                                   width: 5.8rem;
                                   height: 1.55rem;
                                   margin-left: 1rem;
                              }
                         }
                         button{
                              border: none;
                              background-color: transparent;
                              width: 6.5rem;
                              height: 2.4rem;
                              border-radius: 1.2rem;
                              background-color: #f55056;
                              font-size: 1.5rem;
                              color: #ffffff;
                         }

                    }
                    .shop_goods_list{
                         display: flex;
                         justify-content: flex-start;
                         margin-top: 1.5rem;
                         section{
                              margin-left: 0.5rem;
                              width: 10.7rem;
                              .shop_goods_thumb{
                                   width: 100%;
                                   padding-top: 100%;
                                   background-size: cover;
                                   border-radius: 0.5rem;
                              }
                              p{
                                   text-overflow: -o-ellipsis-lastline;
                                   overflow: hidden;
                                   text-overflow: ellipsis;
                                   display: -webkit-box;
                                   -webkit-line-clamp: 2;
                                   line-clamp: 2;
                                   -webkit-box-orient: vertical;
                                   padding: 0.2rem;
                                   font-size: 1rem;
                              }
                         }
                         section:first-child{
                              margin-left: 0
                         }
                    }
               }
          }
     }
</style>

<template>
<div id="searchResult" ref="searchResult" class="steep_padding" @scroll="scroll">
     <div class="searchresult_header" ref="searchresultHeader">
          <HeaderPage>
               <form slot="headerTitle" action="" @submit.prevent="_search()">
                              <input ref="inptFouc" type="text" v-model="serachVla" placeholder="æœç´¢å•†å“">
                         </form>
               <div slot="headerRight" class="right_btn_search">
                    <button @click="_search">æœç´¢</button>
               </div>
          </HeaderPage>
     </div>
     <div style="padding-top:4.4rem;">
          <div class="header_table">
               <span @click="tableIns = index" :class="tableIns == index? 'activeTable' : ''" v-for="(item,index) in tableList" :key="index + 'table'">{{ item }}</span>
          </div>
     <div class="search_result_content">
          <!-- å•†å“ -->
          <div class="goods_cile" v-if="tableIns == 0 && goodsList.length != 0">
               <van-sticky v-if="goodsList.length >= 5" :offset-top="headerHeight">
                    <div class="solt_pai">
                         <div @click="soltClick(index)" v-for="(item,index) in soltList" :key="index + 'solt'">
                              <span :class="soltIns == index? 'activeTable' : ''">{{item}}</span>
                              <template v-if="soltIns == 0 && index == 0">
                                   <img v-if="orderBay == 'desc'" src="@/common/image/searchImg/mail_ico-Spinner.png" alt="">
                                   <img v-if="orderBay == 'asc'" src="@/common/image/searchImg/mail_ico-Spinner_preup.png" alt="">
                               </template>
                               <template v-if="soltIns == 1 && index == 1">
                                   <img v-if="orderBay == 'desc'" src="@/common/image/searchImg/mail_ico-Spinner.png" alt="">
                                   <img v-if="orderBay == 'asc'" src="@/common/image/searchImg/mail_ico-Spinner_preup.png" alt="">
                               </template>
                               <template v-if="soltIns == 2 && index == 2">
                                   <img v-if="orderBay == 'desc'" src="@/common/image/searchImg/mail_ico-Spinner.png" alt="">
                                   <img v-if="orderBay == 'asc'" src="@/common/image/searchImg/mail_ico-Spinner_preup.png" alt="">
                               </template>
                         </div>
                    </div>
               </van-sticky>
               <div v-if="goodsList.length < 5" class="solt_pai">
                         <div @click="soltClick(index)" v-for="(item,index) in soltList" :key="index + 'solt'">
                              <span :class="soltIns == index? 'activeTable' : ''">{{item}}</span>
                             <template v-if="soltIns == 0 && index == 0">
                                   <img v-if="orderBay == 'desc'" src="@/common/image/searchImg/mail_ico-Spinner.png" alt="">
                                   <img v-if="orderBay == 'asc'" src="@/common/image/searchImg/mail_ico-Spinner_preup.png" alt="">
                               </template>
                               <template v-if="soltIns == 1 && index == 1">
                                   <img v-if="orderBay == 'desc'" src="@/common/image/searchImg/mail_ico-Spinner.png" alt="">
                                   <img v-if="orderBay == 'asc'" src="@/common/image/searchImg/mail_ico-Spinner_preup.png" alt="">
                               </template>
                               <template v-if="soltIns == 2 && index == 2">
                                   <img v-if="orderBay == 'desc'" src="@/common/image/searchImg/mail_ico-Spinner.png" alt="">
                                   <img v-if="orderBay == 'asc'" src="@/common/image/searchImg/mail_ico-Spinner_preup.png" alt="">
                               </template>
                         </div>
                    </div>
               <ul>
                    <li @click="toDetail(item.aid)" v-for="(item,index) in goodsList" :key="index + 'goods'">
                         <div class="goods_thumb" :style="{backgroundImage:'url('+ item.thumb +')'}"></div>
                         <div class="goods_right">
                              <p>{{ item.title }}</p>
                              <div class="goods_right_bottom">
                                   <div class="goods_how_get">
                                        <span style="color:#ff0000" class="num_family">{{item.sales}}</span>
                                        <span>äººèŽ·å¾—æ”¶ç›Š</span>
                                   </div>
                                   <div class="goods_price_share">
                                        <div class="goods_price_market">
                                             <span class="goods_price">
                                                  <span style="font-size:1.8rem;">ï¿¥{{ item.shop_price.substring(0,item.shop_price.length - 3) }}</span><span style="font-size:1.1rem">{{ item.shop_price.substring(item.shop_price.length - 3,item.shop_price.length) }}</span>
                                             </span>
                                             <span class="market_parice">ï¿¥{{ item.market_price }}</span>
                                        </div>
                                        <div class="share_goods">
                                             <img src="@/common/image/publicImg/shareright.png" alt="">
                                             <span>{{ item.share_price }}</span>
                                        </div>
                                   </div>
                              </div>
                         </div>
                    </li>
               </ul>
          </div>
          <!-- åº—é“º -->
          <div class="shop_cile" v-if="tableIns == 1">
               <ul>
                    <li @click="_toShop(item.aid)" v-for="(item,index) in shopList" :key="index + 'shop'">
                         <div class="shop_title_header">
                              <div class="shop_title_header_left">
                                   <div class="shop_thubm" :style="{backgroundImage:'url('+ item.thumb +')'}"></div>
                                   <span class="shop_name">{{ item.name }}</span>
                                   <img src="@/common/image/searchImg/qijianshop.png" alt="">
                              </div>
                              <button>è¿›åº—</button>
                         </div>
                         <div class="shop_goods_list" v-if="item.goods.length != 0">
                              <section v-for="(gsItem,gsIndex) in item.goods" :key="gsIndex + 'spgs'">
                                   <div class="shop_goods_thumb" :style="{backgroundImage:'url('+ gsItem.thumb +')'}"></div>
                                   <p>{{ gsItem.title }}</p>
                              </section>
                         </div>
                    </li>
               </ul>
          </div>
     </div>
     </div>
</div>
</template>

<script>
     import storage from '@/common/js/storage.js'
     import HeaderPage from "../components/HeaderPage"
     export default {
          name: "SearchResult",
          data() {
               return {
                   tableList:["å…¨éƒ¨","åº—é“º"],
                   soltList:["ç»¼åˆ","é”€é‡","ä»·æ ¼"],
                   tableIns:0,
                   soltIns:0,
                    serachVla:"",
                   goodsPage:1,
                   goodsList:[],
                   goodsMoreBool:true,
                   shopList:[],
                   shopMoreBool:true,
                   shopPage:1,
                   orderWay:"all",
                   orderBay:"desc",
                   headerHeight:44,
                   scrollBool:true,
                   scrollTop:0
               }
          },
          methods: {
               // åˆ°åº—é“º
               _toShop(aid){
                     this.$router.push({name:"shop",query:{shop_id:aid}})
               },
               _search(){
                    this.shopPage = 1
                    this.goodsPage = 1
                    this.goodsMoreBool = true
                    this.shopMoreBool = true
                    this.searchGoods((data) => {
                         this.goodsList = data
                    })
                    this.searchShop((data) => {
                         this.shopList = data
                    })
                    this.$refs.searchResult.scrollTop = 0
               },
               // ç‚¹å‡»æŽ’åº
               soltClick(index){
                    this.soltIns = index
                     if(index == 0){
                          this.orderWay = "all"
                    }
                    if(index == 1){
                         this.orderWay = "sales"
                    }
                    if(index == 2){
                         this.orderWay = "price"
                    }
                    if(index != index){
                                this.orderBay = "desc"
                          }else{
                               this.orderBay = this.orderBay == "desc"? "asc" : "desc"
                          }
                          this.searchGoods((data) => {
                              this.goodsList = data
                          })
               },
               // åˆ°å•†å“è¯¦æƒ…é¡µ
               toDetail(good_id){
                    this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
               },
               // æ»šåŠ¨
               scroll(){
                    this.headerHeight = this.$refs.searchresultHeader.clientHeight
                    var scrollTop = this.$refs.searchResult.scrollTop
                    this.scrollTop = scrollTop
                    var height = this.$refs.searchResult.scrollHeight
                    var windowHeight = window.innerHeight
                    this.scrollMore(scrollTop,height,windowHeight,() => {
                         if(this.scrollBool){

                              this.scrollBool = false
                              if(this.tableIns == 0){
                                   if(this.goodsMoreBool){
                                         console.log("å¸¦å¨ƒ")
                                        this.goodsPage++
                                        this.searchGoods()
                                   }
                              }else{
                                   if(this.shopMoreBool){
                                        this.shopPage++
                                        this.searchShop()
                                   }
                              }
                         }
                    })

               },
              searchResult(){

              },
          //     æœç´¢å•†å“
          searchGoods(callback){
                var obj = {
                    page:this.goodsPage,
                    keyword:this.serachVla,
                    order_way:this.orderWay,
                    order_bay:this.orderBay
               }
               this.postObj(obj,window.PATHS.SHOPS_GOODS_GET_LIST,(result) => {
                    if(result.code == 1){
                         if(callback){
                                   callback(result.data)
                         }else{
                               if(result.data.length != 0){
                              this.goodsList = this.goodsList.concat(result.data)
                              console.log(this.goodsList)
                              }else{
                                   this.goodsMoreBool = false
                              }
                              setTimeout(() => {
                                   this.scrollBool = true
                              },1000)
                         }

                    }
               })
          },
          // æœç´¢åº—é“º
          searchShop(callback){
               var obj = {
                    page:this.shopPage,
                    name:this.serachVla,
                    is_goods:1,
                    goods_num:3
               }
               this.postObj(obj,window.PATHS.SHOPS_GET_LIST,(result) => {
                    if(result.code == 1){
                         if(!callback){
                              if(result.data.length != 0){
                              this.shopList = this.shopList.concat(result.data)
                              console.log(this.shopList,'åº—é“ºåˆ—è¡¨')
                         }else{
                              this.shopMoreBool = false
                         }
                         setTimeout(() => {
                              this.scrollBool = true
                         },1000)
                         }else{
                              callback(result.data)
                         }
                    }
               })
          },
          // åˆå§‹åŒ–æ•°æ®
          usdateData(){
                   this.tableIns = 0,
                   this.soltIns = 0,
                    this.serachVla = "",
                   this.goodsPage = 1,
                   this.goodsList = [],
                   this.goodsMoreBool = true,
                   this.shopList = [],
                   this.shopMoreBool = true,
                   this.shopPage = 1,
                   this.orderWay = "all",
                   this.orderBay = "desc",
                   this.headerHeight = 44,
                   this.scrollBool = true,
                   this.scrollTop = 0
          },
          },
          mounted() {
          },
          created(){
               this.serachVla = this.$route.query.searchText
               this.searchGoods()
               this.searchShop()
          },
          activated(){
               if(this.$refs.searchResult){
                    this.$refs.searchResult.scrollTop = this.scrollTop
               }
               if(this.$route.meta.keepLoadBool){
                    this.serachVla = this.$route.query.searchText
                    this.searchGoods()
                    this.searchShop()
               }
          },
          watch:{

          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to,from,next){
               if(to.name == "DetailsPage" || to.name == "shop"){
                    from.meta.keepLoadBool = false
               }else{
                    this.usdateData()
                    from.meta.keepLoadBool = true
               }
               next()
          },
     }
</script>





