<!--åˆ†ç±»é¡µé¢-->
<template>
          <div class="content" v-if="classList != null">
               <div class="classify-head">
                    <HeaderPage>
                         <span slot="headerTitle">åˆ†ç±»</span>
                    </HeaderPage>
               </div>
                    <div class="contenter">
                         <!--ä¸€çº§-->
                              <div class="slider steep_padding">
                                   <ul>
                                        <li @click = "changeStyle($event,index)" v-for="(item,index) in classList" :key="index + 'ins'">{{ item.name }}</li>
                                   </ul>
                              </div>
                              <div class="right">
                              <img v-if="classList[oneIndex].icon" :src=classList[oneIndex].icon style="width: 24.5rem;height: 11.7rem;" height="234" width="490"/>
                              <!--äºŒçº§-->
                              <div class="people man" v-if="isMan" v-for="(item,index) in classList[oneIndex].child">
                                   <h3>Â·   {{ item.name }}   Â·</h3>
                                   <ul>
                                        <li @click="search(item.name)" v-for="(item1,index1) in item.child" :key="index1 + 'chirl1'">
                                             <img :src=item1.icon alt="">
                                             <span style="font-size: 1.1rem;margin-top: 0.5rem">{{ item1.name }}</span>
                                        </li>
                                   </ul>
                              </div >
                         </div>
                    </div>
          </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
    export default {
        name: "ClassifyPage",
      data(){
          return{
               isMan:true,
               isWoman:true,
               classList:null,
               oneIndex:0,

        }
      },
      methods:{
        //     ç‚¹å‡»ä¸€çº§å¯¼èˆª
           changeStyle(ev,index){
                $(ev.currentTarget).css({color:"#000"}).siblings().css({color:"#999999"})
                this.oneIndex = index
           },
        //     ç‚¹å‡»å•†å“åˆ†ç±»è·³åˆ°å•†å“åˆ—è¡¨
           search(name){
                this.$router.push({name:"searcjresult",query:{searchText:name}})
           },
        goBack(){
          window.history.go(-1)
        },
        //   èŽ·å–åˆ†ç±»åˆ—è¡¨
        getList(){
             this.$loading('loading...');
               this.postObj({},window.PATHS.SHOPS_GOODS_COLUMN,(result) => {
                    if (result.code == 1){
                         this.$loading.close();
                         this.classList = result.data
                         console.log(this.classList)
                    }else {
                         this.$toast.center(result.info)
                    }
               })
        },
      },
      mounted() {
             this.getList()
      //    ç‚¹å‡»ä¾§è¾¹æ æ”¹å˜æ ·å¼
        $('.slider ul li').click(function () {
          console.log($(this))
          $(this).css('color','black').siblings().css('color','#999999')
        })

      //  èŽ·å–çª—å£é«˜åº¦
      var bodyHeight =  document.body.clientHeight;
        $('.right').css('height',bodyHeight + 'px')
      },
         components:{
             HeaderPage
         }
    }
</script>

<style scoped>
     .classify-head{
          position: fixed;
          top: 0;
          width: 100%;
          background-color: #ffffff;
          z-index: 10;
     }
  .right .people li span{
    display: block;
    text-align: center;
    font-size: 1.5rem;
    margin-top: 1rem;
    color: #161616;
  }
  .right .people li img{
    width: 100%;
    height: auto;
  }
  .right .people li:nth-child(3n + 1){
       margin-left: 0;
  }
  .right .people li{
    width: 7.5rem;
    height: 11.2rem;
    margin-bottom: 1rem;
       margin-left: 1rem;
  }
  .right .people ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .right .people h3{
    font-size: 18px;
    text-align: center;
    margin-top: 5rem;
    margin-bottom: 3.5rem;
  }
  .right{
    width: 27.5rem;
    float: right;
    padding: 1.5rem;
       overflow: scroll;
       padding-top: 4.4rem;
       height: 100vh;
  }
  .slider ul li:first-child{
    color: #000;
  }
  .slider ul li{
    text-align: center;
    font-size: 14px;
    color: #999999;
    margin-bottom: 3.6rem;
  }
  .slider ul li:last-child{
    margin-bottom: 0;
  }
  .slider ul{
    padding-bottom: 2.6rem;
    padding-top: 6.4rem;

  }
  .contenter .slider{
    width: 10rem;
    float: left;
       height: 100vh;
       overflow: scroll;
       background-color: #f5f5f5;
  }
     .slider::-webkit-scrollbar {display:none}
     .right::-webkit-scrollbar {display:none}
  .contenter{
       overflow: hidden;
  }
  .goBack{
    position: absolute;
    left:2rem;
  }
  .content{
  }

</style>



