<style scoped lang="scss">
     .footer {
          position: fixed;
          bottom: 0;
          width: 100%;
          background-color: #ffffff;
          font-size: 1.7rem;
          border-radius: 5px;
          padding: 0 12px;
          padding-bottom: 15px;
          span{
               background-color: #f55056;
               display: flex;
               align-items: center;
               justify-content: center;
               width: 100%;
               border-radius: 5px;
               padding: 7px 0;
               color: #ffffff;
          }
     }
     .sku_content_scroll{
          height: 50vh;
          overflow-y: scroll

     }
     .conversion_btn{
          width: 100% !important;
          background-color: #f55056;
     }
     .com-size ul li {
          float: left;
          padding: 0 1rem;
          line-height: 3rem;
          display: flex;
          border: solid 1px #eeeeee;
          color: #212121;
          margin-top: 1rem;
          margin-right: 1rem;
          font-size: 1.3rem;
     }

     .com-size ul {
          overflow: hidden;
     }

     .com-size {
          margin-top: 1rem;
          padding-left: 1.2rem;
     }

     .activeLi {
          border: solid 1px #e7204f !important;
          color: #e7204f !important;
     }
     .com-number > div {
          margin-top: 1.5rem;
     }


     .com-number > div span:first-child {
          background-color: #fbfbfb;
     }

     .com-number > div span {
          display: inline-block;
          line-height: 2rem;
          text-align: center;
          font-size: 1.6rem;
          padding: 0 0.7rem;
          background-color: #f6f5f5;
     }

     .com-number > div {
          font-size: 0;
     }

     .com-number {
          padding: 0 1.2rem;
          margin-top: 1.6rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 10rem
     }

     .com-type ul li img {
          margin-right: 0.5rem;
          width: 3rem;
          /*height: 3rem;*/
          border: none;
     }

     .com-type ul li {
          display: flex;
          align-items: center;
          border: solid 1px #eeeeee;
          float: left;
          color: #212121;
          margin-top: 1rem;
          margin-right: 1rem;
          line-height: 3rem;
          padding: 0 1rem;
     }

     .com-type ul {
          overflow: hidden;
     }

     .com-type {
          padding-left: 1.2rem;
          margin-top: 1.7rem;
     }

     .comInfo-right > span:last-of-type {
          font-size: 1.2rem;
          color: #212121;
          margin-top: 0.5rem;
          display: block;
     }

     .comInfo-right > span:first-of-type {
          font-size: 1.2rem;
          color: #9d9d9d;
          margin-top: 1rem;
     }

     .can_use_integral {
          padding: 0.3rem 1.4rem;
          background-color: #1ae42f07;
          border-radius: 0.8rem;
          font-size: 1.1rem;
          color: #e7204f;
     }

     .comInfo-right > div {
          display: flex;
          align-items: center;
     }

     .comInfo-right {
          margin-left: 0.5rem;
     }

     .sku-content .com-info > img {
          width: 6.4rem;
          height: 6.4rem;
          border-radius: 0.5rem;
     }

     .sku-content .com-info {
          display: flex;
          justify-content: left;
          padding: 0 1.2rem;
     }

     .sku-title > span {
          position: absolute;
          right: 1.1rem;
          font-size: 3rem;
          top: 0.3rem;
     }

     .sku-title h3 {
          line-height: 4.4rem;
          width: 37.5rem;
          text-align: center;
          font-size: 1.7rem;
          color: #1b1b1b;
     }

     .sku-title {
          position: relative;
     }
     .sku-content {
          background-color: white;
          border-top-left-radius: 2rem;
          border-top-right-radius: 2rem;
          position: relative;
     }
</style>
<template>
     <!--skué€‰æ‹©ç»„ä»¶-->
     <div class="sku-content">
          <div class="sku-title">
               <h3>é€‰æ‹©è§„æ ¼</h3>
               <span @click="sendToDetails">Ã—</span>
          </div>
          <div class="com-info">
               <img @click="toLookImg" ref="titleImg" :src="titleImg? titleImg : this.comInfo.thumb" alt="">
               <div class="comInfo-right">
                    <div>
                         <span style="font-size: 1.8rem;color: #e7204f;">ï¿¥{{ comInfo.is_original == 1? comInfo.market_price : comInfo.shop_price }}</span>
                         <span class="can_use_integral" v-if="comInfo.extra_table == 'goods_exchange'">å¯ç”¨{{ comInfo.extra_data.integral }}éº¦ç©—æŠµæ‰£</span>
                    </div>
                    <span>åº“å­˜{{ stock }}ä»¶</span>
                    <span>
                         é€‰æ‹©
                         <span v-if="newSkuArr1 == ''">é»˜è®¤</span>
                         {{ newSkuArr1 }}
                    </span>

               </div>
          </div>
          <!--äºšç‘Ÿçš„ulli-->
          <div class="sku_content_scroll">
               <div class="com-type" v-for="(item,index) in comInfo.skugroup" :key="index + 'ins'">
                    <h3 style="font-size: 1.2rem;color: #9d9d9d">{{ item.title }}</h3>
                    <ul>
                         <li v-for="(item2,index2) in item.specs" :key="index2 + 'ins'" @click="changeSku(index,index2,$event)">
                              <img :src="item2.icon" alt="" v-if="item.title == 'é¢œè‰²' && item2.icon">
                              <span>{{ item2.name }}</span>
                         </li>
                    </ul>
               </div>
               <div class="com-number">
                    <h3 style="font-size: 1.2rem;color: #9d9d9d;">è´­ä¹°æ•°é‡</h3>
                    <div>
                         <span @click="jianComNum">-</span>
                         <span style="padding: 0 1.1rem;margin: 0 5px;">{{  comNum }}</span>
                         <span @click="addComNum">+</span>
                    </div>
               </div>
               <!-- <div style="margin-bottom: 10rem">
                    <InstallmentPage :installment="comInfo.shop_price"></InstallmentPage>
               </div> -->
          </div>
          <div :style="{paddingBottom:padBotm == 0? 7 + 'px' : padBotm + 'px'}" class="footer" @click="toEntCom">
               <span class="skuShare">ç¡®å®š</span>
          </div>
     </div>
</template>

<script>
     import storage from '@/common/js/storage.js'
     import InstallmentPage from '../components/InstallmentPage'
     import {joinShoppingCar} from "@/common/js/common.js"
     import { Dialog } from 'vant';

     export default {
          name: "SkuChoose",
          data() {
               return {
                    comInfo:null,
                    comNum: 1,
                    titleImg: null,
                    //     å•†å“åº“å­˜
                    stock: null,
                    skuArr: [],
                    newSkuArr: [],
                    newSkuArr1: '',
                    //     å•ä¸ªå•†å“çš„skuåº“å­˜
                    skuStock: 0,
                    skucomInfo:null,
                    //     æ˜¯å¦åŽŸä»·è´­ä¹°
                    is_original: '0',
                    //åˆ†äº«äººid
                    share_user_id: "",
                    padBotm:0,
               }
          },
          methods: {
               //åˆ°å›¾ç‰‡æŸ¥çœ‹é¡µé¢
               toLookImg(){
                    var index = 0
                    var newImgArr = []
                         this.comInfo.skulist.forEach((item,index) => {
                              if (item.icon_id != ''){
                                   newImgArr.push(item.icon_id)
                              }
                         })
                         newImgArr.forEach((item,ins) => {
                              if (item == this.titleImg) {
                                   index = ins
                              }
                         })
                    if (newImgArr.length == 0){
                         newImgArr.push(this.comInfo.thumb)
                         index = 0
                    }
                    // var obj = {
                    //      pictureArr:newImgArr,
                    //      index:index
                    // }
                    this.pictureLook(newImgArr,index)
                    // obj = JSON.stringify(obj)
                    // obj = this.getBaseStr(obj)
                    // location.href = "app://picture/?" + obj
               },
               // åŠ å…¥è´­ç‰©è½¦
               toShoppingCar() {
                    joinShoppingCar(this,{
                         skuList:this.comInfo.skulist,
                         skugroup:this.comInfo.skugroup,
                         chooseSkuList:this.newSkuArr,
                         goods_id: this.comInfo.aid,
                         number :this.comNum,
                         video_id:0,
                         share_user_id :0,
                         extra_table:this.comInfo.extra_table
                    },() => {
                         setTimeout(() => {
                              this.sendToDetails()
                         },300)
                    })
               },
               //åŒ¹é…sku
               changeSku(index, index2, ev) {
                    this.comNum = '1'
                    this.skuArr[index] = index2
                    $(ev.currentTarget).addClass('activeLi').siblings().removeClass('activeLi')
                    //å°†skuä¼ é€’åˆ°è¯¦æƒ…é¡µ
                    this.newSkuArr[index] = this.comInfo.skugroup[index].title + ':' + this.comInfo.skugroup[index].specs[index2].name
                    this.$emit('sentSkuInfo', this.newSkuArr)
                    this.titleImg = this.comInfo.skugroup[index].specs[index2].icon
                    if (this.newSkuArr.length == this.comInfo.skugroup.length) {
                         this.newSkuArr1 = this.newSkuArr.join(',')
                         this.comInfo.skulist.forEach((item, index) => {
                              if (item.title == this.newSkuArr1) {
                                   //åŠ¨æ€æ›´æ”¹é€‰æ‹©skuä»·é’±
                                   if (this.comInfo.is_original == 1) {
                                        this.comInfo.market_price = item.origprice
                                        this.comInfo.extra_data.integral = item.integral
                                   }else{
                                        this.comInfo.shop_price = item.price
                                   }
                                   this.stock = item.stock
                              }
                         })
                    }
                    //èŽ·å–å•ä¸ªskuçš„åº“å­˜
                    this.comInfo.skulist.forEach((item, index) => {
                         if (item.title == this.newSkuArr1) {
                              this.skuStock = item.stock
                         }
                    })
               },
               //å‡ä¸€
               jianComNum() {
                    if (this.comInfo.extra_table == 'goods_group'){
                         return
                    }
                    if (this.comNum <= 1) {
                         return
                    } else {
                         this.comNum--
                    }
               },
               //åŠ ä¸€
               addComNum() {
                    //åˆ¤æ–­æ˜¯å›¢è´­å•†å“ä¸å…è®¸åŠ 
                    if (this.comInfo.extra_table == 'goods_group'){
                         return
                    }
                    if (this.comInfo.extra_table == 'goods_customs') {
                         this.comNum++
                         if (this.comNum * this.comInfo.shop_price > 5000){
                              this.comNum--
                              return
                         }
                         return
                    }
                    if (this.comNum >= this.stock) {
                         return
                    } else {
                         this.comNum++
                    }
               },
               sendToDetails() {
                    this.$emit('listenToSkuChoose', false)
               },
               payPrice(){
                         if (this.comInfo.skugroup.length != 0){
                              var chieck = 1
                              var title = []
                              //è¯·é€‰æ‹©è§„æ ¼
                              this.comInfo.skugroup.forEach((item, index) => {
                                   var index2 = this.skuArr[index]
                                   if (index2 == undefined) {
                                        if (chieck == 1) {
                                             this.$toast.center('è¯·é€‰æ‹©' + item.title)
                                        }
                                        chieck = 0;
                                        return
                                   }
                                   title.push(item.title + ':' + item.specs[index2].name)
                              })
                              if (chieck == 0) {
                                   return
                              }
                              title = title.join(',')
                              this.comInfo.skulist.forEach((item, index) => {
                                   if (item.title == this.newSkuArr1) {
                                        this.skucomInfo = item
                                   }
                              })
                         }
                         var skuComList =
                             {
                                  goods_id: this.comInfo.aid,
                                  title: this.comInfo.title,
                                  thumb: this.skucomInfo? this.skucomInfo.icon_id : this.comInfo.thumb,
                                  integral: this.skucomInfo? this.skucomInfo.integral : this.comInfo.rebate_integral,
                                  number: this.comNum,
                                  specs: this.skucomInfo? this.skucomInfo.title : '',
                                  sku_path: this.skucomInfo? this.skucomInfo.path : '',
                                  share_user_id: this.share_user_id,
                                  video_id: this.skucomInfo.video_id? this.skucomInfo.video_id : 0,
                                  stock: this.stock,
                                  period_id:this.comInfo.period_id? this.comInfo.period_id : 0,
                                  is_original:this.comInfo.is_original? this.comInfo.is_original : 0
                             }
                         if (this.skucomInfo) {
                              if (this.comInfo.is_original == 1){
                                   skuComList.goods_price = this.skucomInfo.origprice
                              }else{
                                   skuComList.goods_price = this.skucomInfo.price
                              }
                         }else{
                              if (this.comInfo.is_original == 1){
                                   skuComList.goods_price = this.comInfo.market_price
                              }else{
                                   skuComList.goods_price = this.comInfo.shop_price
                              }
                         }
                         this.$store.commit("setEntDetailSkuInfo",skuComList)
                         this.$emit('skufalse', false)
                         setTimeout(() => {
                              location.href = "#/ent"
                         },500)
                    },
               //ç«‹å³è´­ä¹°
               toEntCom() {
                    if (!this.maleToken(window.token)) {
                         return
                    }
                    if (this.comInfo.extra_table == 'goods_seckill' && this.comInfo.is_original == 0){
                         if (this.comInfo.extra_data.is_kill != 1){
                              this.$toast.center("å½“å‰å•†å“æš‚æ—¶ä¸æ”¯æŒç§’æ€")
                              return
                         }
                    }
                    if (this.$store.state.isCarOrBuy) {
                         this.payPrice()
                    }else{
                         this.toShoppingCar()
                    }
               },
               },
          mounted() {
          },
          created(){
                this.padBotm = this.$store.state.footerHeight
               this.comInfo = this.$store.state.skuInfo
               this.titleImg = this.comInfo.thumb
               this.stock = this.comInfo.stock
               // this.comInfo = Object.assign({}, this.comInfo);
          },
          watch: {
               comPrice(newVla, oldVal) {
                    this.$emit('sendPrice', newVla)
               }
          },
          components: {
               InstallmentPage
          }
     }
</script>




