
<style scoped lang="scss">
#recommendIndex{
     height: 100vh;
     overflow-y: scroll;
     background-color: #f6f6f6
}
     .recommend_header{
          position: fixed;
          width:100%;
          top:0;
          background-image: url("~@/common/image/recommend/bannerbg.png");
          background-size: 100% auto;
          z-index: 10;
     }
     .table{
          padding: 1.5rem 0;
          ul{
               display: flex;
               align-items: center;
               justify-content:space-between;
               li{
                    width: 50%;
                    text-align: center;
                    font-size: 1.5rem;
                    color:#ffffff;
                    position:relative;
               }
          }
     }
     .table_active:after{
          content:"";
          display:block;
          position: absolute;
          height: 0.4rem;
          border-radius: 0.2rem;
          width: 20%;
          background-color: #fb6e5a;
          left: 50%;
          transform: translateX(-50%);
          bottom: -0.8rem;
     }
     .header_banner{
          width: 100%;
          position: fixed;
          top: 0;
          z-index: 8;
     }
     .recomment_content{
          padding:0 1.2rem;
          position: relative;
          padding-top: 10rem;
          z-index: 9;
     }
     .drecommen_goods_list{
          ul {
               li{
                    background-color: #ffffff;
                    padding: 1rem;
                    margin-bottom: 0.7rem;
                    border-radius:0.5rem;
                    display: flex;
                    justify-content: space-between;
                    .goods_thumb{
                         width: 13rem;
                         height: 13rem;
                         background-size: cover;
                         background-color: #fafafa;
                         flex-shrink: 0;
                         border-radius:0.5rem;
                         position: relative;
                         img{
                              position: absolute;
                              width: 2.5rem;
                              height: 3.25rem;
                              top: 0.5rem;
                              left: 0.5rem;
                         }
                    }
                    .goods_right{
                         flex-grow: 1;
                         margin-left: 1rem;
                         display: flex;
                         flex-direction: column;
                         justify-content: space-between;
                         p{
                              text-overflow: -o-ellipsis-lastline;
                              overflow: hidden;
                              text-overflow: ellipsis;
                              display: -webkit-box;
                              -webkit-line-clamp: 2;
                              line-clamp: 2;
                              -webkit-box-orient: vertical;
                              font-size: 1.4rem;
                              line-height: 2.5rem;
                         }
                         .price_alse{
                              display: flex;
                              align-items: center;
                              justify-content: space-between;
                              .goods_shop_price{
                                   color: #ff0000
                              }
                              .alse{
                                   background-image: url("~@/common/image/recommend/hotImg.png");
                                   background-size: 100% 100%;
                                   width: 12.4rem;
                                   line-height: 3.1rem;
                                   text-align: right;
                                   font-size: 0.9rem;
                                   color: #ffffff;
                                   padding-right: 1rem;
                              }
                         }
                    }
               }
          }
     }
     .recommend_shop_list{
          >ul{
               >li{
                    background-color: #ffffff;
                    padding: 1rem;
                    padding-bottom: 1.3rem;
                    border-radius: 0.5rem;
                    margin-bottom: 0.7rem;
                    .shop_title{
                         display: flex;
                         align-items: center;
                         justify-content: space-between;
                         .shop_title_left{
                              display: flex;
                              align-items: center;
                              justify-content: flex-start;
                              .shop_thumb{
                                   width: 3rem;
                                   height: 3rem;
                                   border-radius: 50%;
                                   background-size: cover;
                         }

                         .let_shop_info{
                              margin-left: 1rem;
                              .shop_info_name{
                              display: flex;
                              align-items: center;
                              .shop_name{
                                   font-size: 1.5rem;
                              }
                              img{
                                   width: 5.8rem;
                                   height: 1.55rem;
                                   margin-left: 1rem;
                              }
                         }
                         .str_pro{
                              font-size: 0.9rem;
                              color: #9d9d9d;
                              margin-top: 0.5rem;
                         }
                         }

                         }
                         .shop_title_right{
                              width: 6.5rem;
                              height: 2.4rem;
                              background-color: #f55056;
                              border-radius: 1.2rem;
                              border: none;
                              font-size: 1.5rem;
                              color: #ffffff;
                         }
                    }
               }
          }
     }
     .shop_goods_list{
          display: flex;
          justify-content: space-between;
          li{
               width: 32%;
               padding-top: 32%;
               background-size: cover;
               border-radius: 0.5rem;
               position: relative;
               overflow: hidden;
               span{
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    background-color: #0000005d;
                    padding: 0.1rem 0.4rem;
                    border-top-right-radius: 0.5rem;
                    color: #ffffff;
               }
          }
     }
</style>
<template>
<!--æ¯æ—¥æŽ¨è-->
      <div ref="recommentIndex" id="recommendIndex" class="steep_padding" @scroll="scroll">
          <div class="recommend_header">
               <HeaderPage :colorType="'white'">
                    <span slot="headerTitle">æ¯æ—¥æŽ¨è</span>
               </HeaderPage>
               <div class="table">
                    <ul>
                         <li @click="clickTable(index)" :class="index == tabIns? 'table_active' : ''" v-for="(item,index) in tabList" :key="index + 'tab'"><span>{{ item }}</span></li>
                    </ul>
               </div>
          </div>
          <img :style="{top:headerBannerTop + 'px'}" class="header_banner" src="@/common/image/recommend/bannerbg.png" alt="">
          <div class="recomment_content">
               <!-- åº—é“º -->
               <div class="recommend_shop_list" v-if="tabIns == 0">
                    <ul>
                         <li @click="toShop(item.aid)" v-for="(item,index) in shopList" :key="index + 'sho'">
                              <div class="shop_title">
                                   <div class="shop_title_left">
                                         <div class="shop_thumb" :style="{backgroundImage:'url('+ item.thumb +')'}"></div>
                                        <div class="let_shop_info">
                                            <div class="shop_info_name">
                                                  <span class="shop_name">{{ item.name }}</span>
                                                  <img v-if="item.tpl_name == 'flagshipshop'" src="@/common/image/recommend/qijianshop.png" alt="">
                                            </div>
                                            <div class="str_pro">
                                                 <span>{{ item.tar }}ç²‰ä¸</span>
                                                 <span>å¥½è¯„çŽ‡{{ item.pro }}%</span>
                                            </div>
                                        </div>
                                   </div>
                                   <button class="shop_title_right">è¿›åº—</button>
                              </div>
                                   <ul class="shop_goods_list">
                                        <li v-for="(itemg,indexg) in item.goods" :key="indexg + 'shpgd'" :style="{backgroundImage:'url('+ itemg.thumb +')'}">
                                             <span>ï¿¥{{ itemg.shop_price.substring(0,itemg.shop_price.length - 3) }}</span>
                                        </li>
                                   </ul>
                         </li>
                    </ul>
               </div>
               <!-- å•†å“ -->
               <div v-if="tabIns == 1" class="drecommen_goods_list">
                    <ul>
                         <li @click="toDetali(item.aid)" v-for="(item,index) in goodsList" :key="item.aid">
                              <div class="goods_thumb" :style="{backgroundImage:'url('+ item.thumb +')'}">
                                   <img v-if="index == 0" src="@/common/image/recommend/number1.png" alt="">
                                    <img v-if="index == 1" src="@/common/image/recommend/number2.png" alt="">
                                     <img v-if="index == 2" src="@/common/image/recommend/number3.png" alt="">
                              </div>
                              <div class="goods_right">
                                   <p>{{ item.title  }}</p>
                                   <div class="price_alse">
                                        <span class="goods_shop_price">
                                             <span style="font-size:1.2rem">ï¿¥</span>
                                             <span class="num_family" style="font-size:1.7rem">{{ item.shop_price.substring(0,item.shop_price.length - 3) }}</span>
                                        </span>
                                        <div class="alse">
                                             <span>çƒ­é”€{{ item.sales }}ä»¶</span>
                                        </div>
                                   </div>
                              </div>
                         </li>
                    </ul>
               </div>
          </div>
      </div>
</template>

<script>
import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "RecommendIndex",
          data(){
               return{
                    tabList:["æ¯æ—¥å¥½åº—","äº§å“çƒ­é”€"],
                    tabIns:0,
                    // èŽ·å–å•†å“æ•°æ®
                    goodsPage:1,
                    goodsMoreBool:true,
                    goodsList:[],
                    // èŽ·å–åº—é“ºæ•°æ®
                    shopPage:1,
                    shopMoreBool:true,
                    shopList:[],
                    //
                    shopScrollTop:0,
                    goodsScrollTop:0,
                    headerBannerTop:0,
                    scrollBool:true
               }
          },
          methods:{
               // åˆ°å•†å“è¯¦æƒ…
               toDetali(aid){
                     this.$router.push({name: 'DetailsPage', query: {comId: aid}})
               },
               // åˆ°åº—é“º
               toShop(shop_id){
                     this.$router.push({name:"shop",query:{shop_id}})
               },
               // æ»šåŠ¨
               scroll(){
                    var scrollTop = this.$refs.recommentIndex.scrollTop
                    var height = this.$refs.recommentIndex.scrollHeight
                    var windowHeight = window.innerHeight
                    // è®°å½•ä¸¤ç§æ»šåŠ¨é«˜åº¦
                    if(this.tabIns == 0){
                         this.shopScrollTop = scrollTop
                    }else{
                         this.goodsScrollTop = scrollTop
                    }
                    // bannerè·Ÿéšæ»šåŠ¨
                    if(scrollTop>0){
                         this.headerBannerTop = -scrollTop
                    }
                    // æ»šåŠ¨åŠ è½½
                    this.scrollMore(scrollTop,height,windowHeight,() => {
                         if(this.scrollBool){
                              this.scrollBool = false
                              // åŠ è½½å•†å“
                              if(this.tabIns == 1){
                                   if(this.goodsMoreBool){
                                        this.goodsPage++
                                   }
                                   this.getGoodsList()
                              }
                              // åŠ è½½åº—é“º
                              if(this.tabIns == 0){
                                   if(this.shopMoreBool){
                                        this.shopPage++
                                   }
                                   this.getShopList()
                              }
                         }
                    })
               },
               // èŽ·å–åº—é“ºåˆ—è¡¨
               getShopList(){
                    var obj = {
                         page:this.shopPage,
                         is_hot:1,
                         is_goods:1,
                         goods_num:3
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GET_LIST,(result) => {
                         if(result.code == 1){
                              if(result.data.length == 0){
                                   this.shopMoreBool = false
                              }else{
                                   this.shopList = this.shopList.concat(result.data)
                              }
                             setTimeout(() => {
                                  this.scrollBool = true
                             },1000)
                         }
                    })
               },
               // èŽ·å–å•†å“åˆ—è¡¨
               getGoodsList(){
                    var obj = {
                         page:this.goodsPage,
                         order_way:'sales',
                         order_bay:'desc',
                         is_hot:1,
                         goods_type:"goods"
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_GET_LIST,(result) => {
                         if(result.code == 1){
                              if(result.data.length == 0){
                                   this.goodsMoreBool = false
                              }else{
                                   this.goodsList = this.goodsList.concat(result.data)
                              }
                              setTimeout(() => {
                                   this.scrollBool = true
                              },1000)
                         }
                    })
               },
               clickTable(index){
                    this.tabIns = index
               },
               // åˆå§‹åŒ–æ•°æ®
               usdateData(){
                    this.tabList = ["æ¯æ—¥å¥½åº—","äº§å“çƒ­é”€"],
                    this.tabIns = 0,
                    // èŽ·å–å•†å“æ•°æ®
                    this.goodsPage = 1,
                    this.goodsMoreBool = true,
                    this.goodsList = [],
                    // èŽ·å–åº—é“ºæ•°æ®
                    this.shopPage = 1,
                    this.shopMoreBool = true,
                    this.shopList = [],
                    //
                    this.shopScrollTop = 0,
                    this.goodsScrollTop = 0,
                    this.headerBannerTop = 0,
                    this.scrollBool = true
               },
          },
          activated(){
               if(this.tabIns == 0){
                    this.$refs.recommentIndex.scrollTop = this.shopScrollTop
               }else{
                    this.$refs.recommentIndex.scrollTop = this.goodsScrollTop
               }
               if(this.$route.meta.keepLoadBool){
                     this.getGoodsList()
                    this.getShopList()
               }
          },
          watch:{
               tabIns(newVal,oldVal){
                    if(newVal == 0){
                          this.$nextTick(() => {
                              this.$refs.recommentIndex.scrollTop = this.shopScrollTop
                              this.headerBannerTop = -this.shopScrollTop
                          })
                    }else{
                        this.$nextTick(() => {
                              this.$refs.recommentIndex.scrollTop = this.goodsScrollTop
                               this.headerBannerTop = -this.goodsScrollTop
                         })
                    }
               }
          },
          created(){
               this.getGoodsList()
               this.getShopList()
          },
          beforeRouteLeave(to,from,next){
               if(to.name == "DetailsPage" || to.name == "shop")  {
                    from.meta.keepLoadBool = false
               } else{
                    this.usdateData()
                    from.meta.keepLoadBool = true
               }
               next()
          },
          components:{
               HeaderPage
          }
     }
</script>





