     import HeaderPage from '../components/HeaderPage'

     export default {
          name: "ShoppingCar",
          data() {
               return {
                    addressList: null,
                    address:null,
                    remark: "",
                    fruitsInfo: null,
                    show:false
               }
          },
          methods: {
               toAddPlace() {
                    location.href = '#/newplace'
               },
               //ç¡®è®¤é¢†å–
               getFruits(){
                    var obj = {
                         land_id:this.fruitsInfo.aid,
                         receiver_name:this.addressList[0].user_name,
                         receiver_phone:this.addressList[0].user_phone,
                         receiver_address:this.addressList[0].user_address,
                         remark:this.remark
                    }
                    this.postObj(obj,window.PATHS.GAME.GAMESTREE_ADDORDER,(result) => {
                         console.log(result)
                         if (result.code == 1){
                              this.show = true
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               toPlace() {
                    location.href = "#/place"
               },
               //     èŽ·å–å½“å‰æˆç†Ÿäº†åœ°çš„ç»“æžœä¿¡æ¯
               getFruitsInfo() {
                    this.postObj({land_id: this.$route.query.land_id}, window.PATHS.GAME.GAMESTREE_GETLANDINFO, (result) => {
                         if (result.code == 1) {
                              this.fruitsInfo = result.data
                              this.fruitsInfo.content = this.fruitsInfo.content.replace(/\n/g,"<br/>")
                              console.log(this.fruitsInfo)
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
          },
          mounted() {
               this.getFruitsInfo()
