
<style scoped lang="scss">
     .shop_price{
          span{
               font-family: æ•°å­—ä¸“ç”¨;
               color: #f55056;
          }
     }
     .goods_img{
          width: 10rem;
          height: 10rem;
          overflow: hidden;
          position: relative;
          border-radius: 5px;
          display: flex;
          flex-shrink: 0;
     }
     .header_title{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: 1.7rem;
     }
     .head{
          width: 100%;
          position: fixed;
          top: 0;
          background-color: #ffffff;
          z-index: 10;
     }
     .progress_parent > span {
          margin-left: 8.5px;
          position: relative;
     }

     .progress_parent {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          color: #9d9d9d;
          font-size: 1rem;
          margin-top: 1rem;
     }

     .progress span {
          display: block;
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          background-color: #f55056;
          border-radius: 0.35rem;
     }

     .progress {
          width: 60%;
          height: 0.7rem;
          background-color: #f1dce1;
          border-radius: 0.35rem;
          overflow: hidden;
          position: relative;
     }
     .progress-text span:last-of-type {
          margin-right: 1rem;
     }

     .progress-text span:first-of-type {
          margin-left: 1rem;
          color: white;
     }

     .conversion_now{
          display: block;
          width: 7.6rem;
          line-height: 3rem;
          text-align: center;
          background-color: #f55056;
          border-radius: 1.5rem;
          color: #ffffff;
          font-size: 1.4rem;
     }

     .Zero-content ul li .li-right > div:last-of-type {
     }

     .Zero-content ul li .li-right p {
          height: 3.5rem;
          font-size: 1.3rem;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
          font-weight: 500;
     }

     .Zero-content ul li .li-right {
          position: relative;
          margin-left: 1rem;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
     }
     .Zero-content ul li {
          padding: 15px;
          background-color: white;
          margin-bottom: 1rem;
          border-radius: 5px;
          display: flex;
          justify-content: flex-start;
     }

     .Zero-content ul {
          padding: 1.2rem;
          padding-top: 5.5rem;
     }

     .van-nav-bar {
          line-height: 4.4rem;
          height: 4.4rem;
     }

     .van-nav-bar__title {
          font-size: 1.7rem;
     }

     .van-nav-bar__arrow {
          font-size: 2.5rem;
          color: #000;
     }

     .Zero-content {
          background-color: #f6f6f6;
          height: 100vh;
          overflow-y: scroll;
     }
     .footer_more{
          text-align: center;
          margin-bottom: 2rem;
          font-size: 1.3rem;
          color: #9d9d9d;
     }
</style>

<template>
     <!--0å…ƒå…‘æ¢é¡µé¢-->
     <div ref="zeroContent" class="Zero-content steep_padding" @scroll="scroll">
          <div class="head">
               <HeaderPage>
                    <span slot="headerTitle">0å…ƒå…‘æ¢</span>
                    <div slot="headerRight" @click="toSearch" class="header_right">
                         <img src="@/common/image/publicImg/search.png" alt="" style="width: 1.5rem;height: 1.5rem;">
                    </div>
               </HeaderPage>
          </div>
          <ul>
               <li v-for="(item,index) in comData" :key="index + 'ansol'" @click="todetails(item.aid)">
                    <div class="goods_img">
                         <img class="same_img" :src="item.thumb" alt="">
                    </div>
                    <div class="li-right">
                         <div>
                              <p>{{ item.title }}</p>
                              <div class="progress_parent">
                                   <div class="progress">
                                        <span :style="'width:' + Math.floor(item.sales / item.stock * 100) + '%'"></span>
                                   </div>
                                   <span>å·²å…‘{{  Math.floor(item.sales / item.stock * 100) }}%</span>
                              </div>
                         </div>
                         <div>
                              <div style="display: flex;justify-content: space-between;align-items: center">
                                   <div>
                                        <div class="shop_price">
                                             <span style="font-size: 1.1rem">ï¿¥</span><span style="font-size: 1.5rem">{{ item.shop_price.substring(0,item.shop_price.length - 3) }}</span><span style="font-size: 1.1rem">{{ item.shop_price.substring(item.shop_price.length - 3,item.shop_price.length) }}</span><span style="font-size: 1.5rem" v-if="item.shop_price"> + {{ item.extra_data.integral }}</span><span style="font-size: 1.1rem;font-family: çŸ³ä½“">éº¦ç©—</span>
                                        </div>

                                        <div style="font-size: 1.1rem;color: #ff0000">
                                             <img src="@/common/image/publicImg/mail_ico_Crowd.png" alt="" style="width: 1.25rem;height: 1rem;">
                                             <span>{{ item.sales }}äººå·²å…‘æ¢</span>
                                        </div>
                                   </div>
                                   <span class="conversion_now">ç«‹å³å…‘æ¢</span>
                              </div>
                         </div>

                    </div>
               </li>
          </ul>
          <div class="footer_more">
               <span v-if="moreBool">æ­£åœ¨åŠ è½½æ›´å¤š...</span>
               <span v-if="!moreBool">å·²ç»æ»‘åˆ°åº•äº†</span>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../components/HeaderPage"
     export default {
          name: "ZeroPage",
          data() {
               return {
                    comData: [],
                    scrollTop:0,
                    page:1,
                    scrollBool:true,
                    moreBool:true
               }
          },
          methods: {
               scroll(){
                    this.scrollTop = this.$refs.zeroContent.scrollTop
                    var height = this.$refs.zeroContent.scrollHeight
                    var windowHeight = window.innerHeight
                    this.scrollMore(this.scrollTop,height,windowHeight,(data) => {
                         if (this.scrollBool){
                              this.scrollBool = false
                              if (this.moreBool) {
                                   this.page++
                                   this.getComInfo()
                              }
                         }
                    })
               },
               //åˆ°æœç´¢é¡µé¢
               toSearch(){
                    location.href = "#/search"
               },
               onClickLeft() {
                    window.history.go(-1)
               },
               todetails(comId) {
                    this.$router.push({path: '/details', query: {comId}})
               },
               getComInfo() {
                    // èŽ·å–0å…ƒæ¢è´­å•†å“åˆ—è¡¨
                    var obj1 = {
                         page: this.page,
                         goods_type: 'goods_exchange'
                    }
                    this.postObj(obj1, window.PATHS.SHOPS_GOODS_GET_LIST, (result) => {
                         if (result.code == 1){
                              setTimeout(() => {
                                   this.scrollBool = true
                              },1000)
                              if (result.data.length != 0){
                                   this.comData = this.comData.concat(result.data)
                                   this.moreBool = true
                              }else{
                                   this.moreBool = false
                              }
                              console.log(this.comData,"0å…ƒå…‘æ¢åˆ—è¡¨æ•°æ®")
                         }

                    })
               },
          },
          mounted() {

          },
          created(){
               this.getComInfo()
          },
          components:{
               HeaderPage
          },
          activated(){
               if (this.$refs.zeroContent){
                    this.$refs.zeroContent.scrollTop = this.scrollTop
               }
               if (this.$route.meta.keepLoadBool){
                    this.getComInfo()
               }

          },
          beforeRouteLeave(to,from,next){
               if (to.name == "/"){
                    from.meta.keepLoadBool = true
                    this.comData = []
                    this.scrollTop = 0
                    this.page = 1
                    this.moreBool = true
                    this.scrollBool = true
               }else{
                    from.meta.keepLoadBool = false
               }
               next()
          },
     }
</script>




